<template>
    <div class="my-address">
    <div class="address-edit">
      <div class="head">
        <span v-if="!willSendData.id">新增收货地址</span>
        <span v-if="willSendData.id">修改收货地址</span>
          <span class="com-div-middle-ab span">设为默认</span>
          <label class="switch-button com-div-middle-ab"><input
            class="mui-switch mui-switch-animbg" v-model="willSendData.isDefault" value="1"
            type="checkbox" :checked="willSendData.isDefault"></label>
      </div>
      <section class="edit-content">
        <div class="phone-code-warp">
          <div class="row">
            <div class="one">*</div>
            <div class="two">收 货 人：</div>
            <div class="three">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!name">请输入收货人的姓名</span>-->
                <input type="text" class="input-tel" v-model="willSendData.consignee" placeholder="请输入收货人的姓名">
              </label>
            </div>
          </div>
          <!---->
          <div class="row paddingRow">
            <div class="one">*</div>
            <div class="two">联系电话：</div>
            <div class="three">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!phone">请输入联系电话</span>-->
                <input type="tel" class="input-tel" v-model="willSendData.phone" placeholder="请输入联系电话">
              </label>
            </div>
          </div>
          <!---->
          <div class="row">
            <div class="one">*</div>
            <div class="two">所在区域：</div>
            <div class="three">
              <label class="input-label" @click="openALertFn()">
                <img class="arrowDown com-div-middle-ab" src='../../assets/my/arrowDown.png'>
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!area">请选择您所在的区域</span>-->
                <input type="text" class="input-tel" :value="thisPCD" placeholder="请选择您所在的区域" disabled>
              </label>
            </div>
          </div>
          <!---->
          <div class="row paddingRow">
            <div class="one">*</div>
            <div class="two">详细地址：</div>
            <div class="three textarea">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab address" v-if="!address">请输入您的详细地址</span>-->
                <textarea class="input-tel" v-model="willSendData.detailAddress" placeholder="请输入您的详细地址"></textarea>
              </label>
            </div>
          </div>
        </div>
        <div class="submit-warp">
          <button class="submit-button com-cursor-hander give-up" type="button" @click="giveUpSubmitFn()" v-if="willSendData.id">放弃修改</button>
          <button class="submit-button com-cursor-hander" type="button" @click="submitFn()">确定提交</button>
        </div>
        <div class="area-mark" v-if="openAreaMark">
          <area-linkage @upAreaMsg='upAreaMsgFn'
                        :upAreaData='willSendData'>

          </area-linkage>
        </div>
      </section>
    </div>
    <div class="address-manage">
      <div class="head">
        已保存的地址
      </div>
      <section class="manage-content">
          <table cellspacing="0" cellpadding="0">
            <tr>
              <th style="width:1.65rem;">收货人</th>
              <th style="width:2.16rem;">所在区域</th>
              <th style="width:2.85rem;">详细地址</th>
              <th style="width:1.76rem;">联系电话</th>
              <th style="width:1.48rem;">操作</th>
            </tr>
            <tr v-if="getData.length==0||!getData">
              <td colspan="5">暂无数据！</td>
            </tr>
            <tr v-for="(sitem,sindex) in getData" :key="sindex" v-if="getData">
              <td v-text="sitem.consignee"></td>
              <td v-text="sitem.provinceName+sitem.cityName+sitem.districtName"></td>
              <td v-text="sitem.detailAddress"></td>
              <td v-text="sitem.phone"></td>
              <td><span class="deleteRed com-cursor-hander" @click="clickDelete(sitem,sindex)">删除</span><span
                class="editGrenn com-cursor-hander" @click="editAddress(sitem.id)">编辑</span></td>
            </tr>
          </table>
      </section>
    </div>

    </div>
</template>
<script type="text/javascript">
  import areaLinkage from 'src/components/com/areaLinkage/areaLinkage.vue';
  export default {
    data(){
      return {
        getData:[],
        openAreaMark: false,
        thisPCD:'',
        willSendData: {
          "id": '',
          "provinceName": "",
          "cityName": "",
          "districtName": "",
          "provinceCode": '',
          "cityCode": '',
          "districtCode": '',
          "detailAddress": "",
          "phone": "",
          "consignee": "",
          "isDefault": "1",
        }

      }
    },

    methods: {
      giveUpSubmitFn(){
        this.openAreaMark = false;
        this.willSendData={
          "id": '',
          "provinceName": "",
          "cityName": "",
          "districtName": "",
          "provinceCode": '',
          "cityCode": '',
          "districtCode": '',
          "detailAddress": "",
          "phone": "",
          "consignee": "",
          "isDefault": "1",
        }
        this.thisPCD='';
        console.log(this.willSendData);
      },
      editAddress(oid){
        this.openAreaMark = false;
        this.getAjax(this.url.findReceiver, {isDefault: '', receiveId: oid}, (res)=> {
          var valName = '';
          if (res.code == 200) {

            if (res && res.data.length >= 1) {
              console.log(0)
              m$.template({
                time: 10,
                val: '',
              })
              this.willSendData = res.data[0];
              this.buildPCDFn();
            }
          }
          else {
            m$.template({
              time: 800,
              val: res.message,
            })
          }


        });
      },
      clickDelete(oitem,oindex){
        this.openAreaMark = false;
        var othis = this;
        this.postAjax(
          this.url.deleteReceiver, {id: oitem.id}, (res) => {
            if (res.code == 200) {
              m$.template({
                time: 800,
                val: '操作成功',
                fn(){
                  // othis.getData.splice(oitem[2], 1)
                  othis.getAddr()
                }
              })

            }
            else {
              m$.template({
                time: 1200,
                val: res.message
              })
            }
          }
        )
      },
      getAddr(){
        this.getAjax(this.url.findReceiver, {isDefault: '', receiveId: ''}, (res) => {
          var valName = '';
          if (res.code == 200) {
            if (res && res.data.length >= 1) {
              m$.template({
                time: 10,
                val: '',
              });
              this.getData =[];
              var othis=this;
              setTimeout(function () {
                othis.getData = res.data.reverse();
              },1)

            }

          }
          else {
            m$.template({
              time: 800,
              val: res.message,
            })
          }


        });
      },
      //以下是编辑操作
      openALertFn(){
        this.openAreaMark = true;
      },
      upAreaMsgFn(oObj){
        this.openAreaMark = false;
        if(oObj==false){
          return;
        }
        if (!oObj.province.id) {
          return;
        }
        this.willSendData.provinceName = oObj.province.name;
        this.willSendData.provinceCode = oObj.province.id;
        this.willSendData.cityName = oObj.city.name;
        this.willSendData.cityCode = oObj.city.id;
        this.willSendData.districtName = oObj.district.name;
        this.willSendData.districtCode = oObj.district.id;
        this.buildPCDFn();
      },
      buildPCDFn(){
        let ostring = "";
        if (this.willSendData.provinceName) {
          ostring += '/' + this.willSendData.provinceName
        }
        if (this.willSendData.cityName) {
          ostring += '/' + this.willSendData.cityName
        }
        if (this.willSendData.districtName) {
          ostring += '/' + this.willSendData.districtName
        }
        ostring = ostring.substring(1, ostring.length)
        this.thisPCD = ostring;
      },
      submitFn(){
        let othis=this;
        var thisUrl=this.url.saveReceiver;
        var phoneExg=/^1(3|4|5|7|8)\d{9}$/;
        var addressExg=/^([A-Za-z0-9]|[\u4E00-\u9FA5])+$/;

        let sendData = {
          consignee: this.willSendData.consignee,
          phone: this.willSendData.phone,
          provinceCode: this.willSendData.provinceCode,
          cityCode: this.willSendData.cityCode,
          districtCode: this.willSendData.districtCode,
          detailAddress: this.willSendData.detailAddress,
          isDefault: this.willSendData.isDefault ? '1' : "0"
        };
        if(this.willSendData.id){
          sendData['id']=this.willSendData.id;
          thisUrl=this.url.updateReceiver;
        };

        if(!this.willSendData.consignee){
          m$.template({
            val:'请填写收件人',
            time:500
          })
          return
        }
        if(!this.willSendData.detailAddress){
          m$.template({
            val:'请填写<br>详细地址',
            time:500
          })
          return
        }
        if(!this.thisPCD){
          m$.template({
            val:'请选择<br>区域信息',
            time:500
          })
          return
        }
        if(phoneExg.test(this.willSendData.phone)==false){
          m$.template({
            val:'手机号<br>格式错误',
            time:500
          })
          return
        }
        if(addressExg.test(this.willSendData.detailAddress)==false){
          m$.template({
            val:'地址详情不能<br>含有特殊字符',
            time:500
          })
          return;
        }
        this.postAjax(thisUrl,sendData,(res)=>{
          this.thisPCD=''
          this.willSendData= {
            "id": '',
              "provinceName": "",
              "cityName": "",
              "districtName": "",
              "provinceCode": '',
              "cityCode": '',
              "districtCode": '',
              "detailAddress": "",
              "phone": "",
              "consignee": "",
              "isDefault": "1",
          };
          if(res.code==200){
            this.getAddr();
            m$.template({
              time:800,
              val:'操作<br>成功',
            })
          }
          else {
            m$.template({
              time:800,
              val:res.message,
            })
          }
        })

      }
    },
    components: {
      areaLinkage
    },
    created(){
        m$.documentTitle("管理收货地址");
      this.getAddr();
        // this.getAjax(this.url.findReceiver, {isDefault: '', receiveId: this.$route.params.status}, (res)=> {
        //   var valName = '';
        //   console.log(res.data.length)
        //   if (res.code == 200) {
        //
        //     if (res && res.data.length >= 1) {
        //       console.log(0)
        //       m$.template({
        //         time: 10,
        //         val: '',
        //       })
        //       this.willSendData = res.data[0];
        //       this.buildPCDFn();
        //     }
        //     else {
        //       console.log(1)
        //       m$.template({
        //         time: 800,
        //         val: '暂无数据',
        //       })
        //     }
        //   }
        //   else {
        //     m$.template({
        //       time: 800,
        //       val: res.message,
        //     })
        //   }
        //
        //
        // });
    },
    beforeDestroy(){
      m$.template({
        time: 1,
        val: '',
      })
    }
  }
</script>
<style scoped lang="less">
  @import '../../components/com/switch.less';
  .area-mark{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.71);
