  }
  .deleteRed{
    color: #fb4874;
  }
  .editGrenn{
    color: #0baf7f;
    margin-left: 0.1rem;

  }
  .arrowDown{
    right: 0.14rem;
    height: 0.08rem;
  }
  .input-label{
    height: 100%;
    position: relative;
    display: inline-block;
