      color:#ffffff;
      letter-spacing:0px;
    }
.give-up{
  border: 1px solid #333;
  color: #333;
  background-color: white;
  margin-right: 0.1rem;
}
  }
  .my-address{
  min-height: 6.68rem;
  _height:6.68rem;
  /*border:1px solid #dbdbdb;*/

  .head{
    background:#f8f8f8;
    border:1px solid #dbdbdb;
    height:0.5rem;
