<template>
  <div id="skill-goods">
    <com-head></com-head>
    <nav-search class="index-search"></nav-search>
    <div class="top">
      <div>
        <div class="top_line">
          <div  :class='{active:(thisDate>=item.gmtStart&&thisDate<item.gmtEndF)}' v-for='(item,ind) in navTimeLine'>
            <p v-text='item.gmtStart+":00"'>08:00</p>
            <p v-if='thisDate>=item.gmtStart&&thisDate<item.gmtEndF'>已开场</p>
            <p v-if='thisDate<item.gmtStart'>未开场</p>
            <p v-if='thisDate>=item.gmtEndF'>已结束</p>
          </div>
        </div>
      </div>
    </div>
    <div class="time-line" v-if="time">
      <div class="left">限时限量  疯狂抢购中</div>
      <div class="right">距离结束还有 <span v-text='time'></span></div>
    </div>
    <section class="type-container" v-if="!noData">
      <type-item :allGoodsData='allGoodsData' v-if='allGoodsData.length'></type-item>
    </section>
    <section v-if="noData" class="type-container">
      <img src="../assets/noOrder.png" class="com-div-center-center-ab">
    </section>
    <com-foot style="margin-top:0.6rem;"></com-foot>
  </div>
</template>
<script type="text/javascript">
  import typeItem from 'src/components/com/typeItem.vue'
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import navSearch from 'src/components/index/navSearch.vue'
  export default {
    components:{typeItem,comFoot,navSearch,comHead},
    data(){
      return {
        ww: '10%',
        active: 1,
        navData: [
          {time: '08:00', text: '已开场', active: false},
          {time: '12:00', text: '已开场', active: false},
          {time: '18:00', text: '已开场', active: true},
          {time: '22:00', text: '已开场', active: false},
          {time: '00:00', text: '已开场', active: false},
        ],

        navTimeLine:[],
        seckillData: [],
        allGoodsData:[],
        time: '',
        timer: '',
        progress:'',
        thisDate:(new Date()).getHours(),
        noData:false,
      }
    },
    methods: {
      getData(){
        this.seckillData=[];
        this.postAjax(this.srcUrl.getProductGroupShopVo, {
          status: 1,
          groupType: 1,
        }, (res) => {
          if (res.code == 200&&res.data.data[0]) {
            // console.log(res)
            this.seckillData = res.data.data[0];
            this.allGoodsData = res.data.data[0].detailVos;


            var currentDate = new Date(this.seckillData.currentDate.replace(
              /\-/g,'/'));
            var gmtEnd = new Date(this.seckillData.gmtEnd.replace(
              /\-/g,'/'));
            var gmtStart = new Date(this.seckillData.gmtStart.replace(
              /\-/g,'/'));
            var time = (gmtEnd.getTime() - currentDate.getTime()) / 1000;
            var detailVos=this.seckillData.detailVos;
            for(var i=0, len=detailVos.length;i<len;i++){
              //获得 秒杀结束时间
              var gmtFinishTime='';
              var gmtFinish="";
              if(detailVos[i].gmtFinish){
                gmtFinish=new Date(detailVos[i].gmtFinish.replace(
                  /\-/g,'/'));
                var t=(gmtFinish.getTime()-gmtStart.getTime())/1000;
                var theTime = parseInt(t);// 秒
                var theTime1 = 0;// 分
                var theTime2 = 0;// 小时
                if (theTime > 60) {
                  theTime1 = parseInt(theTime / 60);
                  theTime = parseInt(theTime % 60);
                  if (theTime1 > 60) {
                    theTime2 = parseInt(theTime1 / 60);
                    theTime1 = parseInt(theTime1 % 60);
                  }
                }
                var result = "" + parseInt(theTime)+"秒";
                if (theTime1 >= 0) {
                  result = "" + parseInt(theTime1) + "分" + result;
                }
                if (theTime2 >= 0) {
                  result = "" + parseInt(theTime2) + "时" + result;
                }
                gmtFinishTime = result
              }
              detailVos[i].gmtFinishTime=gmtFinishTime
              console.log(detailVos[i])
            }

            var _this = this;
            _this.timer = setInterval(function () {
              time--;
              if(time==0){
                clearInterval(_this.timer);
                _this.time='00:00:00'
              }
              _this.formatSeconds(time);
            }, 1000)
          }
          if(this.allGoodsData.length==0){
            this.noData=true;
          }
        })
      },
      getNavTime(){
        this.getAjax(this.srcUrl.getGroupBannerDate,{},(res)=>{
          for(var i=0;i<res.data.length;i++){
            (res.data)[i]['gmtEndF']=res.data[i].gmtEnd;
            if(res.data[i].gmtEnd=='00'){
              (res.data)[i]['gmtEndF']='24';
            }

          }
          this.navTimeLine = res.data
        })
      },
      goGoodsDetail(item){
        m$.localStrages.set('item',item);
        window.location.href = (`#/goodsDetail/${item.productId}`)
      },
      formatSeconds(value){
        var theTime = parseInt(value);// 秒
        var theTime1 = 0;// 分
        var theTime2 = 0;// 小时
        if (theTime > 60) {
          theTime1 = parseInt(theTime / 60);
          theTime = parseInt(theTime % 60);
          if (theTime1 > 60) {
            theTime2 = parseInt(theTime1 / 60);
            theTime1 = parseInt(theTime1 % 60);
          }
        }
        var result = "" + parseInt(theTime);
        if (theTime1 >= 0) {
          result = "" + parseInt(theTime1) + ":" + result;
        }
        if (theTime2 >= 0) {
          result = "" + parseInt(theTime2) + ":" + result;
        }
        this.time = result
      }
    },
    created(){
      this.getData();
      this.getNavTime()
    },
    beforeDestroy(){
      clearInterval(this.timer)
    }
  }
</script>
<style lang="less" scoped>
  //@import "${fileName}";
  #skill-goods {
    width: 100%;
    .top {
      background-color: #333;
      .top_line {
        overflow: hidden;
        width: 12rem;
        margin: 0 auto;
        > div {
          float: left;
          width: 1.4rem;
          height: .56rem;
          padding-top: .08rem;
          > p {
            font-size: .18rem;
            text-align: center;
            height: .22rem;
            line-height: .22rem;
            color: #fff;
          }
        }
        .active{
          background-color: #fb4874;
          >p{
            font-size: .2rem;
          }
        }
      }
    }
    .time-line{
      height: .56rem;
      line-height: .56rem;
      margin: 0 auto;
      width: 12rem;
      >div{
        font-size: .2rem;
        color: #333;
      }
      .left{
        float: left;
      }
      .right{
        float: right;
        >span{
          color: #fb4874;
          font-size: .2rem;
        }
      }
    }
    .type-container{
      margin: 0 auto;
      width: 12rem;
      position: relative;
      min-height: 6.4rem ;
      _height:6.4rem;
    }
  }
</style>



