/*
*作者---杨彬
*
*/
<template>
    <div class="type-nav" v-if="nav">
    <section class="">
      <div class="swiper-container" dir="ltr">
        <div class="swiper-wrapper">
       <div v-for="(oitem,oindex) in nav" :key="oindex" class="swiper-slide nav-div">
         <div>
           <div class="img-warp"><img :src="oitem.icon" class="com-div-center-center-ab"></div>
           <div class="text-warp">
             <p v-text="oitem.name"></p>
             <router-link :to="oitem.url" class="list-link com-cursor-hander" tag="div">点击进入</router-link>
           </div>
         </div>
       </div>
          </div>
        </div>
    </section>

    </div>
</template>
<script type="text/javascript">
  import {indexTypeData} from 'src/assets/componentsData.js';
  export default {
    data(){
      return{
        nav:m$.sessionStores.get('getProType'),
      }
    }  ,
    created(){
      if(!m$.sessionStores.get('getProType')){
        var navProType=[];
        this.getAjax(this.srcUrl.getAllBusinessTypeUrl,{},(res)=>{
          //配置导航栏
          for(var i =0; i<res.data.length;i++){
            var ii = {};
            ii.name = res.data[i].name//导航名字
            ii.url =`/goodsItem/type/${res.data[i].id}`;//导航地址
            ii.id=res.data[i].id;
            ii.icon=res.data[i].icon;
            navProType.push(ii);
          }
          m$.sessionStores.set('getProType',navProType);
          console.log(navProType)
          this.nav=navProType;
          setTimeout(function () {
            var swipe2 = new Swiper('.type-nav .swiper-container', {
              // autoplay: 2500,
              slidesPerView: 4,
              spaceBetween: 53,
            });
          }, 10)
        });
      }
      else {
        setTimeout(function () {
          var swipe2 = new Swiper('.type-nav .swiper-container', {
            // autoplay: 2500,
            slidesPerView: 4,
            spaceBetween: 53,
          });
        }, 100)
      }
    }
  }
</script>
<style scoped lang="less">
  .type-nav{
    font-size: 0;
    text-align: center;
    >section{
      display: inline-block;
    }
  }
section{
  text-align: left;
  width: 12rem;
  .nav-div{
   display: inline-block;
    position: relative;
    width: 2.6rem;
    min-height: 1.58rem;
    _height: 1.58rem;
    /*margin-left: 0.53rem;*/
    padding: 0.18rem 0.18rem 0.18rem 0;
    border-radius:0.1rem;
    background-color: #ffffff;
    /*&:first-of-type {*/
      /*margin-left: 0;*/
    /*}*/
      >div>*{
        vertical-align: top;
        display: inline-block;
      }
      .img-warp{
        position: relative;
        width: 1.08rem;
        height: 1.21rem;
        img{
          max-width: 100%;
          max-height: 100%;
        }
      }
      .text-warp{
        width: 1.34rem;
        margin-top: 0.1rem;
        /*margin-right: 0.18rem;*/
        p{
          font-family:PingFangSC-Regular;
          font-size:0.28rem;
          color:#333333;
          letter-spacing:0px;
          line-height:0.38rem;
          text-align:center;
          padding-top: 0.12rem;
        }
        div{
          background: url("data:image/png;base64,") no-repeat center;
          height: 0.5rem;
          font-family:PingFangSC-Regular;
          font-size:0.18rem;
          color:#ffffff;
          letter-spacing:0px;
          line-height:0.5rem;
          text-align:center;
        }
      }
    }
}
</style>



