<template>
  <div class="index">
    <div class="innerBox" v-text="'宝宝余首页>商品分类>'+businessTypeName+'>'+innerBoxData.name">
    </div>
  </div>
</template>

<script type="text/javascript">
  export default{
    props: ['innerBoxData'],
    data(){
        return{
          businessTypeName:''
        }
    },
    created(){
      // this.getAjax(this.srcUrl.getAllBusinessTypeUrl, {}, (res) => {
      //     for (var i=0;i<res.data.length;i++){
      //         if(res.data[i].id==this.innerBoxData.businessTypeId){
      //           this.businessTypeName = res.data[i].name;
      //         }
      //     }
      // })
      console.log(m$.sessionStores.get('getProType'))
      console.log(this.innerBoxData.businessTypeId)
      var getProTypeData= m$.sessionStores.get('getProType')
      for (var i=0;i<getProTypeData.length;i++){
        if(getProTypeData[i].id==this.innerBoxData.businessTypeId){
          this.businessTypeName = getProTypeData[i].name;
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  //@import "${fileName}";
  .index {
    font-family: PingFangSC-Regular;
    width: 100%;
    height: .5rem;
    background-color: #333;
    .innerBox {
      line-height: .5rem;
      width: 12rem;
      margin: 0 auto;
      text-align: left;
      padding-left: .1rem;
      font-size: .16rem;
      color: #ffffff;
    }
  }
</style>



