<template>
    <div>
      <com-head></com-head>
      <nav-search class="index-search"></nav-search>
      <div class="index-contain">
        <section class="index-banner-warp">
          <div>
            <div class="swiper-container" v-if="bannerData">
              <div class="swiper-wrapper">
                <a :href="oitem.linkurl?oitem.linkurl:'/'" class="swiper-slide com-width-whole com-text-center" v-for="(oitem,oindex) in bannerData" :key="oindex">
                  <img :src="oitem.image" >
                </a>
              </div>
              <div class="swiper-pagination"></div>
            </div>
          </div>
        </section>
        <type-nav style="margin:0.22rem 0 0.14rem;"></type-nav>
        <index-skill></index-skill>
        <index-sale></index-sale>
      </div>

      <com-foot style="margin-top:0.6rem;"></com-foot>
    </div>
</template>
<script type="text/javascript">
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import navSearch from 'src/components/index/navSearch.vue'
  import typeNav from 'src/components/index/typeNav.vue'
  import indexSkill from 'src/components/index/indexSkill.vue'
  import indexSale from 'src/components/index/sale.vue'
  export default {
    components:{comHead,navSearch,typeNav,indexSkill,indexSale,comFoot},
    data(){
      return{
        bannerData:'',

      }
    },
    methods:{
      getIndexBanner(){
        this.getAjax(this.url.getBannerShopVo,{bannerType:1},(res)=>{
          if(res.data&&res.data.length>=1){
            this.bannerData=res.data;
            setTimeout(function () {
              var swiper1 = new Swiper('.index-banner-warp .swiper-container', {
                pagination: '.index-banner-warp .swiper-pagination',
                paginationClickable: true,
                autoplayDisableOnInteraction : false,
                autoplay:2500
              });
            },10)


          }
        })
      },
      gotoNextPage(val){
        m$.sessionStores.set('indexSearch',val.val);
        this.$router.push('/goodsItem/0')
      },

    },
    mounted(){
      this.getIndexBanner();
    },
    created(){
      m$.documentTitle("积分商城");

    }
  }
</script>
<style scoped lang="less">
  .index-search{
    margin-top: 0.36rem;
  }
  .index-contain{
    min-height: 6.84rem;
    _height:6.84rem;
   }
  .index-banner-warp{
    .swiper-container img{
     width:100%;

      max-height: 5.4rem;
    }

  }
</style>



