<template>
    <div class="index-skill" v-if='seckillData&&seckillData.detailVos'>
      <section>
        <div class="menu-title">
          <div>
            <span class="title" v-text="indexSkillData.name"></span>
            <img :src="indexSkillData.clockImg" class="clock"/>
            <span class="is-span" v-text="h1"></span>
            <span class="is-span" v-text="h2"></span>
            <span class="is-symbol">:</span>
            <span class="is-span" v-text="m1"></span>
            <span class="is-span" v-text="m1"></span>
            <span class="is-symbol">:</span>
            <span class="is-span" v-text="s1"></span>
            <span class="is-span" v-text="s2"></span>
            <router-link to="/skillGoods" class="will-go-skill com-div-middle-ab  com-cursor-hander" tag="div"><span v-text="indexSkillData.go.name"></span><img :src="indexSkillData.go.img"></router-link>
          </div>
        </div>
        <div class="title-line"></div>
        <div class="menu-contain">
          <div class="one">
            <div  v-for="(oitem,oindex) in seckillData.detailVos" :key="oitem" v-if="oindex<5&&oitem"  @click="openScan(oitem.productId)" class="com-cursor-hander">
           <div class="circle">
              <img
                   :src="oitem.thumbnail" v-if='oitem.thumbnail'>
           </div>
           <div class="text-warp">
        <p class="p1" v-text="oitem.productName"></p>
        <p class="p2" v-text="oitem.digest"></p>
        <p class="p3"><span class="span1" v-text="'￥'+ oitem.price" v-if='parseInt(oitem.price)>=0'></span><span class="span2" v-text="'￥'+oitem.oldPrice">¥348</span></p>
           </div>
            </div>
          </div>
          <!--<div class="two">-->
            <!--<img src="../../assets/index/skill.png">-->
          <!--</div>-->
        </div>
      </section>
    </div>
</template>
<script type="text/javascript">
  import {indexSkillData} from 'src/assets/componentsData.js';
  export default {
    data(){
      return{
        indexSkillData:indexSkillData,
        s1: 0, s2: 9, m1: 0, m2: 1, h1: 1, h2: 1,//时间
        timer: '',
        seckillData:[],
        seckillTime:''
      }

    } ,
    methods: {
      openScan(oid){
        window.location.href=(`#/goodsDetail/${oid}`);
      },
      getData(){
        this.postAjax(this.srcUrl.getProductGroupShopVo,{
          status:1,
          groupType:1,
          pageNum:1,
          pageSize:10,
        },(res)=>{
          if(res.code==200&&res.data.data[0]){
            this.seckillData = res.data.data[0];
            // if(this.seckillData.detailVos.length){
            //   // console.log(3-this.seckillData.detailVos.length)
            //   for (var i=0;i<=3-this.seckillData.detailVos.length;i++){
            //     var obj={};
            //     // obj.price='';
            //     this.seckillData.detailVos.push(obj)
            //     // console.log(this.seckillData.detailVos)
            //   }
            //
            // }

            var gmtEnd=new Date(this.seckillData.gmtEnd.replace(
              /\-/g,'/'));
            var thisTime=new Date(this.seckillData.currentDate.replace(
              /\-/g,'/'));
            var seckillTime=(gmtEnd.getTime()-thisTime.getTime())/1000;
            this.formatSeconds(seckillTime);
            var othis = this;
            othis.timer = setInterval(function () {
              othis.s2--;
              if (othis.s2 < 0) {
                othis.s1--;
                othis.s2 = 9;
                if (othis.s1 < 0) {
                  othis.m2--;
                  othis.s1 = 5;
                  if (othis.m2 < 0) {
                    othis.m1--;
                    othis.m2 = 9;
                    if (othis.m1 < 0) {
                      othis.h2--;
                      othis.m1 = 5;
                      if (othis.h2 < 0) {
                        othis.h1--;
                        othis.h2 = 9;
                        if (othis.h1 < 0) {
                          othis.s1 = 0;
                          othis.s2 = 0;
                          othis.m1 = 0;
                          othis.m2 = 0;
                          othis.h1 = 0;
                          othis.h2 = 0;
                          clearInterval(othis.timer)
                        }
                      }
                    }
                  }
                }
              }
            }, 1000)
          }
          if(!this.seckillData.detailVos){
            this.formatSeconds(0);
            clearInterval(this.timer)
          }
        })
      },
      formatSeconds(value) {
        //倒计时
        var theTime = parseInt(value);// 秒
        var theTime1 = 0;// 分
        var theTime2 = 0;// 小时
        if (theTime > 60) {
          theTime1 = parseInt(theTime / 60);
          theTime = parseInt(theTime % 60);
          if (theTime1 > 60) {
            theTime2 = parseInt(theTime1 / 60);
            theTime1 = parseInt(theTime1 % 60);
          }
        }
        var result = "" + parseInt(theTime);
        if (theTime1 >= 0) {
          result = "" + parseInt(theTime1) + ":" + result;
        }
        if (theTime2 >= 0) {
          result = "" + parseInt(theTime2) + ":" + result;
        }
        var s = theTime + '';
        var m = theTime1 + '';
        var h = theTime2 + '';
        if (theTime < 10) {
          this.s1 = 0;
          this.s2 = theTime;
        } else {
          this.s1 = s.substr(0, 1);
          this.s2 = s.substr(1, 2);
        }
        if (theTime1 < 10) {
          this.m1 = 0;
          this.m2 = theTime1;
        } else {
          this.m1 = m.substr(0, 1);
          this.m2 = m.substr(1, 2);
        }
        if (theTime2 < 10) {
          this.h1 = 0;
          this.h2 = theTime2;
        } else {
          this.h1 = h.substr(0, 1);
          this.h2 = h.substr(1, 2);
        }
      }
    },
    created(){
      this.getData();

    },
    beforeDestroy(){
      clearInterval(this.timer)
    }
  }
</script>
<style scoped lang="less">
  .menu-title{
    padding: 0.22rem 0;
    text-align: left;
    >div{
      min-height: 0.22rem;
      border-left: 0.02rem solid #fb4874;
      padding-left: 0.12rem;
      position: relative;
      /*line-height: 0.24rem;*/
      font-size: 0;
      .will-go-skill{
        right: 0;
        >span{
          font-family:PingFangSC-Regular;
          font-size:0.18rem;
          color:#666666;
          letter-spacing:0px;
          line-height:0.18rem;
          text-align:center;
        }
        >img{
          margin-left: 0.1rem;
        }
      }
      >*{
        display: inline-block;
        vertical-align: top;
      }
      .is-symbol{
        font-family:PingFangSC-Regular;
        font-size:0.30rem;
        color:#8d8d8d;
        letter-spacing:0px;
        line-height:0.2rem;
        text-align:center;
        margin-left: 0.04rem;
        height: 0.26rem;
        display: inline-block;

      }
      .is-symbol,.is-span{
        vertical-align: top;
      }
      .clock{
        height: 100%;
        margin: 0.02rem 0.12rem 0;
      }
      .title{
        height: 0.26rem;
        font-family:PingFangSC-Medium;
        font-size:0.22rem;
        color:#333333;
        letter-spacing:0px;
        line-height:0.24rem;
        text-align:center;
      }
      .is-span{
        margin-left: 0.04rem;
        display: inline-block;
        height: 0.26rem;
        padding: 0rem 0.01rem;
        background:#434343;
        font-family:PingFangSC-Regular;
        font-size:0.24rem;
        color:#ffffff;
        letter-spacing:0px;
        line-height:0.26rem;
        text-align:center;
      }
    }
  }
   .index-skill{
     text-align: center;
     >section{
       position: relative;
       width: 12rem;
       display: inline-block;
       .title-line{
         display: inline-block;
         height: 1px;
         width: 11.5rem;
         border-bottom: 1px solid #d5d5d5;
         margin-bottom: 0.04rem;
       }
     }
   }
.menu-contain{
  >*{
    vertical-align: top;
    display: inline-block;
  }
  .one{
    width: 100%;
    background-color: #fff;
    text-align: left;
    padding: 0 0.1rem;
    >div{
      margin-left: 0.387rem;
      &:first-of-type{
        margin-left: 0;
      }
      padding: 0.1rem 0 0.1rem 0;
      vertical-align: top;
      display: inline-block;
      width: 2.05rem;
      >div{
        vertical-align: top;
        display: inline-block;
      }
      .text-warp{
        margin-left: 0.1rem;
        width: 1.08rem;
        >p{
          width: 100%;
          text-overflow: ellipsis;
          white-space: nowrap;

          overflow:hidden;
          padding: 0.04rem 0;
        }
        .p1{
          font-family:PingFangSC-Regular;
          font-size:0.18rem;
          color:#666666;
          letter-spacing:0px;
          line-height:100%;
          text-align:left;
        }
        .p2{
          font-family:PingFangSC-Regular;
          font-size:0.14rem;
          color:#999999;
          letter-spacing:0px;
          line-height:100%;
          text-align:left;
        }
        .p3{

          >*{
            font-family:PingFangSC-Medium;
            font-size:0.16rem;
            letter-spacing:0px;
            line-height:100%;
            text-align:left;
          }
          .span1{
            color:#fb4874;
          }
          .span2{
            margin-left: 0.06rem;
            font-size:0.14rem;
            text-decoration: line-through;
            color:#c2c2c2;
          }
        }
      }
      .circle{
        width: 0.77rem;
        height: 0.77rem;
        border-radius: 100%;
        border: 1px solid #dddddd;
        >img{
          max-width: 100%;
          max-height: 100%;
        }
      }
  }
  }
  /*.two{*/
   /*width: 1.6rem;*/
    /*height: 100%;*/
    /*img{*/
      /*width: 100%;*/
      /*height: 100%;*/
    /*}*/
  /*}*/
}
</style>



