/*
*作者---杨彬
*
*/
<template>
    <div>
      <my-nav :listData="myIndexDataNav"></my-nav>
      <div class="coupon-small" v-if="!noData">
        <coupon-small-item  :oitem="oitem" :oindex="oindex" :oid="oitem.id" @couponCkFn='conponItemClick'
                            v-for="(oitem,oindex) in allCouponData"
                            :key="oindex" v-if="allCouponData" class="coupon-small-item"></coupon-small-item>
        <section class="com-loading" v-if="isMoreData.more"><img src="../../assets/loading.png"></section>
      </div>
      <section class="coupon-small" v-if="noData">
        <img src="../../assets/noOrder.png" class="com-div-center-center-ab" v-if="noData">
      </section>

    </div>
</template>
<script type="text/javascript">
  import couponSmallItem from "src/components/com/couponSmallItem.vue"
  import myNav from "src/components/com/myNav.vue"
  import {myIndexData} from 'src/assets/componentsData.js'
  export default {
    data(){
      return{
        myIndexDataNav:myIndexData.couponNav,
        status: '',
        allCouponData: [],
        spage: 1,
        slength: 10,
        isMoreData:{
          init:true,
          more:true,

        },
        noData:false,
      }
    },
    methods: {
      conponItemClick(obj){
        this.allCouponData[obj[0]].status = 0;
        this.allCouponData[obj[0]].detailId = obj[1];
      },
      getData(opage, olength){
        var othis=this;
        if(othis.isMoreData.init){
          m$.scroll(function () {
            othis.isMoreData.more=true;
          })
        }
        this.getAjax(this.url.myCoupon, {
          pageNum: this.spage,
          pageSize: this.slength,
          status: this.status
        }, (res)=> {
          if (res.code == 200) {
            othis.isMoreData.more=false;
            if(res.data.length>=1){
              for (var i=0;i<res.data.length;i++){
                this.allCouponData.push(res.data[i]);
              }
            }
            var j=res.data;

            if(j.length%10==0){
              othis.isMoreData.init=true;
              m$.scroll(function () {
                othis.spage++;
                othis.getData(this.status)
              })
            }
            else {
              m$.scroll(function () {
                othis.isMoreData.more=false;
              })
            }
            if(this.allCouponData.length==0){
              this.noData=true;
            }
          }
          else {
            othis.isMoreData.more=false;
            m$.template({
              time: 1000,
              val: res.message
            });
          }
        })

      }
    },
    created(){

      this.status = this.$route.params.id;
      this.getData();
      m$.documentTitle('我的优惠券')
      document.title = '我的优惠券';

    },
    watch: {
      '$route': function (val) {
        if (val.params.id != 3) {
          this.status = val.params.id;
          this.spage=1;
          this.slength=10;
          this.allCouponData=[];
          this.isMoreData={
            init:true,
            more:false,
          }
          this.noData=false;
          this.getData();
        }
      }
    },
    components:{myNav,couponSmallItem}
  }

</script>
<style scoped lang="less">
    .coupon-small{
      padding: 0.38rem 0.5rem 0 0.5rem;
      border:1px solid #dbdbdb;
      min-height: 6rem;
      _height:6rem;
      position: relative;
      border-top: none;
      .coupon-small-item{
        display: inline-block;
        &:nth-of-type(2n){
          margin-left: 0.5rem;
        }
        margin-bottom: 0.25rem;
      }
    }
</style>



