/*
*作者---杨彬
*
*/
<template>
  <div class="login">
    <div class="header-warp">
      <img src="../../assets/login/logo.png">
    </div>
    <section class="content-warp">
      <div class="com-div-center-center-ab content">
        <header class="">登 录</header>
        <section>
          <div class="warp">
            <div style="margin-bottom: 0.19rem;">
              <img src="../../assets/login/account.png" class="com-div-middle-ab">
              <input class="full input" type="text" v-model="mobile" placeholder="请输入手机号">
            </div>
            <div>
              <img src="../../assets/login/password.png" class="com-div-middle-ab">
              <input class="full input" type="password"  v-model="password"
                     placeholder="请输入6-16位英文数字字符组合密码" @keyup.enter="goToSubmit(mobile,password)">
            </div>
            <div class="remember"style="margin-bottom: 0rem;">
              <label class="remember-label">
                <input type="checkbox" v-model="isReme" value="1">
                <span class="img"><img src="../../assets/login/check.png" class="com-div-center-center-ab"></span>
                <span class="span">记住用户名</span></label>
              <router-link tag="span" to="/forget" class="span com-div-middle-ab" style="right:0;">忘记密码？</router-link>
            </div>
                <div class="notice"style="margin-bottom: 0.19rem;" v-text="notice"></div>
            <div>
              <button class="full button" type="button" @click="goToSubmit(mobile,password)">登录</button>
            </div>
            <section class="go-register">
              没有账号？
              <router-link tag="span" to="/register" class="span">立即注册</router-link>
            </section>
          </div>
        </section>
      </div>
    </section>
    <div class="foot-logo">
      © Copyright All rights reserved. | 版权所有: 成都友木科技有限公司
    </div>
  </div>
</template>
<script type="text/javascript">
  export default {
    data(){
      return {
        mobile:  m$.localStrages.get('accountPcData')?m$.localStrages.get('accountPcData'):'',
        password: "",
        isReme:1,
        notice:''
      }
    },
    methods:{
      goToSubmit(oname, opassword){
        if (oname == "" || opassword == "") {
          this.notice='请输入手机号及密码'
          return;
        }
        else {
          this.postAjax('/user/login',//登录请求
            {mobile: oname, password: opassword},
            (res) => {
              if(res.code==200){
                m$.localStrages.set('yxdyb',res.data);
                if(this.isReme==1){
                  m$.localStrages.set('accountPcData',oname);
                }
                else {
                  m$.localStrages.remove('accountPcData');
                }
                this.$router.push('/index')
              }
              else {
               this.notice=res.message;
              }
            })
        }

      }
  },
    watch:{
      'mobile':function (val) {
        this.notice='';
      },'password':function (val) {
        this.notice='';
      },

    },
  created(){
    m$.documentTitle("登录");
  }
  }
</script>
<style scoped lang="less">
  .go-register {
    height: 0.34rem;
    text-align: right;
    font-family: PingFangSC-Regular;
    font-size: 0.14rem;
    color: #e9e9e9;
    letter-spacing: 0.0087rem;
    line-height: 0.34rem;;
    .span {
      cursor: pointer;
      font-size: 0.14rem;
      color: #fb4874;;
    }
  }

  .header-warp {
    width: 12rem;
    text-align: left;
    margin: 0.78rem auto 0.2rem auto;
    img {
      height: 0.5rem;
    }
  }

  .content-warp {
    min-width: 12rem;
    height: 6.8rem;
    position: relative;
    background: url("../../assets/login/banner.jpg") no-repeat center;
    background-size: auto 100%;
    .content {
      width: 6rem;
      > header {
        background: rgba(0, 0, 0, 0.69);
        height: 0.5rem;
        font-family: PingFangSC-Regular;
        font-size: 0.22rem;
        line-height: 0.5rem;
        color: #ffffff;
        letter-spacing: 0px;

        text-align: center;
      }
      > section {
        background: rgba(0, 0, 0, 0.5);
        padding: 0.18rem 0 0.15rem 0;
        .warp {
          width: 3.2rem;
          margin: 0 auto;
          .notice{
            height: 0.2rem;
            line-height: 0.2rem;
            font-family:PingFangSC-Regular;
            font-size:0.14rem;
            color:#ff3838;
            letter-spacing:0.008rem;
            text-align:left;
          }
          .remember {
            text-align: left;
            margin: 0 0 0.17rem 0;
            line-height: 0.36rem;
            position: relative;
            .span {
              cursor: pointer;
              margin-left: 0.06rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              color: #e9e9e9;
              letter-spacing: 0.0087rem;
              text-align: left;
            }
            .remember-label {
              display: inline-block;
              height: 0.16rem;
              line-height: 0.16rem;

              >*{
                vertical-align: top;
              }
              input{
                display: none;
              }
              input:checked ~ .img {

                img {
                  display: inline-block;

                }
              }
              .img {
                display: inline-block;
                width: 0.16rem;
                height: 0.16rem;
                position: relative;
                background-color: #fff;
                img {
                  display: none;
                  width: 0.12rem;
                }

              }
            }

          }
          div {
            > img {
              left: 0.06rem;
              height: 0.27rem;
            }
            position: relative;
            height: 0.38rem;
            .button {
              background: rgba(251, 72, 116, 0.9);
              font-family: PingFangSC-Regular;
              font-size: 0.18rem;
              color: #ffffff;
              letter-spacing: 0px;
              line-height: 0.32rem;
              text-align: center;
            }
            .input {

              background-color: #fff;
              padding-left: 0.4rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              color: #949494;
              letter-spacing: 0.0087rem;

              text-align: left;
            }
            .full {
              width: 100%;
              height: 100%;
              border-radius: 0.02rem;
              padding: 0 0.4rem;
            }
          }
        }
      }
    }
  }

  .foot-logo {
    padding: 1.75rem 0 0.6rem 0;
    font-family: PingFangSC-Regular;
    font-size: 0.16rem;
    color: #878787;
    letter-spacing: 0px;
    line-height: 100%;
    text-align: center;
  }
</style>



