/*
*作者---杨彬
*
*/
<template>
    <div class="my-nav">
     <div v-for="(oitem,oindex) in listData" :key="oitem">
       <div class="mark" @click="goToNext(oitem.url)"></div>
       <router-link class="show" tag="div" :to="oitem.url" active-class="will-red" v-text="oitem.name"></router-link>
     </div>
    </div>
</template>
<script type="text/javascript">
  export default {
    props:["listData"],
    methods:{
      goToNext(ourl){
        this.$router.replace(ourl);

      }    }
  }

</script>
<style scoped lang="less">
    .my-nav{
      height:0.66rem ;
      background:#ffffff;
      border:1px solid #dbdbdb;
      >div{
        display: inline-block;
        position: relative;
        width: 1.5rem;
        height: 0.64rem;
        .show{
          font-family:PingFangSC-Medium;
          font-size:0.2rem;
          color:#333333;
          letter-spacing:0px;
          line-height:0.64rem;
          text-align:center;
        }
        .will-red{
          color:#fb4874;
          position: relative;
          &:after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            z-index: 2;
            -webkit-transform: translateY(50%);
            -moz-transform: translateY(50%);
            -ms-transform: translateY(50%);
            -o-transform: translateY(-50%);
            transform: translateY(50%);
            height: 0.02rem;
            background-color:#fb4874 ;

          }
        }
        .mark{
          position: absolute;
          height: 100%;
          width: 100%;
          top: 0;
          left: 0;
          z-index: 3;
        }
      }
    }
</style>



