<template>
  <div class="head-div">
    <div>
      <!--1-->
      <div class="head-button person-head com-cursor-hander" v-if="isLogo" type="button">
        <img :src="userData.headIcon" class="com-div-middle-ab com-cursor-hander" @click="gotoNext(9)"/>
        <span @click="gotoNext(9)" v-text="userData.mobile" class="com-cursor-hander">Azalea</span>
        <span style="margin-left:0.1rem;" @click="logout()" class="com-cursor-hander ">[退出]</span>
      </div>
      <router-link :to="comHeadData[0].url"  tag="button" class="head-button com-cursor-hander" v-if="!isLogo">
        <img :src="comHeadData[0].img" v-if="comHeadData[0].img"/>
        <span v-text="comHeadData[0].name"></span>
      </router-link>
      <!--1-->
      <router-link :to="comHeadData[1].url" tag="button" class="head-button com-cursor-hander" v-if="!isLogo">
        <img :src="comHeadData[1].img" v-if="comHeadData[1].img"/>
        <span v-text="comHeadData[1].name"></span>
      </router-link>
      <!--1-->
      <router-link :to="comHeadData[2].url" tag="button" class="head-button com-cursor-hander" type="button">
        <img :src="comHeadData[2].img" v-if="comHeadData[2].img"/>
        <span v-text="comHeadData[2].name"></span>
      </router-link>
      <!--1-->
      <button type="button" class="head-button com-cursor-hander" @click="gotoNext(3)">
        <img :src="comHeadData[3].img" v-if="comHeadData[3].img"/>
        <span v-text="comHeadData[3].name"></span>
        <span class="car-num" v-if='carNum>0'><span v-text='carNum'>2</span></span>
      </button>
      <button type="button" class="head-button com-cursor-hander" @click="gotoNext(4)">
        <img :src="comHeadData[4].img" v-if="comHeadData[4].img"/>
        <span v-text="comHeadData[4].name"></span>
      </button>
    </div>
  </div>

</template>
<script type="text/javascript">
  import {comHeadData} from 'src/assets/componentsData.js';
  export default {
    props:['thisTime'],
    data(){
      return{
        comHeadData:comHeadData,
        isLogo:m$.sessionStores.get("pcClientDataUser")?true:false,
        userData: m$.sessionStores.get("pcClientDataUser")? m$.sessionStores.get("pcClientDataUser"):{},
        carNum:0
      }

    } ,
    methods:{
      gotoNext(oindex){
        this.getAjax(this.url.isLogged,{},(res)=>{
            // this.isLogo=false
          var willUrl=''
          if(oindex==9){
            willUrl='/my'
          } else {
            willUrl= this.comHeadData[oindex].url
          }
          this.$router.push(willUrl);
            if(res.code==200&&res.data==true){
              this.$router.push(willUrl);
            }
            else {
              this.isLogo=false;
              this.carNum=0;
            }
        })

      },
      gotoCar(){
        this.getAjax(this.url.isLogged,{},(res)=>{
          // this.isLogo=false
          if(res.code==200&&res.data==true){
            this.isLogo=true;
            this.getCarNum();
            this.getAjax(this.url.my,{},(res)=>{
              if(res.code==200){

                m$.sessionStores.set("pcClientDataUser",res.data);
                this.userData = res.data;
              }
            });
          }
        })
      },
      getCarNum(){
          this.getAjax(this.srcUrl.getCountByUserId,{},(res)=>{
                this.carNum=res.data;
          })
      },
      getiSLogo(){
          this.getAjax(this.url.isLogged,{},(res)=>{
            // this.isLogo=false
            if(res.code==200&&res.data==true){
              this.isLogo=true;
              this.getCarNum();
              this.getAjax(this.url.my,{},(res)=>{
                if(res.code==200){

                  m$.sessionStores.set("pcClientDataUser",res.data);
                  this.userData = res.data;
                }
              });
            }
          })
      },
      logout(){
        this.postAjax(this.url.logout,{},(res)=>{
          var othis=this;

          if(res.code==200&&res.message=='ok'){

            m$.sessionStores.remove("pcClientDataUser");
            this.isLogo=false;
            this.carNum=0;
            // m$.template({
            //   val:'操作<br>成功',
            //   time:1200,
            //   fn:function () {
            //
            //   }
            // })
          }
        })
      },

  },
watch:{
  "thisTime":function (val) {
    this.getiSLogo();
  }
},
    created(){
      // this.openLoginTemplate=true;
      this.getiSLogo();
    }
  }
</script>
<style scoped lang="less">

.head-div{
  width: 100%;
  min-width: 12rem;
  padding: 0.15rem 0;
  text-align: center;
  background-color: #f1f1f1;
  >div{
    display: inline-block;
    width: 12rem;
    text-align: right;

    >.head-button{
      text-align: center;
      display: inline-block;
      vertical-align: top;
      background-color: transparent;
      &.person-head{
position: relative;
        >img{
          left: -0.08rem;
          height: 0.27rem;
          width: 0.27rem;
          margin-right: 0;
          display: inline-block;
          border-radius: 100%;
        }
      }
      &:first-of-type{
        border-left: none;

      }
      &:last-of-type{
        padding: 0 0 0 0.27rem;
      }
      border-left:1px dashed #9C9C9C ;
      padding: 0 0.27rem;
      >*{
        display: inline-block;
        vertical-align: text-top;
      }
      >img{
        height: 100%;
        margin-right: 0.04rem;
      }
      >span{

        font-family:PingFangSC-Regular;
        font-size:0.18rem;
        color:#666666;
       line-height: 100%;
        letter-spacing:0px;
        text-align:left;
      }
      .car-num{
        position: relative;
        >span{
          position: absolute;
          left: -.05rem;
          top:-.1rem;
          font-size: .14rem;
          width: .15rem;
          height: .15rem;
          color: #fff;
          line-height: .15rem;
          border-radius: 1rem;
          background-color: #fb4874;
          display: inline-block;
          text-align: center;
        }
      }
    }

  }
}
</style>



