/*
*作者---杨彬
*
*/
<template>
  <div class="login">
    <div class="header-warp">
      <img src="../../assets/login/logo.png">
    </div>
    <section class="content-warp">
      <div class="com-div-center-center-ab content">
        <header class="">账号注册</header>
        <section>
          <div class="warp">
            <div style="margin-bottom: 0.19rem;">
              <img src="../../assets/login/account.png" class="com-div-middle-ab">
              <input class="full input" type="text" placeholder="请输入手机号" v-model="phone">
            </div>
            <div style="margin-bottom: 0.19rem;">
              <img src="../../assets/login/code.png" class="com-div-middle-ab">
              <input class="code input" type="tel" placeholder="短信验证码" v-model="codeCheck" :disabled='isGetCode'>
              <button class="code-button com-cursor-hander" @click="getAuthCode()" type="button"v-if="time==0">获取验证码</button>
              <button class="code-button" v-text="time+'s'"  v-if="time!=0" type="button"></button>
            </div>
            <div>
              <img src="../../assets/login/password.png" class="com-div-middle-ab">
              <input class="full input" type="password" placeholder="请输入6-16位英文数字字符组合密码" v-model="password">
            </div>
            <section class="notice" v-text="notice">
            </section>
            <div style="margin-top: 0.1rem;">
              <button class="full button com-cursor-hander" type="button" @click="nextStatus()">注&ensp;&ensp;册</button>
            </div>
            <section class="go-register">
              已有账号？
              <router-link tag="span" to="/login" class="span com-cursor-hander">立即登录</router-link>
            </section>
          </div>
        </section>
      </div>
    </section>
    <div class="foot-logo">
      © Copyright All rights reserved. | 版权所有: 成都友木科技有限公司
    </div>
  </div>
</template>
<script type="text/javascript">
  export default {
    data(){
      return{
        password: '',
        codeCheck: '',
        phone: '',
        time:0,
        selIt:'',
        notice:'',
        isGetCode:true
      }
    },
    methods:{ nextStatus(){
      var othis=this;
      let willSendData = {};
        willSendData['mobile'] = this.phone;
        willSendData['authCode'] = this.codeCheck;
        willSendData['password'] = this.password;
        this.postAjax(this.url.regist,willSendData,(res)=>{
          if(res.code==200){
            m$.template({
              time:1200,
              val:'注册成功',
              fn(){
                othis.$router.push('/index')
              }
            });
          }
          else {
           this.notice=res.message
          }
        })

      },
      getAuthCode(){
        var thisReg=/^1[3|4|5|7|8][0-9]{9}$/;
        let  autoCodeUrl='';//验证码初始地址
        var othis=this;
        if(!this.phone||thisReg.test(this.phone)==false){

            this.notice='请正确填写手机号'
        }
        else {
            autoCodeUrl = this.url.registAuthCode;
          this.notice='';
          this.postAjax(
            autoCodeUrl, {mobile: this.phone},
            (res)=> {
              if (res.code == 200) {
                this.isGetCode=false;
                m$.template({
                  time: 1200,
                  val: '验证码<br>已发送',
                  fn(){
                    othis.time = 59;

                    othis.selIt = window.setInterval(function () {
                      othis.time--;
                      if (othis.time == 0) {
                        window.clearInterval(othis.selIt);
                        othis.selIt='';
                      }
                    }, 1000)
                  }
                })
              }
              else {
               this.notice=res.message;
              }
            })
        }
    }
    },
    created(){
      m$.documentTitle("注册");
    },
    watch:{
      'phone':function (val) {
        var thisReg=/^1[3|4|5|7|8][0-9]{9}$/;
        if(thisReg.test(val)==false){
          this.notice='请正确填写手机号';
        }
       else {
          this.notice='';
        }
      },'password':function (val) {
        var pwdReg=/^[A-Za-z0-9]{6,16}$/;
        if(!pwdReg.test(val)){

          this.notice='密码由:6-16位字母数字组合';

        }
        else {
          this.notice='';
        }

      },'codeCheck':function (val) {
        this.notice='';
      },
    },
    beforeDestroy(){
      // m$.hiddenScroll(false);
      let othis=this;
      if(othis.selIt){
        window.clearInterval(othis.selIt);
      }
    }
  }
</script>
<style scoped lang="less">
  .go-register {
    height: 0.34rem;
    text-align: right;
    font-family: PingFangSC-Regular;
    font-size: 0.14rem;
    color: #e9e9e9;
    letter-spacing: 0.0087rem;
    line-height: 0.34rem;;
    .span {
      cursor: pointer;
      font-size: 0.14rem;
      color: #fb4874;;
    }
  }

  .header-warp {
    width: 12rem;
    text-align: left;
    margin: 0.78rem auto 0.2rem auto;
    img {
      height: 0.5rem;
    }
  }

  .content-warp {
    min-width: 12rem;
    height: 6.8rem;
    position: relative;
    background: url("../../assets/login/banner.jpg") no-repeat center;
    background-size: auto 100%;
    .content {
      width: 6rem;
      > header {
        background: rgba(0, 0, 0, 0.69);
        height: 0.5rem;
        font-family: PingFangSC-Regular;
        font-size: 0.22rem;
        line-height: 0.5rem;
        color: #ffffff;
        letter-spacing: 0px;

        text-align: center;
      }
      > section {
        background: rgba(0, 0, 0, 0.5);
        padding: 0.18rem 0 0.15rem 0;
        .warp {
          width: 3.2rem;
          margin: 0 auto;
      .notice{
        overflow: hidden;
        height: 0.34rem;
        line-height: 0.34rem;
        font-family:PingFangSC-Regular;
        font-size:0.14rem;
        color:#ff3838;
        letter-spacing:0.008rem;
        text-align:left;
      }
          div {
            > img {
              left: 0.06rem;
              height: 0.27rem;
            }
            position: relative;
            height: 0.38rem;
            .button {
              background: rgba(251, 72, 116, 0.9);
              font-family: PingFangSC-Regular;
              font-size: 0.18rem;
              color: #ffffff;
              letter-spacing: 0px;
              line-height: 0.32rem;
              text-align: center;
            }
            .code{
              vertical-align: top;
              background:#ffffff;
              border-radius:0.02rem;
              width:2.3rem;
              height:100%;
              padding: 0 0.2rem 0 0 ;
            }
            .code-button{
              vertical-align: top;
              font-family:PingFangSC-Regular;
              font-size:0.12rem;
              color:#fb4874;
              letter-spacing:0.0075rem;
              margin-left: 0.02rem;
              background: rgba(255, 255, 255, 0.9);
              border-radius:0.02rem;
              width:0.88rem;
              height:100%;
            }
            .input {

              background-color: #fff;
              padding-left: 0.4rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              color: #949494;
              letter-spacing: 0.0087rem;

              text-align: left;
            }
            .full {
              width: 100%;
              height: 100%;
              border-radius: 0.02rem;
              padding: 0 0.4rem;
            }
          }
        }
      }
    }
  }

  .foot-logo {
    padding: 1.75rem 0 0.6rem 0;
    font-family: PingFangSC-Regular;
    font-size: 0.16rem;
    color: #878787;
    letter-spacing: 0px;
    line-height: 100%;
    text-align: center;
  }
</style>



