/*
*作者---杨彬
*
*/
<template>
    <div class="coupon-item">
      <img :src="thisData[oitem.status+1]" class="couponBg">
      <section :class="'cp'+oitem.status">
        <p class="p1" v-text="oitem.couponName"></p>
        <p class="p2" v-text="oitem.gmtStart+'-'+oitem.gmtEnd"></p>
        <p class="p3" v-text="oitem.useIntro"></p>
      </section>
      <router-link class="click" tag="div" :to="'/my/myCoupon/couponDetail/'+oid" v-if="oitem.status!=-1">

      </router-link>
      <router-link class="click" tag="div" :to="'/decorate/message/'+oitem.id"
                   v-if="oitem.status==-1&&oitem.isDecorate==true">
      </router-link>
      <div class="click" @click="conponClick(oitem,oindex)" v-if="oitem.status==-1&&oitem.isDecorate==false">
      </div>
    </div>
</template>
<script type="text/javascript">
  import {couponImg} from "src/assets/componentsData.js";
  export default {
    data(){
      return {
        thisData: couponImg.small,
      }
    },
    props: ['oitem', 'oindex', 'oid'],
    methods: {
      conponClick(obj, sindex){
        let othis = this;
        this.getAjax(this.url.getCoupon, {id: obj.id}, (res) => {
          if (res.code == 200) {
            m$.template({
              time: 800,
              val: okImgDiv,
              fn(){
                othis.$emit('couponCkFn', [sindex, res.data])
              }
            })
          }
          else {
            m$.template({
              time: 1200,
              val: res.message,
            })
          }
        })
      }
    },
    created(){
    }
  }
</script>
<style scoped lang="less">
    .coupon-item{
      box-shadow:0px 2px 10px 0px rgba(179,179,179,0.50);
      width: 4.16rem;
      height: 1.03rem;
      position: relative;
      >.couponBg{
        max-width: 5.8rem;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        height: 100%;
      }
      >*{
        height: 100%;
        position: relative;
        z-index: 2;
      }
      >section{
        position: relative;
width: 3.18rem;
        &.cp1{
          .p1{color: #a0a0a0;}
          .p2{color: #a0a0a0;}
          .p3{color: #a0a0a0;}
        }
        &.cp2{
          .p1{color: #bebebe;}
          .p2{color: #bebebe;}
          .p3{color: #bebebe;}
        }
        >p{
          position: absolute;
          left: 0.54rem;

        }
        .p1{
top: 0.13rem;
          height: 0.26rem;
          width: 2.4rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-family:PingFangSC-Regular;
          font-size:0.26rem;
          color:#fb4874;
          letter-spacing:0px;
          line-height:100%;
          text-align:left;
        }
        .p2{
          top:0.44rem;
          font-family:PingFangSC-Regular;
          font-size:0.18rem;
          color:#484848;
          letter-spacing:0px;
          line-height:100%;
          text-align:left;
        }
        .p3{
          font-family:PingFangSC-Regular;
          font-size:0.18rem;
          max-width: 2.64rem;
          color:#a0a0a0;
          letter-spacing:0px;
          line-height:100%;
          text-align:left;
          bottom: 0.13rem;
          padding-right: 0.2rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      >.click{
        position: absolute;
        top: 0;
        right: 0;
        display: inline-block;
        cursor: pointer;
width: 0.98rem;
      }
    }
</style>



