<template>
  <div class="today-goods">
    <com-head></com-head>
    <nav-search class="index-search"></nav-search>
    <div style="background-color:#333;height:0.5rem;" v-if="$route.params.id==0"></div>
    <div class="type-banner" v-if="$route.params.id!=0">
      <img :src="isBanner">
    </div>
    <section class="type-container" v-if="!noData">
      <type-item :allGoodsData='allGoodsData' v-if='allGoodsData.length'></type-item>
      <div class="com-loading" v-if="isMoreData.more"><img src="../assets/loading.png"></div>
    </section>
    <section v-if="noData" class="type-container">
      <img src="../assets/noOrder.png" class="com-div-center-center-ab">
    </section>
    <com-foot style="margin-top:0.6rem;"></com-foot>
  </div>
</template>

<script type="text/javascript">
  import typeItem from 'src/components/com/typeItem.vue'
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import navSearch from 'src/components/index/navSearch.vue'
  export default {
    components: {typeItem, comFoot, navSearch, comHead},
    data(){
      return {
        productData: [
          // {name:'鱼子酱',oldPrice:'19',amount:"2",thumbnail:'1',sail:'4'},
          // {name:'慧儿面包',oldPrice:'29',amount:"2",thumbnail:'2',sail:'5'},
          // {name:'彬彬饼干',oldPrice:'39',amount:"2",thumbnail:'2',sail:'5'},
          // {name:'花花洗洁精',oldPrice:'9',amount:"2",thumbnail:'2',sail:'5'},
        ],
        isBanner: '',
        notice: '',
        allGoodsData: [],
        pageNum: 1,
        isMoreData: {
          init: true,
          more: true,

        },
        noData: false,
      }
    },
    methods: {
      goBuy(id){
        window.location.href = (`#/goodsDetail/${id}`);
      },
      getData(){
        let othis = this;
        if (othis.isMoreData.init) {
          m$.scroll(function () {
            othis.isMoreData.more = true;
          })
        }
        this.postAjax(this.srcUrl.getProductGroupShopVo, {
          pageNum: this.pageNum,
          pageSize: '10',
          status: 1,
          groupType: 2,
        }, (res) => {
          // this.productData = res.data.data;
          var j = res.data.data;
          othis.isMoreData.more = false;
          for (var i = 0; i < res.data.data.length; i++) {
            //groupType=2为 特价商品
            for (var ii = 0; ii < res.data.data[i].detailVos.length; ii++) {
                this.allGoodsData.push(res.data.data[i].detailVos[ii])
            }
          }
          if (j && j.length > 0 && j.length % 10 == 0) {
            othis.isMoreData.init = true;
            m$.scroll(function () {
              othis.pageNum++;
              othis.getData()
            })
          }
          else {
            m$.scroll(function () {
              othis.isMoreData.more = false;
            })
          }
          if (this.allGoodsData.length == 0) {
            this.noData = true;
          }
        })
      },
      postProductGroupDetailShopVoById(){
        let othis = this;
        if (othis.isMoreData.init) {
          m$.scroll(function () {
            othis.isMoreData.more = true;
          })
        }
        this.postAjax(this.srcUrl.getProductGroupDetailShopVoById, {groupId: this.$route.params.id}, (res) => {
          if (res.data) {

            if (res.data.groupImg) {
              this.isBanner = res.data.groupImg;
            }
            var j = res.data.detailVos;
            for (var i = 0; i < res.data.detailVos.length; i++) {
              //groupType=2为 特价商品
              this.allGoodsData.push(res.data.detailVos[i])
            }
            if (j && j.length > 0 && j.length % 10 == 0) {
              othis.isMoreData.init = true;
              m$.scroll(function () {
                othis.pageNum++;
                othis.getData()
              })
            }
            else {
              m$.scroll(function () {
                othis.isMoreData.more = false;
              })
            }
            if (this.allGoodsData.length == 0) {
              this.noData = true;
            }
          }
        })
      }
    },
    created () {
      if (this.$route.params.id == 0) {
        //控制是否显示banner
        this.getData();
      }
      else {
        this.postProductGroupDetailShopVoById();
      }


      m$.documentTitle("今日特价");
    }
  }
</script>
<style lang="less" scoped>
  //@import "${fileName}";
  .today-goods {
    .type-container {
      margin: 0 auto;
      width: 12rem;
      min-height: 6.5rem;
      _height: 6.5rem;
      position: relative;
      padding-top: 0.3rem;
    }
  }

  .type-banner {
    > img {
      width: 100%;
      max-height: 4.7rem;
    }
  }
</style>



