<template>
    <div id="sure-order">
      <address-box @cancelFn='cancelFnn' :isEdterC='isEdter' v-if='addressBoxBtn' ></address-box>
      <com-head></com-head>
      <goods-head :lineData='2'></goods-head>
        <div class="order">
          <div class="head">
            <span class="left">收货信息</span>
          </div>
          <div class="addr-box">

              <div class="addrs" v-for="(item,ind) in addrData" :class="{active:addrModel==item.id}">
                <div class="img-top"></div>
                <div class="addr " >
                  <span class="chang-addr"><input type="radio" v-model='addrModel' :value='item.id'></span>
                  <div class="info">
                      <div class="name" v-text='item.name'>liqinqin</div>
                      <div class="phone"  v-text='item.phone'>1669097898</div>
                  </div>
                  <div class="info border-b"  v-text='item.address'>
                    四川省成都市高新区剑南大道中段世豪广场
                  </div>
                  <div class="info" style="margin-top:.13rem">
                    <span class="set-detail" :class="{'addr-default':addrMessage==item.id}">
                      <span>设为默认</span>
                      <input type="radio" name="addr" v-model='addrMessage' :value='item.id'>
                    </span>
                    <span style="margin-left:1.32rem;margin-right:.27rem" @click="addressAdd(item)">修改</span>
                    <span @click="delAddrMessageFn(item)">删除</span>
                  </div>
                </div>
              </div>

          </div>
          <div class="pay-type">
            <div class="type1" @click="addressAdd(0)">
              <span style="color:#666"><img src="./../assets/goods/sureOrder/add.png">添加新地址</span>
            </div>
          </div>
          <div class="head">
            <span class="left">支付方式</span>
          </div>
          <div class="pay-type">
              <div class="type1 " :class="{type2:payType==1}">
                <input type="radio" v-model='payType' name="r" value="1">
                <span><img src="./../assets/goods/sureOrder/weixin.png">微信支付</span>
              </div>

              <div class="type1" :class="{type2:payType==2}">
                <input type="radio" v-model='payType' name="r" value="2">
                <span><img src="./../assets/goods/sureOrder/zhifubao.png">支付宝支付</span>
              </div>
          </div>
          <div class="head">
            <span class="left">商品订单</span>
            <router-link to="/productCar" tag="span" class="right com-cursor-hander">返回购物车修改 <span><img src="./../assets/goods/sureOrder/01.png"></span></router-link>
          </div>
          <div class="table-head">
            <table>
              <tr class="tr1">
                <td style="width:66.66%">商品信息</td>
                <td style="width:16.666%">数量</td>
                <td style="width:16.666%">单价</td>
              </tr>
              <tr class="tr2" v-for='items in sendProduct.items'>
                <td>
                  <!--<span><img src="./../assets/goods/sureOrder/choice_1.png"></span>-->
                  <span class="img"><img :src="items.thumbnail"></span>
                  <span v-text='items.name'>Gulogo 撞色拼接时装鞋</span>
                </td>
                <td v-text='items.quantity'></td>
                <td v-text='items.price'>¥218.00</td>
              </tr>
            </table>
          </div>
          <div class="order-info">
            <div>
              <span class="span1" v-text="'共有'+sendProduct.quantity+'件商品 总商品金额：'">共有3件商品 总商品金额：</span>
              <span class="span2" v-text="'￥'+sendProduct.totalAmount">¥898.00</span>
            </div>
            <div>
              <span class="span1">总运费：</span>
              <span class="span2" v-text="'￥'+sendProduct.freight">¥898.00</span>
            </div>
            <div>
              <span class="span1">优惠券抵扣：</span>
              <span class="span2">¥ 0</span>
            </div>
          </div>
          <div class="pay-money">
            <div class="left">
              <!--<span>付款剩余时间：</span><span class="red">00:30:00</span>-->
            </div>
            <div class="right"><span>合计金额：</span><span class="red" v-text="'￥'+sendProduct.paidAmount">¥898.00</span><span class="pay-order com-cursor-hander" @click="createOrder">提交订单</span></div>
          </div>
        </div>
      <com-foot style="margin-top:0.6rem;"></com-foot>

    </div>
</template>

<script type="text/javascript">
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import goodsHead from 'src/goodsPages/comGoods/goodsHead.vue'
  import addressBox from 'src/goodsPages/comGoods/address.vue'
  export default{
    components:{goodsHead,comHead,comFoot,addressBox},
        data(){
            return{
              payType:'1',
              addrMessage:'',
              addrBtn:false,
              addrData:[
                    // {name:'liqinqin1',phone:'1324567',addr:'四川省成都市高新区剑南大道中段世豪广场1',isDetail:true},
                    // {name:'liqinqin2',phone:'345678',addr:'四川省成都市高新区剑南大道中段世豪广场2',isDetail:false},
                    // {name:'liqinqin3',phone:'345678',addr:'四川省成都市高新区剑南大道中段世豪广场3',isDetail:false},
                    // {name:'liqinqin3',phone:'345678',addr:'四川省成都市高新区剑南大道中段世豪广场3',isDetail:false},
                  ],
              sendProduct: {
                items: [],
                quantity: 0,
                freight: 0,
                totalAmount: 0,
                paidAmount: 0,
              },
              addrModel:'',
              mark:'',
              addressBoxBtn:'',
              isEdter:''
            }
        },
      watch:{
        payType(val){
            console.log(val)
        },
        addrMessage(val){

          this.goCheckRadio(val)
        },
        addrModel(val){
            console.log(val)
        }
      },
      methods:{
        cancelFnn(boo){
          // this.getAddress();
          this.addressBoxBtn=false;
          if(boo==true){
            this.getAddress();
          }

        },
        addressAdd(boo){
            if(boo){
                this.isEdter = boo
            }else {
              this.isEdter = false
            }
            this.addressBoxBtn = true;
        },
        goCheckRadio(oid){
          // console.log(this.itemRadio)
          this.postAjax(this.url.updateReceiver,{id:oid,isDefault:1},(res)=>{

          })
        },
        delAddrMessageFn(item){
            this.postAjax(this.url.deleteReceiver,{id:item.id},(res)=>{
                if(res.code==200){
                    this.getAddress()
                }
            })
        },
        createOrder () {
          m$.template({
            val:'',
          });
          var obj = {};
          obj.receiverId=this.addrModel?this.addrModel:this.addrMessage;
          obj.remark = this.mark//备注
          if(!obj.receiverId){
            m$.template({
              val:'请选择<br>地址信息',
              time:700
            });
            return;
          }
          for (var i in this.sendProduct) {
            if (i != 'items') {
              obj[i] = this.sendProduct[i]
            }
          }
          for (var i = 0; i < this.sendProduct.items.length; i++) {
            for (var skey in this.sendProduct.items[i]) {
              obj['items[' + i + '].' + skey] = this.sendProduct.items[i][skey];
              if (skey == 'thumbnail') {
                obj['items[' + i + '].' + skey] = this.sendProduct.items[i][skey].split(',')[1];
              }
            }
          }

          this.postAjax(this.srcUrl.createOrderUrl, obj, (res) => {
            if (res.code == 200) {
                if(res.data[0].paidAmount==0){
                    m$.template({
                      val:'0元<br>秒杀成功',
                      time:1000,
                      fn(){
                        window.location.href=(`#/my/myOrder/type/3`);
                      }
                    });
                }else {
                  m$.localStrages.set('payInfo', res.data);
                  var _this=this;
                  m$.template({
                    val:'处理中...',
                    time:800,
                    fn(){
                      if(_this.payType==1){
                        window.location.href=(`#/goPay/1`)
                      }else if(_this.payType==2){
                        window.location.href=(`#/goPay/2`)
                      }
                    }
                  });
                }
            } else {
              m$.template({
                val: res.message,
                time: 1000
              })
            }
          })
        },
        getAddress () {
          this.getAjax(this.srcUrl.findReceiverUrl, {isDefault: '', receiveId: ''}, (res) => {
            if (res.code==200) {
              this.addrData = [];
                for (var i =0;i<res.data.length;i++){
                  var addr = {};
                  addr.address = res.data[i].provinceName + res.data[i].cityName + res.data[i].districtName + res.data[i].detailAddress + '';//地址
                  addr.phone = res.data[i].phone;//电话
                  addr.name = res.data[i].consignee;//人
                  addr.id = res.data[i].id;//地址id
                  addr.isDefault = res.data[i].isDefault;//地址id
                  this.addrData.push(addr);
                  if( res.data[i].isDefault==1){
                      this.addrMessage=res.data[i].id;
                      this.addrModel=res.data[i].id;
                  }
                }
            }
          })
        },
      },
      mounted(){
            // console.log(m$.sessionStores.get("products"));
        m$.documentTitle("提交订单");
        this.getAddress();
        //判断来自商品详情页 还是  购物车
        var _this = this;
        // console.log(m$.sessionStores.get("products").length)
        if (m$.sessionStores.get("products").length) {
          var goods = m$.sessionStores.get("products");
          var oflog = -1;
          for (var ii = 0; ii < goods.length; ii++) {
            m$.imgRead(goods[ii].productImg, {quality: 1}, function (e) {
              oflog++;
              var iii = {};
              iii.quantity = parseFloat(goods[oflog].quantity);
              iii.productId = goods[oflog].productId;
              iii.name = goods[oflog].productName;
              iii.digest = goods[oflog].digest;
              iii.price = parseFloat(goods[oflog].endPrice);
              iii.cartId = goods[oflog].id;
              iii.businessId = goods[oflog].businessId;
              _this.sendProduct.quantity += goods[oflog].quantity;
              // console.log(iii.quantity +"=="+ iii.price)
              _this.sendProduct.totalAmount += goods[oflog].quantity * goods[oflog].endPrice;

              iii.thumbnail = e;
              _this.sendProduct.items.push(iii);

              // ii++;
              if(oflog==(goods.length-1)){
                _this.sendProduct.paidAmount = _this.sendProduct.totalAmount + _this.sendProduct.freight;
                  _this.sendProduct.totalAmount=parseFloat(_this.sendProduct.totalAmount.toFixed(2))
                _this.sendProduct.paidAmount=parseFloat(_this.sendProduct.paidAmount.toFixed(2));
              }



            });
          }
          // console.log(this.sendProduct)

        }
        else {
          var iii = {};
          // console.log(m$.sessionStores.get("products"))
          iii.quantity = parseFloat(m$.sessionStores.get("products").quantity);
          iii.price = parseFloat(m$.sessionStores.get("products").endPrice);
          iii.name = m$.sessionStores.get("products").name;
          iii.digest = m$.sessionStores.get("products").digest;
          iii.productId = m$.sessionStores.get("products").id;//商品项商品id
          iii.businessId = m$.sessionStores.get("products").businessId;//商家id
          iii.cartId = '';//购物车id

          _this.sendProduct.totalAmount = iii.quantity * iii.price;//zong
          // console.log(iii.quantity +"=="+ iii.price)
          _this.sendProduct.paidAmount = iii.quantity * iii.price;//实际支付金额
          _this.sendProduct.quantity = iii.quantity;
          m$.imgRead(m$.sessionStores.get("products").thumbnail, {quality: 1}, function (e) {
            iii.thumbnail = e;
            _this.sendProduct.items.push(iii);
              _this.sendProduct.totalAmount=parseFloat(_this.sendProduct.totalAmount.toFixed(2))
              _this.sendProduct.paidAmount=parseFloat(_this.sendProduct.paidAmount.toFixed(2));

          });
        }

      },
      created(){

      }
    }
</script>
