<template>
  <div class="navSearch">
    <div class="one">
      <div class="search-warp">
        <router-link tag="img" :src="indexSearchData.search.logoImg" class="logoImg com-div-middle-ab com-cursor-hander" to="/index"></router-link>
        <section :class="searchHidden?'searchHidden':''">

          <div class="search">
            <input type="text" v-model="searchVal" @keydown.enter="thisGoSearch(searchVal)">
            <button @click="thisGoSearch(searchVal)"class="com-cursor-hander"><img :src="indexSearchData.search.searchImg"></button>
          </div>
          <div class="active-product">
            <span v-for="(oitem,oindex) in indexSearchDataReal" :key="oindex" v-text="oitem" class="com-cursor-hander" @click="clickChangeVal(indexSearchData.search.activeText[oindex])"></span>
          </div>
        </section>
        <img src="../../assets/index/qcode.png" class="qrcodeImg com-div-middle-ab">
      </div>
    </div>
    <div :class="bgVal?'nav-bg':''" class="nav-warp">
      <div class="com-div-center-ab">
        <div class="warp-vav-item" v-for="(oitem,oindex) in indexSearchData.nav" :key="oindex" :class="{'opcity-bgc':oindex==1}">
          <router-link :to="xitem.url" tag="div" v-for="(xitem,xindex) in oitem" :key="xitem"
                       class="nav-button com-cursor-hander" v-text="xitem.name" active-class="nav-button-active">
          </router-link>
          <div v-for="(xitem,xindex) in oitem" :key="xitem" @click="checkIsLogin(xitem.url)" v-if="oindex>=4&&oindex<=5"
               class="nav-button com-cursor-hander nav-button-top" v-text="xitem.name" active-class="nav-button-active">
          </div>
        </div>
      </div>
    </div>

    <section class="head-login" v-if="openLoginTemplate">
      <div class="com-div-center-center-ab head-login-warp">
        <header class="">登&ensp;&ensp;录<img src="../../assets/decorate/maskClose.png" class="com-div-middle-ab com-cursor-hander" @click="nogoLogin"></header>

        <section>
          <div class="warp">
            <div style="margin-bottom: 0.19rem;">
              <img src="../../assets/login/account.png" class="com-div-middle-ab">
              <input class="full input" type="text" v-model="mobile" placeholder="请输入手机号">
            </div>
            <div>
              <img src="../../assets/login/password.png" class="com-div-middle-ab">
              <input class="full input" type="password"  v-model="password"
                     placeholder="请输入6-16位英文数字字符组合密码" @keyup.enter="goToSubmit(mobile,password)">
            </div>
            <div class="remember"style="margin-bottom: 0rem;">
              <label class="remember-label">
                <input type="checkbox" v-model="isReme" value="1">
                <span class="img"><img src="../../assets/login/check.png" class="com-div-center-center-ab"></span>
                <span class="span com-cursor-hander">记住用户名</span></label>
              <router-link tag="span" to="/forget" class="span2 com-div-middle-ab" style="right:0;">忘记密码？</router-link>
            </div>
            <div class="notice"style="margin-bottom: 0.14rem;" v-text="notice"></div>
            <div>
              <button class="full button com-cursor-hander" type="button" @click="goToLoginSubmit(mobile,password)">登录</button>
            </div>
            <section class="go-register">
              没有账号？
              <router-link tag="span" to="/register" class="span com-cursor-hander">立即注册</router-link>
            </section>
          </div>
        </section>
      </div>
    </section>
  </div>

</template>
<script type="text/javascript">
  import {indexSearchData} from 'src/assets/componentsData.js';
  export default {
    props:['bgVal','searchHidden'],
    data(){
      return{
        indexSearchData:indexSearchData,
        searchVal:'',
        indexSearchDataReal:[],
        mobile:  m$.localStrages.get('accountPcData')?m$.localStrages.get('accountPcData'):'',
        password: "",
        isReme:1,
        notice:'',
        openLoginTemplate:false,
        isloginstatus:"",
      }

    },
    methods:{
      nogoLogin(){
        var othis=this;
        othis.openLoginTemplate=false;
        othis.isloginstatus='';
        m$.hiddenScroll(false)
      }
      ,
      checkIsLogin(oindex){
        var othis=this;
        this.getAjax(this.url.isLogged,{},(res)=>{
          if(res.code==200&&res.data==true){
            this.$router.push(oindex)
          }
          else {
            othis.openLoginTemplate=true;
            othis.isloginstatus=oindex;
            m$.hiddenScroll(true)
          }
        })
      },
      goToLoginSubmit(oname, opassword){
        if (oname == "" || opassword == "") {
          this.notice='请输入手机号及密码'
          return;
        }
        else {
          this.postAjax('/user/login',//登录请求
            {mobile: oname, password: opassword},
            (res) => {
              if(res.code==200){
                m$.localStrages.set('yxdyb',res.data);
                if(this.isReme==1){
                  m$.localStrages.set('accountPcData',oname);
                }
                else {
                  m$.localStrages.remove('accountPcData');
                }
                this.openLoginTemplate=false;
                m$.hiddenScroll(false);
                this.$router.push(this.isloginstatus)

              }
              else {
                this.notice=res.message;
              }
            })
        }

      },
      clickChangeVal(val){
        this.searchVal=val;
        this.thisGoSearch(val)
      },
      thisGoSearch(val){
        if(this.$route.name=='productType'){
          this.$emit("goSearch",val);
        }
        else {
          m$.sessionStores.set("thisPcSearchVal",val);
          this.$router.push('/goodsItem');
        }

      }
    },
    created(){
      this.getAjax(this.url.findProductSearchHotKeywords,{},(res)=>{

        this.indexSearchDataReal=[];
        for(var i=0,j=res.data;i<j.length;i++){
          var ostring=j[i];
          if(ostring.length>=4){
            ostring=ostring.substring(0,4)+'...'
          }
          this.indexSearchDataReal.push(ostring)

        }
        this.indexSearchData.search.activeText=res.data;
      });
      if(this.$route.fullPath=='/goodsItem/type/0'){
        if(m$.sessionStores.get('thisPcSearchVal')||m$.sessionStores.get('thisPcSearchVal')===0){
          this.searchVal=m$.sessionStores.get('thisPcSearchVal');
           m$.sessionStores.remove('thisPcSearchVal');
        }
      }
      if(!m$.sessionStores.get('getProType')){
        var navProType=[];
        this.getAjax(this.srcUrl.getAllBusinessTypeUrl,{isFilter:1},(res)=>{
          //配置导航栏
          for(var i =0; i<res.data.length;i++){
            var ii = {};
            ii.name = res.data[i].name//导航名字
            ii.url =`/goodsItem/type/${res.data[i].id}`;//导航地址
            ii.id=res.data[i].id;
            ii.icon=res.data[i].icon;
            navProType.push(ii);
          }
          m$.sessionStores.set('getProType',navProType);
          var onav=navProType;
          for(var i=0;i<onav.length;i++){

            var oflag=false;
            for(var k=0,h=this.indexSearchData.nav[1];k<h.length;k++){
              if(h[k].url==onav[i].url){
                oflag=true;
              }

            }
            if(oflag==false){
              this.indexSearchData.nav[1].push({name: onav[i].name,url:onav[i].url});
            }
          }
        });
      }
      else {
        var onav=m$.sessionStores.get('getProType');
        for(var i=0;i<onav.length;i++){

          var oflag=false;
          for(var k=0,h=this.indexSearchData.nav[1];k<h.length;k++){
            if(h[k].url==onav[i].url){
              oflag=true;
            }

          }
          if(oflag==false){
            this.indexSearchData.nav[1].push({name: onav[i].name,url:onav[i].url});
          }
        }
      }
    }
  }
</script>
<style scoped lang="less">
  .head-login{
    position: fixed;
    z-index: 333;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /*min-width: 12rem;*/
    /*_width: 12rem;*/
    min-height: 3.3rem;
    _height:3.3rem;
    background: rgba(19, 19, 19, 0.44);
    .go-register {
      height: 0.34rem;
      text-align: right;
      font-family: PingFangSC-Regular;
      font-size: 0.14rem;
      color: #474747;
      letter-spacing: 0.0087rem;
      line-height: 0.34rem;;
      .span {
        cursor: pointer;
        font-size: 0.14rem;
        color: #fb4874;;
      }
    }
    .head-login-warp{
      width: 6rem;
      > header {
        background:#fb5a81;
        height: 0.5rem;
        font-family: PingFangSC-Regular;
        font-size: 0.22rem;
        line-height: 0.5rem;
        color: #ffffff;
        letter-spacing: 0px;
        position: relative;
        text-align: center;
        img{
          right: 0.3rem;
          height: 0.3rem;
        }
      }
      > section {
        background:#ffffff;
        padding: 0.18rem 0 0.15rem 0;
        .warp {
          width: 3.2rem;
          margin: 0 auto;
          .notice{
            height: 0.2rem;
            line-height: 0.2rem;
            font-family:PingFangSC-Regular;
            font-size:0.14rem;
            color:#ff3838;
            letter-spacing:0.008rem;
            text-align:left;
          }
          .remember {
            text-align: left;
            margin: 0 0 0.17rem 0;
            line-height: 0.36rem;
            position: relative;
            .span {
              cursor: pointer;
              margin-left: 0.06rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              color: #333;
              letter-spacing: 0.0087rem;
              text-align: left;
            }
            .span2{
              cursor: pointer;
              margin-left: 0.06rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              letter-spacing: 0.0087rem;
              text-align: left;
              color:#fb5a81;
            }
            .remember-label {
              display: inline-block;
              height: 0.16rem;
              line-height: 0.16rem;

              >*{
                vertical-align: top;
              }
              input{
                display: none;
              }
              input:checked ~ .img {

                img {
                  display: inline-block;

                }
              }
              .img {
                display: inline-block;
                width: 0.16rem;
                height: 0.16rem;
                position: relative;
                background-color: #fff;
                border:1px solid #979797;
                img {
                  display: none;
                  width: 0.12rem;
                }

              }
            }

          }
          div {
            > img {
              left: 0.06rem;
              height: 0.27rem;
            }
            position: relative;
            height: 0.38rem;
            .button {
              background: #333333;
              font-family: PingFangSC-Regular;
              font-size: 0.18rem;
              color: #ffffff;
              letter-spacing: 0px;
              line-height: 0.32rem;
              text-align: center;
            }
            .input {

              background-color: #fff;

              padding-left: 0.4rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              color: #949494;
              border:1px solid #979797;

              letter-spacing: 0.0087rem;

              text-align: left;
            }
            .full {
              width: 100%;
              height: 100%;
              border-radius: 0.02rem;
              padding: 0 0.4rem;
            }
          }
        }
      }

    }
  }
  .navSearch{
    text-align: center;
    > .nav-warp{
      margin-top: 0.2rem;
      position: relative;
      height: 0.44rem;
      background-color: white;
      >div{
        margin: 0 auto;
        width: 12rem;
        text-align: left;
        position: absolute;
        height: 0.44rem;
        top: 0;
        z-index: 20;
        background-color: inherit;
      }
      .warp-vav-item{
        display: inline-block;
        vertical-align: top;
        height: 0.44rem;
        overflow: hidden;
        cursor: pointer;
        position: relative;
        background-color: inherit;
      }
      .warp-vav-item:hover{
        overflow: visible;
        cursor: pointer;
        background-color: inherit;
      }
      .opcity-bgc:hover{
        >.nav-button{
          background-color: inherit;
          opacity: .8;
          &.nav-button-active{
            border-bottom: none
          }
        }
      }
      .nav-button{
        &.nav-button-top{
          position: absolute;
          z-index: 3;
          top: 0;
          left: 0;
          opacity: 0;
        }
        background-color: inherit;
        height: 0.44rem;
        padding: 0 0.32rem;
        position: relative;
        font-family:PingFangSC-Medium;
        font-size:0.2rem;
        color: #333333;
        letter-spacing:0px;
        line-height:0.44rem;
        cursor: pointer;
        text-align:center;
        font-weight: bold;
        &.nav-button-active{
          color:#fb4874;
          border-bottom: 0.02rem solid #fb4874;
        }
      }
    }
    >.nav-bg{
      background-color: #333;
      .nav-button{
        color: #fff;
        cursor: pointer;
      }
    }
    >.one{
      margin: 0 auto;
      width: 12rem;
      .search-warp{
        min-height: 0.78rem;
        .logoImg{
          left: 0;
          height: 0.66rem;
        }
        .qrcodeImg{
          right:0;
          height: 0.78rem;
        }
        position: relative;
        >.searchHidden{
          display: none;
        }
        >section{
          display: inline-block;
          min-height: 0.78rem;
          .search{
            >* {vertical-align: top;}
            input{
              background-color: transparent;
              width: 5.32rem;
              height: 0.44rem;
              font-size: 0.18rem ;
              padding: 0 0.15rem;
              color: #222222;
              border: 0.02rem solid #333333;
              border-right: none;
            }
            button{
              background:#333333;
              width: 1rem;
              height:0.44rem;
              >img{
                height: 0.26rem;
              }
            }
          }
          .active-product{
            text-align: left;
            padding: 0.16rem 0 0.02rem 0;
            max-width: 6.32rem;
            >span{
              margin-left: 0.2rem;
              font-family:PingFangSC-Regular;
              font-size:0.16rem;
              color:#999999;
              letter-spacing:0px;
              line-height:100%;
              text-align:left;
              &:first-of-type{
                margin-left: 0;
              }
            }
          }

        }

      }
    }
  }

</style>



