/*
*作者---杨彬
*
*/
<template>
    <div class="coupon-item">
      <img :src="thisData[oitem.status+1]" class="couponBg">
    <section>
      <p class="p1" v-text="oitem.couponName"></p>
      <p class="p2" v-text="oitem.gmtStart+'-'+oitem.gmtEnd"></p>
      <p class="p3" v-text="oitem.useIntro"></p>
    </section>
      <router-link class="click" tag="div" :to="'/my/myCoupon/couponDetail/'+oid" v-if="oitem.status!=-1">

      </router-link>
      <router-link class="click" tag="div" :to="'/decorate/message/'+oitem.id"
                   v-if="oitem.status==-1&&oitem.isDecorate==true">
      </router-link>
      <div class="click" @click="conponClick(oitem,oindex)" v-if="oitem.status==-1&&oitem.isDecorate==false">
      </div>
    </div>
</template>
<script type="text/javascript">
  import {couponImg} from "src/assets/componentsData.js";
  export default {
    data(){
      return {
        thisData: couponImg.big,
      }
    },
    props: ['oitem', 'oindex', 'oid'],
    methods: {
      conponClick(obj, sindex){
        let othis = this;
        this.getAjax(this.url.getCoupon, {id: obj.id}, (res) => {
          if (res.code == 200) {
            m$.template({
              time: 800,
              val: okImgDiv,
              fn(){
                othis.$emit('couponCkFn', [sindex, res.data])
              }
            })
          }
          else {
            m$.template({
              time: 1200,
              val: res.message,
            })
          }
        })
      }
    },
    created(){
    }
  }
</script>
<style scoped lang="less">
    .coupon-item{
      box-shadow:0px 2px 10px 0px rgba(179,179,179,0.50);
      width: 5.8rem;
      height: 1.43rem;
      position: relative;
      >.couponBg{
        max-width: 5.8rem;
        position: absolute;
        left: 0;
       top: 0;
        z-index: 1;
        height: 100%;
      }
      >*{
        position: relative;
        z-index: 2;
        height: 100%;
      }
      >section{

        position: relative;
width: 4.44rem;
        >p{
          position: absolute;
          left: 0.68rem;

        }
        .p1{
top: 0.28rem;
          height: 0.26rem;
          width: 3rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-family:PingFangSC-Regular;
          font-size:0.26rem;
          color:#fb4874;
          letter-spacing:0px;
          line-height:100%;
          text-align:left;
        }
        .p2{
          top:0.64rem;
          font-family:PingFangSC-Regular;
          font-size:0.18rem;
          color:#484848;
          letter-spacing:0px;
          line-height:100%;
          text-align:left;
        }
        .p3{
          font-family:PingFangSC-Regular;
          font-size:0.18rem;
          width: ;
          color:#a0a0a0;
          letter-spacing:0px;
          line-height:100%;
          text-align:left;
          bottom: 0.29rem;
        }
      }
      >.click{
        position: absolute;
        top: 0;
        right: 0;
        display: inline-block;
        cursor: pointer;
width: 1.34rem;
      }
    }
</style>



