<template>
  <div id="product-car">
    <com-head></com-head>
    <goods-head :lineData='1'></goods-head>
    <div class="car" v-if='detailData.length'>
      <table>
        <tr class="t-header">
          <td class="che" style="width:4.16%">
                      <span class="allcheck">
                        <input type="checkbox" @click="goAllCheck" :checked="allcheck">
                        <img src="./../assets/goods/car/allChoice_no.png" v-if='!allcheck'>
                        <img src="./../assets/goods/car/allChoice.png" v-if='allcheck'>
                      </span>
          </td>
          <td style="width:41.66%">全选</td>
          <td style="width:12.5%">单价</td>
          <td style="width:12.5%">数量</td>
          <td style="width:12.5%">小计</td>
          <td style="width:12.5%">操作</td>
        </tr>
        <tr class="t-body" v-for='(item,ind) in detailData'>
          <td>
                      <span class="aCheck">
                        <input type="checkbox" v-model='check' :value='item' class="all-choice">
                        <img src="./../assets/goods/car/choice_no.png" class="choice_no">
                        <img src="./../assets/goods/car/choice.png" class="choice">
                      </span>
          </td>
          <td class="name">
            <div class="img">
              <img :src="item.productImg">
            </div>
            <span v-text='item.productName'>Gulogo 撞色拼接时装鞋</span>
          </td>
          <td v-text="'¥'+item.endPrice">218.00</td>
          <td class="car-num">
            <div>
              <div @click="changQuantity(0,item)"><img src="./../assets/goods/car/jian_w.png"
                                                       class="com-div-center-center-ab com-cursor-hander ">
              </div>
              <div><input type="text" v-model='item.quantity' @input='changeQuantity(ind,item,0)'
                          @blur='changeQuantity(ind,item,1)' maxlength="4"></div>
              <div @click="changQuantity(1,item)"><img src="./../assets/goods/car/jia_r.png"
                                                       class="com-div-center-center-ab com-cursor-hander "></div>
            </div>
          </td>
          <td style="color:#fb4874" v-text="'¥'+item.amountPrice">¥218.00</td>
          <td><img src="./../assets/goods/car/del_1.png" class="com-cursor-hander" @click="upDel(1,item.id)"></td>
        </tr>
      </table>
      <div class="goods-total">
        <span>商品金额：<span v-text="'￥'+allPay.money">￥788</span></span>
      </div>
      <!--<div style="width:100%;height:.74rem"></div>-->
      <div class="pay-money">
        <div class="left">
          <span class="aCheck">
                        <input type="checkbox" @click="goAllCheck" :checked="allcheck">
                        <img src="./../assets/goods/car/choice_no.png" v-if='!allcheck'>
                        <img src="./../assets/goods/car/choice.png" v-if='allcheck'>
                      </span>
          <span>全选</span>
          <router-link style="margin-left:.4rem;margin-right:.18rem;cursor:pointer" tag="span" to="/goodsItem">继续购物</router-link>
          <!--<span style='color:#979797;margin-right:.18rem;'>|</span>-->
          <!--<span>删除选中商品</span>-->
        </div>
        <div class="right">
          <span>已选商品 <span v-text='allPay.amount'>9</span> 件</span>
          <span style="margin-left:.3rem;margin-right:.1rem">合计金额：<span v-text="'￥'+allPay.money">¥898.00</span></span>
          <span class="go-buy" @click="goBuy">结算</span>
        </div>
      </div>
    </div>
    <div class="no-car" v-if='!detailData.length'>
      <div class="login">
        <img src="./../assets/goods/car/pigCar.png">
      </div>
      <p>您的购物车还是空空的，去看看心仪的商品吧~</p>
      <router-link to="/goodsItem" class="btn" tag="div"><span>去购物</span></router-link>
    </div>
    <div class="goods-del-box" v-if='upDelBox'>
      <div class="up-box com-div-center-center-ab">
        <div style="margin-top:.67rem;margin-bottom:.32rem"><span> <img
          src="./../assets/goods/car/del_2.png"></span><span style='font-size:.24rem'>删除商品</span></div>
        <div><span>确定要删除该商品吗？</span></div>
        <div>
          <button class="button1 com-cuesor-hander" @click="goodsDel">确定</button>
          <button class="button2 com-cuesor-hander" @click="upDel(0,0)">取消</button>
        </div>
        <span @click="upDel(0,0)" class="com-cursor-hander"><img src="./../assets/goods/car/close.png"></span>
      </div>
    </div>
    <div style="height:1.75rem"></div>
    <div id='footer'>
      <com-foot></com-foot>
    </div>

  </div>
</template>

<script type="text/javascript">
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import goodsHead from 'src/goodsPages/comGoods/goodsHead.vue'
  export default{
    components: {goodsHead, comHead, comFoot},
    data(){
      return {
        detailData: [],
        viewData: [],
        check: [],
        allcheck: true,
        upDelBox: false,
        goodsId: '',
        allPay: {
          money: 0,
          amount: 0
        },
        check1_btn: true
      }
    },
    watch: {
      detailData(val){
        console.log(val)
      },
      check(val){
        if (val.length == this.detailData.length) {
          this.allcheck = true
        } else {
          this.allcheck = false
        }
        this.num();
      },

    },
    methods: {
      goAllCheck(){
        if(this.allcheck==true){
          this.allcheck=false;
          this.check=[];
        }
        else {
          this.allcheck=true;
          this.check=[];
          for(var i=0,j=this.detailData;i<j.length;i++){
            this.check.push(j[i])
          }

        }
      },
      changeQuantity(ind, item, btn){
          if(btn){
              if(item.quantity==''){
                item.quantity = 1
              }
          }
        var numgeG = /^[0-9]{1,6}$/;
        if (item.quantity && (item.quantity <= 0 || numgeG.test(item.quantity) == false)) {
          item.quantity = 1
        }
        this.detailData[ind].amountPrice = parseFloat((this.detailData[ind].endPrice * this.detailData[ind].quantity).toFixed(2));
        this.num();
        if(item.quantity!=''){
          this.updateCart(item,item.quantity)
        }

      },
      upDel(boo, id){
        if (boo) {
          if (id) {
            this.goodsId = id;
          }
          this.upDelBox = true
        } else {
          this.upDelBox = false
        }
      },
      goodsDel(){
        this.upDelBox = false;
        this.postAjax(this.srcUrl.deleteCartUrl, {id: this.goodsId}, (res) => {
          if (res.code == 200) {
            m$.template({
              val: okImgDiv,
              time: 1000,
              fn(){
                window.location.reload()

              }
            })
          } else {
            m$.template({
              val: res.message,
              time: 1000
            })
          }
        })
      },
      goBuy(){
        if (this.check.length) {
          m$.sessionStores.set("products", this.check);
          window.location.href = ('#/sureOrder');
        } else {
          m$.template({
            val: '请选择商品',
            time: 1000
          })
        }
      },
      updateCart(item, num){
        this.postAjax(this.srcUrl.updateCartUrl, {
          id: item.id,
          productId: item.productId,
          quantity: num
        }, (res) => {
          if (res.code == 200) {
            // window.location.reload()
          } else {
              this.getDetailData();
            m$.template({
              val: res.message,
              time: 800,
            })

          }
        })
      },
      changQuantity(boo, item){
        for (var i = 0; i < this.detailData.length; i++) {
          if (this.detailData[i].id == item.id) {
            if (boo) {
              this.detailData[i].quantity++
            } else {
              if(this.detailData[i].quantity>1){
                this.detailData[i].quantity--
              }
            }
            this.detailData[i].amountPrice = parseFloat((this.detailData[i].endPrice * this.detailData[i].quantity).toFixed(2));
          }
        }
        this.updateCart(item,item.quantity);
        this.num()
      },
      num(){
        this.allPay.money = 0;
        this.allPay.amount = 0;
        for (var i = 0; i < this.check.length; i++) {
          this.allPay.money += this.check[i].endPrice * this.check[i].quantity;
          this.allPay.amount += parseInt(this.check[i].quantity?this.check[i].quantity:0);
        }
        this.allPay.money = this.allPay.money.toFixed(2)
      },
      getDetailData(){

        this.getAjax(this.srcUrl.getCartVoUrl, {
          pageNum: '',
          pageSize: ''
        }, (res) => {
          if (res.code == 200) {
            this.detailData=[];
            this.check=[];
            for (var j = 0; j < res.data.length; j++) {
              var ii = {};
              ii.businessId = res.data[j].businessId;
              ii.id = res.data[j].id;
              ii.digest = res.data[j].digest;
              ii.productId = res.data[j].productId;
              ii.productImg = res.data[j].productImg;
              ii.productName = res.data[j].productName;
              ii.bargainPrice = res.data[j].bargainPrice;
              ii.promotionPrice = res.data[j].promotionPrice;//
              ii.quantity = res.data[j].quantity;//数量
              ii.endPrice = (typeof res.data[j].bargainPrice == 'number') ? res.data[j].bargainPrice : ((typeof res.data[j].promotionPrice == 'number') ? res.data[j].promotionPrice : res.data[j].oldPrice);
              var money = ii.endPrice * ii.quantity;
              ii.amountPrice = money.toFixed(2);
              ii.isChecked = false;
              this.allPay.money += money;
              this.allPay.amount += ii.quantity;
              this.detailData.push(ii);
              this.check.push(ii)
            }
            // this.allPay.money = this.allPay.money.toFixed(2);
          } else {
            m$.template({
              val: res.message,
              time: 1000
            })
          }
        })
      }
    },
    created(){
      this.getDetailData()
    }
  }
</script>
