<template>
  <div>
  <div class="type-item-warp com-cursor-hander" v-for='item in allGoodsData' @click="goGoodsDetail(item)">
    <div class="img-warp" >
      <div class="this-car" @click.stop="checkIsLogin(item)" v-if="newChangeYangbinCode">
        <img src="../../assets/goods/car/car2.png">
      </div>
      <img :src="item.thumbnail" class="com-div-center-center-ab">
    </div>
    <section class="text-warp" >
      <P class="p1" v-text='(item.productName?item.productName:item.name)'>暴龙眼镜</P>
      <P class="p2" v-text='item.digest'>暴龙眼镜春游装备特购满299减30上不封顶，赶快去抢购吧...</P>
      <P class="p3" v-if="(typeof item.price!='number')"><span class="span1"><span>¥</span >{{(typeof item.bargainPrice=='number')&&item.bargainPrice?item.bargainPrice:((typeof parseFloat(item.promotionPrice)=='number')&&item.promotionPrice?item.promotionPrice:item.oldPrice)}}</span></P>
      <P class="p3" v-if="(typeof item.price=='number')"><span class="span1"><span>¥</span >{{item.price}}</span><s class="span2" v-text="'￥'+item.oldPrice">¥246</s></P>
      <span v-text="'库存量:'+(item.quantity?item.quantity:item.total)+'件'">100</span>
    </section>
  </div>
    <section class="head-login" v-if="openLoginTemplate">
      <div class="com-div-center-center-ab head-login-warp">
        <header class="">登&ensp;&ensp;录<img src="../../assets/decorate/maskClose.png" class="com-div-middle-ab com-cursor-hander" @click="nogoLogin"></header>

        <section>
          <div class="warp">
            <div style="margin-bottom: 0.19rem;">
              <img src="../../assets/login/account.png" class="com-div-middle-ab">
              <input class="full input" type="text" v-model="mobile" placeholder="请输入手机号">
            </div>
            <div>
              <img src="../../assets/login/password.png" class="com-div-middle-ab">
              <input class="full input" type="password"  v-model="password"
                     placeholder="请输入6-16位英文数字字符组合密码" @keyup.enter="goToSubmit(mobile,password)">
            </div>
            <div class="remember"style="margin-bottom: 0rem;">
              <label class="remember-label">
                <input type="checkbox" v-model="isReme" value="1">
                <span class="img"><img src="../../assets/login/check.png" class="com-div-center-center-ab"></span>
                <span class="span">记住用户名</span></label>
              <router-link tag="span" to="/forget" class="span2 com-div-middle-ab" style="right:0;">忘记密码？</router-link>
            </div>
            <div class="notice"style="margin-bottom: 0.14rem;" v-text="notice"></div>
            <div>
              <button class="full button" type="button" @click="goToLoginSubmit(mobile,password)">登录</button>
            </div>
            <section class="go-register">
              没有账号？
              <router-link tag="span" to="/register" class="span">立即注册</router-link>
            </section>
          </div>
        </section>
      </div>
    </section>
  </div>
</template>
<script type="text/javascript">
  export default {
    props:['allGoodsData'],
    data(){
        return{
          mobile:  m$.localStrages.get('accountPcData')?m$.localStrages.get('accountPcData'):'',
          password: "",
          isReme:1,
          notice:'',
          thisOitem:'',
          openLoginTemplate:false,
        }
    },
    created(){

    },
    watch:{
      'mobile':function (val) {
        this.notice='';
      },'password':function (val) {
        this.notice='';
      }
    },
    methods:{
      nogoLogin(){
        var othis=this;
        othis.openLoginTemplate=false;
        othis.mobile=m$.localStrages.get('accountPcData')?m$.localStrages.get('accountPcData'):'';
          othis.password= "";
          othis.isReme=1;
          othis.notice='';
        m$.hiddenScroll(false)
      }
      ,
      checkIsLogin(oitem){
        var othis=this;
        this.getAjax(this.url.isLogged,{},(res)=>{
          if(res.code==200&&res.data==true){
              this.goSpeedPay(oitem);
          }
          else {
            this.thisOitem=oitem;
            othis.openLoginTemplate=true;
            m$.hiddenScroll(true)
          }
        })
      },
      goToLoginSubmit(oname, opassword){
        if (oname == "" || opassword == "") {
          this.notice='请输入手机号及密码';
          return;
        }
        else {
          this.postAjax('/user/login',//登录请求
            {mobile: oname, password: opassword},
            (res) => {
              if(res.code==200){
                m$.localStrages.set('yxdyb',res.data);
                if(this.isReme==1){
                  m$.localStrages.set('accountPcData',oname);
                }
                else {
                  m$.localStrages.remove('accountPcData');
                }
                this.openLoginTemplate=false;
                this.mobile=m$.localStrages.get('accountPcData')?m$.localStrages.get('accountPcData'):'';
                this.password= "";
                this.isReme=1;
                this.notice='';

                m$.hiddenScroll(false);
                  this.goSpeedPay(this.thisOitem);

              }
              else {
                this.notice=res.message;
              }
            })
        }

      },
      goSpeedPay(oitem){
        var oid=''
        if(typeof oitem.price=='number'){
          oid=oitem.productId;
        }
        else {
          oid=oitem.id;
        }
        this.getAjax(this.srcUrl.getProductDetailShopVoUrl,{productId:oid},(res)=>{
          if(res.code==200){
            // this.aGoodsData = res.data;
            var ii={};
            ii.endPrice=(typeof res.data.bargainPrice=='number')?res.data.bargainPrice:((typeof res.data.promotionPrice=='number')?res.data.promotionPrice:res.data.oldPrice);
            ii.quantity = 1;
            ii.total = (typeof res.data.quantity=='number')?res.data.quantity:res.data.total;
            ii.id = res.data.id;
            ii.name = res.data.name;
            ii.groupType = res.data.groupType;
            ii.digest = res.data.digest;
            ii.businessId = res.data.businessId;
            ii.businessTypeId = res.data.businessTypeId;
            ii.saleCount = res.data.saleCount;
            ii.oldPrice = res.data.oldPrice;
            ii.intro = res.data.intro;
            ii.images = res.data.images;
            ii.thumbnail = res.data.thumbnail;
            ii.magnifyingImg = res.data.images[0];
            ii.businessName = res.data.businessName;
            if(!ii.groupType){
              this.postAjax(this.srcUrl.saveCartUrl,{
                productId:ii.id,
                quantity:ii.quantity,
                price:ii.endPrice
              },(res)=>{
                if(res.code==200){
                  m$.template({
                    val:'成功<br>加入购物车',
                    time:800,
                  });
                 this.$emit('newCarNum',new Date().getTime());
                }else {
                  m$.template({
                    val:res.message,
                    time:800,
                  })
                }
              })

            }
            else {
              if(ii.total<ii.quantity){
                m$.template({
                  val:'库存不足！',
                  time:800,
                });
                return;
              }

              m$.sessionStores.set('products',ii);
              if(m$.sessionStores.get('products')){
                window.location.href=('#/sureOrder')
              }
            }

          }
          else {
            m$.template({
              val:res.message,
              time:800
            })
          }
        })
      },
      goGoodsDetail(oitem){
        if(oitem.productId){
          window.location.href=(`#/goodsDetail/${oitem.productId}`)
        }
        else {
          window.location.href=(`#/goodsDetail/${oitem.id}`)
        }

      }
    }
  }
</script>
<style scoped lang="less">
  .head-login{
    position: fixed;
    z-index: 333;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /*min-width: 12rem;*/
    /*_width: 12rem;*/
    min-height: 3.3rem;
    _height:3.3rem;
    background: rgba(19, 19, 19, 0.44);
    .go-register {
      height: 0.34rem;
      text-align: right;
      font-family: PingFangSC-Regular;
      font-size: 0.14rem;
      color: #474747;
      letter-spacing: 0.0087rem;
      line-height: 0.34rem;;
      .span {
        cursor: pointer;
        font-size: 0.14rem;
        color: #fb4874;;
      }
    }
    .head-login-warp{
      width: 6rem;
      > header {
        background:#fb5a81;
        height: 0.5rem;
        font-family: PingFangSC-Regular;
        font-size: 0.22rem;
        line-height: 0.5rem;
        color: #ffffff;
        letter-spacing: 0px;
        position: relative;
        text-align: center;
        img{
          right: 0.3rem;
          height: 0.3rem;
        }
      }
      > section {
        background:#ffffff;
        padding: 0.18rem 0 0.15rem 0;
        .warp {
          width: 3.2rem;
          margin: 0 auto;
          .notice{
            height: 0.2rem;
            line-height: 0.2rem;
            font-family:PingFangSC-Regular;
            font-size:0.14rem;
            color:#ff3838;
            letter-spacing:0.008rem;
            text-align:left;
          }
          .remember {
            text-align: left;
            margin: 0 0 0.17rem 0;
            line-height: 0.36rem;
            position: relative;
            .span {
              cursor: pointer;
              margin-left: 0.06rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              color: #333;
              letter-spacing: 0.0087rem;
              text-align: left;
            }
            .span2{
              cursor: pointer;
              margin-left: 0.06rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              letter-spacing: 0.0087rem;
              text-align: left;
              color:#fb5a81;
            }
            .remember-label {
              display: inline-block;
              height: 0.16rem;
              line-height: 0.16rem;

              >*{
                vertical-align: top;
              }
              input{
                display: none;
              }
              input:checked ~ .img {

                img {
                  display: inline-block;

                }
              }
              .img {
                display: inline-block;
                width: 0.16rem;
                height: 0.16rem;
                position: relative;
                background-color: #fff;
                border:1px solid #979797;
                img {
                  display: none;
                  width: 0.12rem;
                }

              }
            }

          }
          div {
            > img {
              left: 0.06rem;
              height: 0.27rem;
            }
            position: relative;
            height: 0.38rem;
            .button {
              background: #333333;
              font-family: PingFangSC-Regular;
              font-size: 0.18rem;
              color: #ffffff;
              letter-spacing: 0px;
              line-height: 0.32rem;
              text-align: center;
            }
            .input {

              background-color: #fff;

              padding-left: 0.4rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              color: #949494;
              border:1px solid #979797;

              letter-spacing: 0.0087rem;

              text-align: left;
            }
            .full {
              width: 100%;
              height: 100%;
              border-radius: 0.02rem;
              padding: 0 0.4rem;
            }
          }
        }
      }

    }
  }
  .type-item-warp {
    box-shadow:0px 2px 10px 0px rgba(179,179,179,0.50);
    width: 2.85rem;
    height: 4.35rem;
    vertical-align: top;
    max-height: 4.35rem;
    display: inline-block;
    margin-right: .2rem;
    margin-bottom: .3rem;
    .img-warp {
      width: 100%;
      height: 2.85rem;
      position: relative;
      >.this-car{
        position: absolute;
        top: 0.2rem;
        left: 0.2rem;
        height: 0.3rem;
        width: 0.3rem;
        border-radius: 100%;
        text-align: center;
        z-index: 3;
        cursor: pointer;
        >img{
          height: 0.3rem;
        }
      }
      > img {
        z-index: 1;
        max-height: 2.85rem;
        max-width: 100%;
      }
    }
    .text-warp {
      position: relative;
      padding: 0 0.16rem;
      .p1 {
        font-family: PingFangSC-Regular;
        font-size: 0.2rem;
        color: #939393;
        letter-spacing: 0px;
        line-height: 100%;
        text-align: left;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin: 0.2rem 0 0.1rem 0;
      }
      .p2 {
        height: .48rem;
        font-family: PingFangSC-Regular;
        font-size: 0.18rem;
        color: #939393;
        letter-spacing: 0px;
        line-height: 0.24rem;
        text-align: left;
        text-overflow:ellipsis;
        -o-text-overflow:ellipsis;
        white-space:nowrap;
        overflow:hidden;
      }
      .p3 {
        padding: 0.1rem 0 0.15rem 0;
        vertical-align: bottom;
        text-align: left;
        .span1 {
          span {
            font-family: PingFangSC-Regular;
            font-size: 0.18rem;
            color: #fb4874;
            letter-spacing: 0.0103rem;
            line-height: 100%;
