          letter-spacing: 0px;
          line-height: 100%;
          text-align: left;
        }
      }
      >span {
        position: absolute;
        right: 0.16rem;
        bottom: 0.15rem;
        font-family: PingFangSC-Regular;
        font-size: 0.12rem;
        color: #b0aeae;
        letter-spacing: 0.69px;
        line-height: 100%;
        text-align: left;
      }
    }

  }
  .type-item-warp:nth-of-type(4n){
    margin-right: 0;
  }
</style>



