/*
*作者---杨彬
*
*/
<template>
  <div class="coupon-detail">
    <div class="detail-edit">
      <div class="head">
        优惠券详情
      </div>
      <section class="content">
        <div class="text-warp">
          <div class="item-warp">
            <img src="../../assets/coupon/statusIcon.png">
            <span class="title">状<span style="visibility: hidden;">杨</span>态</span>
            <span class="text" v-if="thisData.status==0">可使用</span>
            <span class="text" v-if="thisData.status==1">已使用</span>
            <span class="text" v-if="thisData.status==2">已过期</span>
          </div>
          <div class="item-warp">
            <img src="../../assets/coupon/nameIcon.png">
            <span class="title">名<span style="visibility: hidden;">杨</span>称</span>
            <span class="text" v-text="thisData.typeName"></span>
          </div>
          <div class="item-warp">
            <img src="../../assets/coupon/timeIcon.png">
            <span class="title">有效期</span>
            <span class="text" v-text="thisData.gmtStart+'-'+thisData.gmtEnd"></span>
          </div>
          <div class="item-warp">
            <img src="../../assets/coupon/explain.png">
            <span class="title">说<span style="visibility: hidden;">杨</span>明</span>
            <span class="text" v-text="thisData.useIntro"></span>
          </div>
        </div>
        <section>
          <p>商家扫码即可使用</p>
          <span id="qcodeSpan"></span>
          <p class="p2">二维码使用步骤: 关注微信公众号（宝宝余积分商城）>  登录宝宝余账号 >  我的优惠券  >  点击查看详情即可线下使用</p>
        </section>
      </section>
    </div>


  </div>
</template>
<script type="text/javascript">
  export default {
    data(){
      return{
        thisData:''
      }
    },
    methods:{
      getDataFn(){
        var othis=this;
        this.getAjax(this.url.couponDetail,{id:this.$route.params.id},(res)=>{
          this.thisData=res.data;
          m$.template({
            time:200,
            val:'',
            fn(){
              // new QRCode(m$.dom('#qcodeSpan')[0],othis.thisData.couponNo)
              new QRCode('qcodeSpan', {
                text: othis.thisData.couponNo,
                width: 200,
                height: 200,
                colorDark : '#000000',
                colorLight : '#ffffff',
                correctLevel : QRCode.CorrectLevel.H
              });
            }
          })


        })
      }
    },
    created(){
      document.title="优惠券详情";
      this.getDataFn();
      m$.template({
        time:'',
        val:'',
      })
    }
  }
</script>
<style scoped lang="less">
  .coupon-detail{
    min-height: 6.68rem;
    _height:6.68rem;
    border: 1px solid #dbdbdb;
    border-top: none;
    .head{
      background:#f8f8f8;
      border:1px solid #dbdbdb;
      height:0.5rem;
      padding-left: 0.3rem;
      line-height: 0.5rem;
      font-family:PingFangSC-Regular;
      font-size:0.22rem;
      color:#333333;
      letter-spacing:0px;
      text-align:left;
    }
    .content {
      width: 9.1rem;
      margin: 0 auto;
      .text-warp {
        border-bottom: 1px solid #dbdbdb;
        padding: 0.2rem 0 0.3rem 0;
        .item-warp {
          padding-left: 3.1rem;
          line-height: 0.38rem;
          text-align: left;
          height: 0.38rem;
          > * {
            vertical-align: middle;
          }
          > img {
            height: 0.2rem;
          }
          .title {
            width: 0.88rem;
            display: inline-block;
            margin-left: 0.1rem;
            font-family: PingFangSC-Regular;
            > * {
              font-size: 0.2rem;
              line-height: 0;
            }
            font-size: 0.2rem;
            color: #999999;
            text-align: left;
          }
          .text {
            font-family: PingFangSC-Regular;
            font-size: 0.18rem;
            color: #212121;
            letter-spacing: 0px;
          }
        }
      }
      > section {
        background-color: white;
        text-align: center;
        font-size: 0;
        padding-top: 0.36rem;
        p {
          font-family: PingFangSC-Regular;
          font-size: 0.2rem;
          color: #999999;
          letter-spacing: 0px;
          line-height: 100%;
        }
        .p2{
          font-size: 0.16rem;
        }
        span {
          margin: 0.2rem auto;
          display: inline-block;
          width: 2rem;
          height: 2rem;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }

</style>



