<template>
  <div id="leave-word">
    <div class="buy-rule">
      <div class="word" v-for='item in deData'>
        <div class="top">
          <p class="tel" v-text='item.userMobile'></p>
          <p class="time" v-text='item.gmtCreate'></p>
        </div>
        <div class="info" >
          <p v-text='item.text'></p>
        </div>
      </div>
    </div>
    <div class="loading" >
      <img src="./../assets/goods/detail/loading1.png" v-if="btn1">
      <span v-if="btn1">正在加载更多</span>
    </div>
    <div class="btn" @click="showPop(1)">我要留言</div>
    <div class="pop-up" v-if='isLeaveBtn' @click="cancel">
      <div class="no-login com-div-center-center-ab" v-if='isLoading' @click.stop="stopPop">
        <p>登陆后可以留言</p>
        <div><button  @click.stop="goLogin">确定</button></div>
      </div>
      <div class="com-div-center-center-ab inner-box" v-if='isLoading1' @click.stop="stopPop">
        <div class="pop-area">
          <textarea placeholder="输入留言内容" v-model='leaveWordModel' maxlength="100" ></textarea>
          <p v-text="leaveWordModel.length+'/100'"></p>
        </div>
        <div class="pop-btn">
          <button style="margin-right:.5rem" @click.stop="showPop(0)">取消</button>
          <button @click.stop="sureLeaveWord">确认</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default{
    props: ['sData'],
    data(){
      return {
        deData: []
        , isLeaveBtn: false,
        leaveWordModel: '',
        pageNum: 0,
        pageSize: 10,
        btn1: false,//分页loding显示隐藏按钮
        btn2: false,//添加留言，
        isLoading:false,
        isLoading1:false
      }
    },
    methods: {
      stopPop(){

      },
      showPop(boo){
        if (boo) {
          this.isLeaveBtn = true;
          this.getAjax(this.url.isLogged,{},(res)=>{
            if(res.code==200){
              if(!res.data){
                this.isLoading=true;
                this.isLoading1=false;
              }else {
                this.isLoading=false;
                this.isLoading1=true;
              }
            }
          });

        } else {
          this.leaveWordModel = '';
          this.isLeaveBtn = false;
        }
      },
      goLogin(){
        this.getAjax(this.url.my,{},(res)=>{

        })
      },
      unique5(array){
        var r = [];
        for (var i = 0, l = array.length; i < l; i++) {
          for (var j = i + 1; j < l; j++)
            if (array[i].id === array[j].id) j = ++i;
          r.push(array[i]);
        }
        return r;
      },
      getData(){
        if(this.btn2){
          this.deData=[]
        }
        this.btn2=false;
        var obj = {};
        obj.productId = this.$route.params.id;
        obj.pageSize = this.pageSize;
        var lastData = this.deData.length&&this.deData[this.deData.length-1];
        if (this.deData.length>0) {
          obj.lastGmtCreate = lastData.gmtCreate;
          obj.lastId = lastData.id;
        }
        m$.scroll(function () {

        });
        this.postAjax(this.srcUrl.productReviewsPage, JSON.stringify(obj), (res) => {
          if (res.code == 200) {
            var othis = this;
            for (var i=0;i<res.data.length;i++){
              this.deData.push(res.data[i]);
            }
            if(this.deData.length>0&&this.deData.length%10==0){
              this.btn1 = true;
              setTimeout(function () {
                othis.btn1=false
              },1000);
              m$.scroll(function () {
                othis.getData();
              })
            }else {
              this.btn1 = false;
              m$.scroll(function () {

              })
            }
          }else {

          }
        })
      },
      cancel(){
        this.leaveWordModel = '';
        this.isLeaveBtn = false;
      },
      sureLeaveWord(){
        var obj = {};
        obj.text = this.leaveWordModel;
        obj.product = {
          'id': this.$route.params.id
        };
        this.postAjax(this.srcUrl.productReviewsSave, JSON.stringify(obj), (res) => {
          if (res.code == 200) {
            var _this = this;
            m$.template({
              val: '成功',
              time: 600,
              fn(){
                  _this.leaveWordModel = '';
                _this.btn2=true;
                _this.isLeaveBtn = false;
                _this.getData()
              }
            })

          } else {

          }
        })
      }
    },
    created(){
      this.getData()
    }
  }
</script>
<style lang="less" scoped>
  //@import "${fileName}";
  #leave-word {
    width: 12rem;
    margin: 0 auto;
    .buy-rule {
      padding: 0 .2rem;
      > .word {
        .top {
          width: 100%;
          border-bottom: 1px solid #e3e3e3;
          overflow: hidden;
          padding: .14rem 0;
          > p {
            float: left;
            font-size: .16rem;
            color: #9c9c9c;
          }
          > p:nth-of-type(2) {
            float: right;
          }
        }
        .info {
          p {
            font-size: .2rem;
            color: #6e6e6e;
          }
          padding: .14rem 0;
          border-bottom: 1px solid #e3e3e3;
        }
      }

    }
    .loading {
      height: .56rem;
      min-height: .56rem;
      _height: .56rem;
      line-height: .56rem;
      text-align: center;
      > img {
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: rotate(360deg);
        animation: rotation 3s linear infinite;
        -moz-animation: rotation 3s linear infinite;
        -webkit-animation: rotation 3s linear infinite;
        -o-animation: rotation 3s linear infinite;
      }
      > span {
        display: inline-block;
        vertical-align: middle;
        margin-left: .2rem;
        color: #9a9a9a;
        font-size: .18rem;
      }
    }
    .btn {
      cursor: pointer;
      margin: 0 auto;
      width: 2.8rem;
      height: .48rem;
      line-height: .48rem;
      text-align: center;
      font-size: .2rem;
      background-color: #fb4874;
      color: #fff;
    }
    .pop-up {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 100;
      background-color: rgba(23, 23, 23, .48);
      .inner-box {
        border-radius: 4px;
        width: 6.6rem;
        height: 4.8rem;
        background-color: #fff;
        padding: .3rem;

        .pop-area {
          position: relative;
          width: 6rem;
          height: 3.25rem;
          > textarea {
            width: 6rem;
            height: 3.25rem;
            color: #9a9a9a;
            padding: .16rem;
            font-size: .24rem;
            resize: none;
            border-radius: 1px;
            border: 1px solid #979797;
          }
          > p {
            position: absolute;
            bottom: 0.08rem;
            right: 0.08rem;
            font-size: .26rem;
            color: #9c9c9c;
          }
        }

        > .pop-btn {
          width: 100%;
          height: .54rem;
          font-size: 0;
          text-align: center;
          margin-top: .26rem;
          > button {
            cursor: pointer;
            display: inline-block;
            height: .54rem;
            width: 1.4rem;
            font-size: .2rem;
            border-radius: .1rem;
            background-color: #333;
            border: 1px solid #333;
            color: #fff;
          }
          > button:nth-of-type(1) {
            background-color: #fff;
            color: #333;
          }
        }
      }
      .no-login{
        width: 4.84rem;
        height: 2.80rem;
        border-radius: 4px;
        background-color: #fff;
        padding-top: .7rem;
        >p{
          text-align: center;
          font-size:.24rem;
          color:#333333;
          line-height:24px;
        }
        >div{
          width: 100%;
          margin-top: .7rem;
          text-align: center;
          >button{
            cursor: pointer;
            border-radius: 2px;
            margin: 0 auto;
            font-size: .2rem;
            background:#333333;
            width:1.80rem;
            color: #fff;
            height:.50rem;
          }
        }
      }
    }
  }

  @-webkit-keyframes rotation {
    from {
      -webkit-transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
    }
  }
</style>



