/*
*作者---杨彬
*
*/
<template>
    <div class="couponCenter">
      <com-head></com-head>
      <nav-search class="index-search"></nav-search>
    <div class="banner">
      <img src="../../assets/coupon/centerBanner.png">
    </div>
    <section class="container" v-if="allCouponData.length">
      <coupon-item :oitem="oitem" :oindex="oindex" :oid="oitem.detailId" @couponCkFn='conponItemClick'
                    v-for="(oitem,oindex) in allCouponData"
                    :key="oindex" v-if="allCouponData" class="cunpon-list"></coupon-item>
      <div class="com-loading" v-if="isMoreData.more"><img src="../../assets/loading.png"></div>
    </section>
      <section v-if="!allCouponData.length" class="container">
        <img src="../../assets/noOrder.png" class="com-div-center-center-ab">
      </section>
      <com-foot style="margin-top:0.6rem;"></com-foot>
    </div>
</template>
<script type="text/javascript">
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import navSearch from 'src/components/index/navSearch.vue'
  import couponItem from "src/components/com/couponItem.vue"
  export default {
    components:{couponItem,comFoot,navSearch,comHead},
    data(){
      return {
        noData:true,
        kind: 2,
        status: 3,
        allCouponData: [],
        spage:1,
        slength:10,
        isMoreData:{
          init:true,
          more:true,

        },
      }
    },
    methods: {
      conponItemClick(obj){
        this.allCouponData[obj[0]].status = 0;
        this.allCouponData[obj[0]].detailId = obj[1];
      },
      getData(opage, olength){
        let othis=this;
        if(othis.isMoreData.init){
          m$.scroll(function () {
            othis.isMoreData.more=true;
          })
        }
        this.getAjax(this.url.couponCenter,{pageNum:this.spage,pageSize:this.slength},(res)=> {
          othis.isMoreData.more=false;
          if(res.code==200){
            var j=res.data;
            for (var i=0;i<res.data.length;i++){
              this.allCouponData.push(res.data[i]);
              // m$.scroll(function () {
              //   if(othis.allCouponData.length%10<=0){
              //     othis.spage++;
              //     othis.getData()
              //   }
              //   else {}
              // })
            }
            if(j&&j.length>0&&j.length%10==0){
              othis.isMoreData.init=true;
              m$.scroll(function () {
                othis.spage++;
                othis.getData()
              })
            }
            else{
              m$.scroll(function () {
                othis.isMoreData.more=false;
              })
            }
          }else {
            m$.template({
              val:'暂无数据',
              time:1000,
              // fn(){
              //     window.location.href=('#/index')
              // }
            })
          }
        })

      }
    },
    created(){
      this.getData();
      document.title = '领券中心';

    }
  }
</script>
<style scoped lang="less">
.couponCenter{

  >.banner{
    width: 100%;
    >img{
      width: 100%;
    }
  }
  >.container{
    position: relative;
    width: 12rem;
    margin: 0 auto;
    text-align: left;
    min-height: 3rem;
    _height:3rem;
    >.cunpon-list{
      &:nth-of-type(2n+1){
        margin-left: 0;
      }
      margin-top: 0.2rem;
      display: inline-block;
      margin-left: 0.39rem;
    }
  }
}
</style>



