<template>
  <div class="decorate-address">
    <div class="address-edit">
      <div class="head">
        装修贷款申请
      </div>
      <section class="edit-content">
        <div class="phone-code-warp">
          <div class="row">
            <div class="one">*</div>
            <div class="two">姓&ensp;&ensp;&ensp;&ensp;名：</div>
            <div class="three">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!name">请输入收货人的姓名</span>-->
                <input type="text" class="input-tel" v-model="willSendData.realName" placeholder="请输入您的真实姓名">
              </label>
            </div>
          </div>
          <!---->
          <div class="row paddingRow">
            <div class="one">*</div>
            <div class="two">联系电话：</div>
            <div class="three">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!phone">请输入联系电话</span>-->
                <input type="tel" class="input-tel" v-model="willSendData.mobile" placeholder="请输入联系电话">
              </label>
            </div>
          </div>
          <!---->
          <div class="row">
            <div class="one">*</div>
            <div class="two">所在地址：</div>
            <div class="three">
              <label class="input-label" @click="openALertFn()">
                <img class="arrowDown com-div-middle-ab" src='../../assets/my/arrowDown.png'>
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!area">请选择您所在的区域</span>-->
                <input type="text" class="input-tel com-cursor-hander" :value="thisPCD" placeholder="请选择您所在的区域" disabled>
              </label>
            </div>
          </div>
          <!---->
          <div class="row paddingRow">
            <div class="one">*</div>
            <div class="two">详细地址：</div>
            <div class="three textarea">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab address" v-if="!address">请输入您的详细地址</span>-->
                <textarea class="input-tel" v-model="willSendData.dcAddress" placeholder="请输入您的详细地址"></textarea>
              </label>
            </div>
          </div>
        </div>
        <div class="submit-warp">
          <button class="submit-button com-cursor-hander" type="button" @click="submitFn">确定提交</button>
        </div>
      </section>
      <div class="area-mark" v-if="openAreaMark">
        <area-linkage @upAreaMsg='upAreaMsgFn'
                      :upAreaData='willSendData'>

        </area-linkage>
      </div>
    </div>


  </div>
</template>
<script type="text/javascript">
  import areaLinkage from 'src/components/com/areaLinkage/areaLinkage.vue';
  export default{
    components: {areaLinkage},
    data(){
      return{
        openAreaMark: false,
        thisPCD: '',
        willSendData: {
          "dcArea": "",
          "provinceName": "",
          "cityName": "",
          "districtName": "",
          "provinceCode": '',
          "cityCode": '',
          "districtCode": '',
          "dcAddress": "",
          "mobile": "",
          "realName": "",
          "coupon.id": "",
        }
      }
    },
    methods: {
      openALertFn(){
        this.openAreaMark = true;
      },
      upAreaMsgFn(oObj){
        console.log(oObj)
        this.openAreaMark = false;
        if(oObj==false){
          return;
        }
        if (!oObj.province.id) {
          return;
        };
        this.willSendData.provinceName = oObj.province.name;
        this.willSendData.provinceCode = oObj.province.id;
        this.willSendData.cityName = oObj.city.name;
        this.willSendData.cityCode = oObj.city.id;
        this.willSendData.districtName = oObj.district.name;
        this.willSendData.districtCode = oObj.district.id;
        this.buildPCDFn();
      },
      buildPCDFn(){
        let ostring = "";
        if (this.willSendData.provinceName) {
          ostring += '/' + this.willSendData.provinceName
        }
        if (this.willSendData.cityName) {
          ostring += '/' + this.willSendData.cityName
        }
        if (this.willSendData.districtName) {
          ostring += '/' + this.willSendData.districtName
        }
        ostring = ostring.substring(1, ostring.length)
        this.thisPCD = ostring;
      },
      submitFn(){
        let othis=this;
        var addressExg=/^([A-Za-z0-9]|[\u4E00-\u9FA5])+$/;
        let sdata = {
          'coupon.id': this.$route.params.id,
          realName: this.willSendData.realName,
          mobile: this.willSendData.mobile,
          dcDistrict: this.willSendData.districtCode,
          dcArea: ''+this.willSendData.provinceName+this.willSendData.cityName+this.willSendData.districtName,
          dcAddress: this.willSendData.dcAddress
        }
        if (!sdata.mobile || !sdata.realName) {
          m$.template({
            val: '手机号和姓名<br>不能为空',
            time: 1200
          })
          return;
        }
        ;
        if (!sdata.dcArea || !sdata.dcAddress) {
          m$.template({
            val: '地址信息<br>不能为空',
            time: 1200
          })
          return;
        }
        if(addressExg.test(this.willSendData.dcAddress)==false){
          m$.template({
            val:'地址详情不能<br>含有特殊字符',
            time:500
          })
          return;
        }
        this.postAjax(this.url.decorateApple, sdata, (res)=> {
          console.log(res)
          if (res.code == 200) {
            m$.template({
              val: okImgDiv,
              time: 1200,
              fn(){
                history.go(-1)
              }
            })
          }
          else {
            m$.template({
              val: res.message,
              time: 1200,
            })
          }
        })
      }
    },
    created(){
      var othis=this;

      othis.getAjax(othis.url.my,{},(res)=>{
        if(res.code==200){
          if(res.data.mobile){
            othis.canInput=true;
            othis.willSendData.mobile=res.data.mobile;
          }

        }

      })
      document.title = '装修贷款申请';
      m$.documentTitle("装修贷款申请")
    }
  }
  </script>
<style scoped lang="less">
  .area-mark{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.71);
    z-index: 9999;
  }
  .decorate-address{
    width: 100%;
    position: relative;
    padding-top:0.33rem;
  }
  .address-edit{
    position: relative;
    max-width: 12rem;
    margin: 0 auto;

  }
  .deleteRed{
    color: #fb4874;
  }
  .editGrenn{
    color: #0baf7f;
    margin-left: 0.1rem;

  }
  .arrowDown{
    right: 0.14rem;
    height: 0.08rem;
  }

  .paddingRow{
    padding: 0.28rem 0;
  }
  .submit-warp{
    margin-top: 0.02rem;
    text-align: center;
    .submit-button{
      background:#333333;
      border-radius:0.04rem;
      width:2.02rem;
      height:0.56rem;
      font-family:PingFangSC-Regular;
      font-size:0.22rem;
      color:#ffffff;
      letter-spacing:0px;
    }

  }

    /*border:1px solid #dbdbdb;*/

    .head{
      background:#f8f8f8;
      border:1px solid #dbdbdb;
      height:0.5rem;
      padding-left: 0.3rem;
      line-height: 0.5rem;
      font-family:PingFangSC-Regular;
      font-size:0.22rem;
      color:#333333;
      letter-spacing:0px;
      text-align:left;
    }
    .edit-content{
      border:1px solid #dbdbdb;
      border-top: none;
      height: 5.28rem;
      padding-top: 0.52rem;
      .phone-code-warp {
        .row {

          > div {
            display: inline-block;
            vertical-align: top;
            height: 0.54rem;
            line-height: 0.54rem;
          }
          .one {
            width: 2.99rem;
            /* * copy 2:*/;
            font-family: PingFangSC-Regular;
            font-size: 0.2rem;
            color: #fb4874;
            letter-spacing: 0px;
            text-align: right;
            padding-right: 0.06rem;
          }
          .two {
            width: 1.2rem;
            /* * copy 2:*/;
            font-family: PingFangSC-Regular;
            font-size: 0.2rem;
            padding-right:0.2rem ;
            color: #333;
            letter-spacing: 0px;
            text-align: right;
          }
          .three {
            &.textarea{
              width: 4.96rem;
              height:1.1rem ;
              .input-label {
                width: 100%;
                .input-tel{
                  padding-top: 0.13rem;
                  width: 100%;
                  resize: none;
                }
              }
            }
            .input-label{
              height: 100%;
              position: relative;
              display: inline-block;
              background-color: #f0f0f0;
              .input-placeHolder{
                left: 0;
                font-size:0.2rem;
                padding: 0 0.15rem ;
                letter-spacing:0px;
                text-align:left;
                color: #999999;
              }
              .address{
                top: 0.28rem;
              }
              .input-tel {
                background-color: transparent;
                border: 1px solid #c4c4c4;
                border-radius: 0.02rem;
                width: 3.46rem;
                height: 100%;
                font-family:PingFangSC-Regular;
                font-size:0.2rem;
                padding: 0 0.15rem ;
                letter-spacing:0px;
                text-align:left;
                color: #999999;
              }
            }

          }
        }
      }
    }
</style>



