import Vue from 'vue'
import Router from 'vue-router'
import components from 'src/router/components.js'
Vue.use(Router)
var myRouter = new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      component: components.index
    },
    {
      path: '/login',
      name: 'login',
      component: components.login
    },{
      path: '/about',
      name: 'about',
      component: components.about
    },{
      path: '/question',
      name: 'question',
      component: components.question
    }, {
      path: '/forget',
      name: 'forget',
      component: components.forget
    },
    {
      path: '/register',
      name: 'register',
      component: components.register
    },
    {
      path: '/couponCenter',
      name: 'couponCenter',
      component: components.couponCenter
    },
    {
      path: '/decorate',
      name: 'decorate',
      redirect: '/decorate/index',
      component: components.emptyIndexVue,
      children: [
        {
          path: 'center',
          name: 'decorateCenter',
          component: components.decorateCenter,
        }, {
          path: 'message/:id',
          name: 'decorateMassage',
          component: components.decorateMassage,
        }, {
          path: 'index',
          name: 'decorateIndex',
          component: components.decorateIndex,
        }
      ]

    },
    {
      path: '/my',
      name: 'myIndex',
      redirect: '/my/myOrder',
      component: components.myIndex,
      children: [
        {
          name:'myCharge',
          path:'charge',
          component: components.charge,

        },
        {
          name: 'myPhoneParent', path: '/my/phone',
          component: components.emptyVue,
          redirect: '/my/phone/action',
          children: [
            {
              path: 'action',
              name: 'myPhone',
              component: components.myPhone,
            }, {
              path: 'success',
              name: 'myPhoneSuccess',
              component: components.changeSuccess,
            }
          ]
        },
        {
          name: 'myPasswordParent', path: '/my/password',
          component: components.emptyVue,
          redirect: '/my/password/action',
          children: [
            {
              path: 'action',
              name: 'myPassword',
              component: components.myPassword,
            }, {
              path: 'success',
              name: 'myPasswordSuccess',
              component: components.changeSuccess,
            }
          ]
        },
        {
          name: 'myAddressParent', path: '/my/address',
          component: components.emptyVue,
          redirect: '/my/address/action',
          children: [
            {
              path: 'action',
              name: 'myAddress',
              component: components.myAddress,
            }, {
              path: 'success',
              name: 'myAddressSuccess',
              component: components.changeSuccess,
            },
          ]
        },
        {
          path: 'myOrder',
          name: 'myOrderParent',
          component: components.emptyVue,
          redirect: '/my/myOrder/type/-1',
          children: [
            {
              path: 'type/:id',
              name: 'myOrder',
              component: components.myOrder,
            },
            {'name':'orderDetail',path:'orderDetail/:id',component:components.orderDetail},

          ]
        },
        {
          path: 'myCoupon',
          name: 'myCouponParent',
          component: components.emptyVue,
          redirect: '/my/myCoupon/type/0',
          children: [
            {
              path: 'couponDetail/:id',
              name: 'couponDetail',
              component: components.couponDetail,
            },
            {
              path: 'type/:id',
              name: 'myCoupon',
              component: components.myCoupon,
            }
          ]
        },
        {
          path: 'decorateOrder',
          name: 'decorateOrderParent',
          component: components.emptyVue,
          redirect: '/my/decorateOrder/type/0',
          children: [
            {
              path: 'type/:id',
              name: 'decorateOrder',
              component: components.decorateOrder,
            }
          ]
        },
      ]
    },
    {
      path: '/goodsItem',
      name: 'goodsItem',
      redirect: '/goodsItem/type/0',
      children: [
        {
          path: 'type/:id',
          name: 'productType',
          component: components.productType
        }
      ],
      component: components.emptyVue
    },

    {
      name: 'goodsDetail', path: '/goodsDetail/:id', component: components.goodsDetail
    }, {
      name: 'productCar', path: '/productCar', component: components.productCar
    }, {
      name: 'sureOrder', path: '/sureOrder', component: components.sureOrder
    }, {
      name: 'skillGoods', path: '/skillGoods', component: components.skillGoods
    },{
      name: 'goPay', path: '/goPay/:type', component: components.buySuccess
    },{
      name: 'goPayByOrder', path: '/goPayByOrder', component: components.buySuccessByOrder
    },
    {
      name: 'todayGoodsParent', path: '/todayGoods', component: components.emptyVue,
      redirect:'/todayGoods/type/0',
      children:[
        { name:'todayGoods',path:'type/:id',component:components.todayGoods}
      ]
    },
    {
      path: '*',
      redirect: '/index'
    }

  ]
})
export default myRouter



