<template>
  <div class="login">
    <div class="header-warp">
      <img src="../../assets/login/logo.png">
    </div>
    <section class="content-warp">
      <div class="com-div-center-center-ab content" v-if="status!=3">
        <div>
          <div class="header" :class="{'willRed':status==1}">验证手机号</div>
          <div class="header" :class="{'willRed':status==2}">输入新密码</div>
        </div>
        <section class="section1" v-if="status==1">
          <div class="warp">
            <div style="margin-bottom: 0.19rem;">
              <img src="../../assets/login/account.png" class="com-div-middle-ab">
              <input class="full input" type="text" placeholder="请输入手机号" v-model="phone">
            </div>
            <div>
              <img src="../../assets/login/code.png" class="com-div-middle-ab">
              <input class="code input" type="tel" placeholder="短信验证码" v-model="codeCheck" :disabled="!phone">
              <button class="code-button com-cursor-hander" @click="getAuthCode()" type="button"v-if="time==0">获取验证码</button>
              <button class="code-button" v-text="time+'s'"  v-if="time!=0" type="button"></button>
            </div>
            <!--<div>-->
              <!--<img src="../../assets/login/password.png" class="com-div-middle-ab">-->
              <!--<input class="full input" type="password" placeholder="请输入6-16位英文数字字符组合密码" v-model="password">-->
            <!--</div>-->
            <section class="notice" v-text="notice">
            </section>
            <div style="">
              <button class="full button com-cursor-hander" type="button" @click="nextStatus()">下一步</button>
            </div>
            <section class="go-register">
              已有账号？
              <router-link tag="span" to="/login" class="span">立即登录</router-link>
            </section>
          </div>
        </section>
        <section class="section1" v-if="status==2">
          <div class="warp">
            <!--<div style="margin-bottom: 0.19rem;">-->
              <!--<img src="../../assets/login/account.png" class="com-div-middle-ab">-->
              <!--<input class="full input" type="text" placeholder="请输入手机号" v-model="phone">-->
            <!--</div>-->
            <!--<div>-->
              <!--<img src="../../assets/login/code.png" class="com-div-middle-ab">-->
              <!--<input class="code input" type="tel" placeholder="短信验证码" v-model="codeCheck">-->
              <!--<button class="code-button" @click="getAuthCode()" type="button"v-if="time==0">获取验证码</button>-->
              <!--<button class="code-button" v-text="time+'s'"  v-if="time!=0" type="button"></button>-->
            <!--请输入6-16位英文数字字符组合密码-->
            <!--</div>-->
            <div style="margin-bottom: 0.19rem;">
            <img src="../../assets/login/password.png" class="com-div-middle-ab">
            <input class="full input" type="password" placeholder="新密码" v-model="newPassword">
            </div>
            <div>
            <img src="../../assets/login/password.png" class="com-div-middle-ab">
            <input class="full input" type="password" placeholder="确认新密码" v-model="surePassword">
            </div>
            <section class="notice" v-text="notice">
            </section>
            <div style="">
              <button class="full button com-cursor-hander" type="button" @click="nextStatus()">提交</button>
            </div>
            <section class="go-register">
            已有账号？
            <router-link tag="span" to="/login" class="span">立即登录</router-link>
            </section>
          </div>
        </section>
      </div>
      <div class="com-div-center-center-ab success" v-if="status==3">
        <div><img src="../../assets/login/success.png" class="img"></div>
        <div class="text">登录密码重置成功！</div>
        <div class="go-login"><router-link to="/login" tag="button" class="button">去登录</router-link></div>
      </div>
    </section>
    <div class="foot-logo">
      © Copyright All rights reserved. | 版权所有: 成都友木科技有限公司
    </div>
  </div>
</template>
<script type="text/javascript">
  export default {
    data(){
      return{
        status:1,
        password: '',
        codeCheck: '',
        newPassword:'',
        surePassword:'',
        notice:'',
        phone: '',
        time:0,
        selIt:'',
      }
    },
    methods:{
      nextStatus(){
        var othis=this;
        let willSendData = {};
         if (this.status == '1') {
          willSendData['mobile'] = this.phone;
          willSendData['authCode'] = this.codeCheck;
          this.getAjax(this.url.checkCode,willSendData,(res)=>{
            if (res.data == true) {
              this.status=2;
            }
            else {
              this.notice='验证码错误';
            }
          })
        }
        else if (this.status == '2') {

          var pwdReg=/^[A-Za-z0-9]{6,20}$/;
          if(!pwdReg.test(this.newPassword)){

             this.notice='密码由:6-20位字母数字组合';

            return;
          };
          if(this.newPassword!=this.surePassword||(!this.newPassword)){

            this.notice='两次密码不一致';
            return;
          }

          willSendData['mobile'] = this.phone;
          // willSendData['authCode'] = this.codeCheck;
          willSendData['password'] = this.newPassword;
          this.postAjax(this.url.resetPassword2, willSendData,
            (res)=> {
              if (res.code == 200) {
               this.status=3;


              }
              else {
                m$.template({
                  time:800,
                  val:res.message
                })
              }
            })
        }
        else {
          this.$router.push('/login')
        }


      },
      getAuthCode(){
        var thisReg=/^1[3|4|5|7|8][0-9]{9}$/;
        let  autoCodeUrl='';//验证码初始地址
        var othis=this;
        if(!this.phone||thisReg.test(this.phone)==false){
          this.notice="请输入正确的手机号";
         return;
        }
        else {
          autoCodeUrl = this.url.passwordAuthCode;
          this.postAjax(
            autoCodeUrl, {mobile: this.phone},
            (res)=> {
              if (res.code == 200) {
                m$.template({
                  time: 800,
                  val: '验证码<br>已发送',
                  fn(){
                    othis.time = 59;

                    othis.selIt = window.setInterval(function () {
                      othis.time--;
                      if (othis.time == 0) {
                        window.clearInterval(othis.selIt);
                        othis.selIt='';
                      }
                    }, 1000)
                  }
                })
              }
              else {
                m$.template({
                  time: 800,
                  val: res.message
                })
              }
            })
        }
      }
    },
    watch:{
      phone:function (val) {
    var thisReg=/^1[3|4|5|7|8][0-9]{9}$/;
        if(thisReg.test(val)==false){
          this.notice="输入的手机号格式错误！"
        }
        else {
          this.notice=""
        }
  },
      newPassword:function (val) {
    var pwdReg=/^[A-Za-z0-9]{6,20}$/;
        if(pwdReg.test(val)==false){
          this.notice="密码由:6-20位字母数字组合！"
        }
        else {
          this.notice=""
        }
  },
    },
    created(){
      m$.documentTitle("忘记密码");
    },
    beforeDestroy(){
      // m$.hiddenScroll(false);
      let othis=this;
      if(othis.selIt){
        window.clearInterval(othis.selIt);
      }
    }
  }
</script>
<style scoped lang="less">
  .go-register {
    height: 0.34rem;
    text-align: right;
    font-family: PingFangSC-Regular;
    font-size: 0.14rem;
    color: #e9e9e9;
    letter-spacing: 0.0087rem;
    line-height: 0.34rem;;
    .span {
      cursor: pointer;
      font-size: 0.14rem;
      color: #fb4874;;
    }
  }

  .header-warp {
    width: 12rem;
    text-align: left;
    margin: 0.78rem auto 0.2rem auto;
    img {
      height: 0.5rem;
    }
  }

  .content-warp {
    min-width: 12rem;
    height: 6.8rem;
    position: relative;
    background: url("../../assets/login/banner.jpg") no-repeat center;
    background-size: auto 100%;
    .success{
      background: rgba(0, 0, 0, 0.4);
      width:4.4rem;
      height:2.78rem;
      text-align: center;
      padding:  0.3rem 0;
      .button{
        background:#ffffff;
        border-radius:1rem;
        width:1.2rem;
        height:0.32rem;
        font-family:PingFangSC-Regular;
        font-size:0.18rem;
        color:#fb4874;
        letter-spacing:0px;
        text-align:center;
      }
      .text{
        font-family:PingFangSC-Regular;
        font-size:0.24rem;
        color:#fb4874;
        letter-spacing:0px;
        text-align:center;
        padding: 0.2rem 0 0.5rem 0;

      }
      .img{
     height: 0.7rem;
      }

    }
    .content {
      width: 6rem;
      >div{
        height: 0.38rem;
        .header {
          width: 50%;
          display: inline-block;
          float: left;
          vertical-align: top;
          background: rgba(0, 0, 0, 0.69);
          height: 100%;
          font-family: PingFangSC-Regular;
          font-size: 0.16rem;
          line-height: 0.38rem;
          color: #ffffff;
          letter-spacing: 0px;

          text-align: center;
        }
        .willRed{
          background-color: rgba(251, 72, 116, 0.69);;
        }
      }

      > .section1 {
        min-height: 3.3rem;
        _height:3.3rem;
        background: rgba(0, 0, 0, 0.5);
        padding: 0.6rem 0 0.15rem 0;
        .warp {
          width: 3.2rem;
          margin: 0 auto;
          .notice{
            height: 0.34rem;
            line-height: 0.34rem;
            font-family:PingFangSC-Regular;
            font-size:0.14rem;
            color:#ff3838;
            letter-spacing:0.008rem;
            text-align:left;
            margin-bottom: 0.06rem;
          }
          div {
            > img {
              left: 0.06rem;
              height: 0.27rem;
            }
            position: relative;
            height: 0.38rem;
            .button {
              background: rgba(251, 72, 116, 0.9);
              font-family: PingFangSC-Regular;
              font-size: 0.18rem;
              color: #ffffff;
              letter-spacing: 0px;
              line-height: 0.32rem;
              text-align: center;
            }
            .code{
              vertical-align: top;
              background:#ffffff;
              border-radius:0.02rem;
              width:2.3rem;
              height:100%;
              padding: 0 0.2rem 0 0 ;
            }
            .code-button{
              vertical-align: top;
              font-family:PingFangSC-Regular;
              font-size:0.12rem;
              color:#fb4874;
              letter-spacing:0.0075rem;
              margin-left: 0.02rem;
              background: rgba(255, 255, 255, 0.9);
              border-radius:0.02rem;
              width:0.88rem;
              height:100%;
            }
            .input {

              background-color: #fff;
              padding-left: 0.4rem;
              font-family: PingFangSC-Regular;
              font-size: 0.14rem;
              color: #949494;
              letter-spacing: 0.0087rem;

              text-align: left;
            }
            .full {
              width: 100%;
              height: 100%;
              border-radius: 0.02rem;
              padding: 0 0.4rem;
            }
          }
        }
      }
    }
  }

  .foot-logo {
    padding: 1.75rem 0 0.6rem 0;
    font-family: PingFangSC-Regular;
    font-size: 0.16rem;
    color: #878787;
    letter-spacing: 0px;
    line-height: 100%;
    text-align: center;
  }
</style>



