<script type="text/javascript">
    import addrNav from 'src/goodsPages/comGoods/goosAddrNav.vue'
    import leaveWord from 'src/goodsPages/leaveWord.vue'
    import comHead from 'src/components/com/comHead.vue'
    import comFoot from 'src/components/com/comFoot.vue'
    import navSearch from 'src/components/index/navSearch.vue'
    export default{
        components: {addrNav,comFoot,navSearch,comHead,leaveWord},
        data(){
            return {
              isInfo: true,
              mobile:  m$.localStrages.get('accountPcData')?m$.localStrages.get('accountPcData'):'',
              password: "",
              isReme:1,
              notice:'',
              openLoginTemplate:false,
              aGoodsData:{

              },
              innerBoxData:{
                // businessTypeId:'',
                // name:''
              },
              goodsType:null,
              thisTime:'',
              isloginstatus:"",
              time:'',
              seckillTimeVal:''
            }
        },
        methods: {
          nogoLogin(){
            var othis=this;
            othis.openLoginTemplate=false;
            othis.isloginstatus='';
            m$.hiddenScroll(false)
          }
          ,
          checkIsLogin(oindex){
            var othis=this;
            this.getAjax(this.url.isLogged,{},(res)=>{
              if(res.code==200&&res.data==true){
                if(oindex==1){
                  this.goSureOrder();
                }
                else if(oindex==2){
                  this.goCar();
                }
                else {

                }
              }
              else {
                othis.openLoginTemplate=true;
                othis.isloginstatus=oindex;
                m$.hiddenScroll(true)
              }
            })
          },
          goToLoginSubmit(oname, opassword,ostatus){
            if (oname == "" || opassword == "") {
              this.notice='请输入手机号及密码';
              return;
            }
            else {
              this.postAjax('/user/login',//登录请求
                {mobile: oname, password: opassword},
                (res) => {
                  if(res.code==200){
                    m$.localStrages.set('yxdyb',res.data);
                    if(this.isReme==1){
                      m$.localStrages.set('accountPcData',oname);
                    }
                    else {
                      m$.localStrages.remove('accountPcData');
                    }
                    this.openLoginTemplate=false;
                    m$.hiddenScroll(false);
                    if(this.isloginstatus==1){
                      this.goSureOrder();
                    }
                    else if(this.isloginstatus==2){
                      this.goCar();
                    }
                    else {

                    }

                  }
                  else {
                    this.notice=res.message;
                  }
                })
            }

          },
            infoBtn(boo){
                var _this=this;
                if (boo) {
                        _this.isInfo = true
                } else {
                      _this.isInfo = false
                }
            },
          numBtn(boo){
            if (boo) {
              this.aGoodsData.quantity++
            } else {
              this.aGoodsData.quantity--
            }
          },
          magnifyingImgBtn(item,oindex){
              this.aGoodsData.magnifyingImg=item;
            var j=m$.dom('#small-img-warp .small>div');
            for(var i=0;i<j.length;i++){
              j[i].style.border="none";
            };
            j[oindex].style.border='1px solid #fb4874'
          },
          goCar(){
              m$.template({
                  val:'',
              });
              this.postAjax(this.srcUrl.saveCartUrl,{
                productId:this.aGoodsData.id,
                quantity:this.aGoodsData.quantity,
                price:this.aGoodsData.endPrice
              },(res)=>{
                  if(res.code==200){
                      m$.template({
                          val:'成功<br>加入购物车',
                          time:800,
                      });
                    this.thisTime=new Date().getTime();
                  }else {
                      m$.template({
                          val:res.message,
                          time:800,
                      })
                  }
              })
          },
          goSureOrder(){
            if(this.aGoodsData.total<this.aGoodsData.quantity){
              m$.template({
                val:'库存不足！',
                time:800,
              });
              return;
            }

              m$.sessionStores.set('products',this.aGoodsData);
            if(m$.sessionStores.get('products')){
                  window.location.href=('#/sureOrder')
            }
          }
          ,getTimeChange(){
            this.postAjax(this.srcUrl.getProductGroupShopVo, {
              status: 1,
              groupType: 1,
            }, (res) => {
              if (res.code == 200 && res.data.data[0]) {
                // res.data.data[0]
                var currentDate = new Date(res.data.data[0].currentDate.replace(
                  /\-/g, '/'));
                var gmtEnd = new Date(res.data.data[0].gmtEnd.replace(
                  /\-/g, '/'));
                var gmtStart = new Date(res.data.data[0].gmtStart);
                var time = (gmtEnd.getTime() - currentDate.getTime()) / 1000;
                //获得 秒杀结束时间
// console.log(time)
                var _this = this;
                _this.timer = setInterval(function () {
                  time--;
                  if (time == 0) {
                    clearInterval(_this.timer);
                    _this.seckillTimeVal == '00:00:00:000';
                    return;
                  }
                  _this.formatSeconds(time);
                }, 1000)
              }
            })
          },
          formatSeconds(value){
            var theTime = parseInt(value);// 秒
            var theTime1 = 0;// 分
            var theTime2 = 0;// 小时
            if (theTime > 60) {
              theTime1 = parseInt(theTime / 60);
              theTime = parseInt(theTime % 60);
              if (theTime1 > 60) {
                theTime2 = parseInt(theTime1 / 60);
                theTime1 = parseInt(theTime1 % 60);
              }
            }
            var result = "" + parseInt(theTime);
            if (theTime1 >= 0) {
              result = "" + parseInt(theTime1) + ":" + result;
            }
            if (theTime2 >= 0) {
              result = "" + parseInt(theTime2) + ":" + result;
            }

            this.seckillTimeVal = result;

          },
        },

      created(){
        m$.documentTitle("商品详细");
            this.getAjax(this.srcUrl.getProductDetailShopVoUrl,{productId:this.$route.params.id},(res)=>{
              if(res.code==200){
                  // this.aGoodsData = res.data;
                var ii={};
                ii.endPrice=(typeof res.data.bargainPrice=='number')?res.data.bargainPrice:((typeof res.data.promotionPrice=='number')?res.data.promotionPrice:res.data.oldPrice);
                ii.quantity = 1;
                ii.total = (typeof res.data.quantity=='number')?res.data.quantity:res.data.total;
                ii.id = res.data.id;
                ii.name = res.data.name;
                ii.groupType = res.data.groupType;
                ii.digest = res.data.digest;
                ii.businessId = res.data.businessId;
                ii.businessTypeId = res.data.businessTypeId;
                ii.saleCount = res.data.saleCount;
                ii.oldPrice = res.data.oldPrice;
                ii.intro = res.data.intro;
                ii.images = res.data.images;
                ii.thumbnail = res.data.thumbnail;
                ii.magnifyingImg = res.data.images[0];
                ii.businessName = res.data.businessName;
                this.goodsType=res.data.groupType;
                this.aGoodsData = ii;
                if (this.goodsType == 1) {
                  this.getTimeChange();
                }
                //处理头部商品类型和名字
                this.innerBoxData.businessTypeId=res.data.businessTypeId;
                this.innerBoxData.name=res.data.name;
              }else {
               m$.template({
                   val:res.message,
                    time:800
               })
              }
              setTimeout(function () {
                var allChriden = m$.dom('#pUm *');
                for (var j = 0; j < allChriden.length; j++) {
                  if (allChriden[j].tagName.toLowerCase() != 'br') {
                    allChriden[j].style.maxWidth = "100%";
                    allChriden[j].style.width = "";
                    allChriden[j].style.height = '';
                  }
                }
              }, 12)
            })
      },
      watch: {
        'mobile':function (val) {
          this.notice='';
        },'password':function (val) {
          this.notice='';
        },
        'aGoodsData':function (val) {



            setTimeout(function () {
              if(val.intro){
              var allChriden = m$.dom('#pUm *');
              for (var j = 0; j < allChriden.length; j++) {
                if (allChriden[j].tagName.toLowerCase() != 'br') {
                  allChriden[j].style.maxWidth = "100%";
                  allChriden[j].style.width = "";
                  allChriden[j].style.height = '';
                }
              }
              };
              m$.dom('#small-img-warp .small>div')[0].style.border='1px solid #fb4874';
            }, 12)

        },
        'aGoodsData.quantity':function (val) {
          var numgeG=/^[0-9]{1,6}$/;
          if (val&&(val <= 0  || numgeG.test(val)==false)) {
            this.aGoodsData.quantity = 1
          }
        }
      },
      beforeDestroy(){
        m$.hiddenScroll(false);
      }
    }
</script>

