/*
*作者---杨彬
*
*/
<template>
  <div class="my-order">
    <my-nav :listData="myIndexDataNav"></my-nav>
    <section v-if="!noData">
      <div class="order-item-warp"  v-for='(iii,indexs) in carOrder' :key="iii">
        <table class="" cellpadding="0" cellspacing="0">
          <tr class="item-head">
            <th v-text="'订单编号：'+iii.orderNo+'合同金额:￥'+iii.totalAmount"></th>
            <th style="width:1.27rem;text-align:left;">交易操作</th>
          </tr>
          <tr class="item-content">
            <td>
              <!--<div class="progress">-->
              <div class="progress-item" v-for='(item,ind) in iii.phases' :key="item" :class="item.status==3?'progress-right':''">
                <div><span v-text="ind+1"></span></div>
                <p v-text="item.name"></p>
              </div>
              <!--</div>-->
            </td>
            <td v-if='iii.ss==1' class="last-tr">
              <button class="agree com-cursor-hander" @click="verifiDcrOrder(iii.activeId,1,iii)">同意</button>
              <button class="refuse com-cursor-hander" @click="verifiDcrOrder(iii.activeId,0,iii)">不同意</button>
            </td>
            <td  v-if='iii.ss==2'>
              进度验收为不同意，等待装修公司再次提交！
            </td>
            <td  v-if='iii.ss==0'>
              正在进行中
            </td>
            <td  v-if='iii.ss==3'>
              交易成功
            </td>
          </tr>
        </table>
      </div>
      <div class="com-loading" v-if="isMoreData.more"><img src="../../assets/loading.png"></div>
    </section>
    <section v-if="noData">
      <img src="../../assets/noOrder.png" class="com-div-center-center-ab">
    </section>
  </div>
</template>
<script type="text/javascript">
  import myNav from "src/components/com/myNav.vue"
  import {myIndexData} from 'src/assets/componentsData.js'
  export default {
    data(){
      return {
        myIndexDataNav: myIndexData.decorateNav,
        carOrder:[],
        isMoreData:{
          init:true,
          more:true,

        },
        pageNum:1,
        noData:false,
      }
    },
    components: {myNav},
    methods:{
      verifiDcrOrder(id,boo,nitem){
        //boo为是否通过的传值
        if(boo){
          boo = true
        }else {
          boo = false
        }
        this.postAjax(this.srcUrl.verifiDcrOrderUrl,{id:id,isPass:boo,remark:''},(res)=>{
            if(res.code==200){
              if(boo==true){
                nitem.phases[nitem.sindex].status=3;
                if(nitem.sindex==4){
                  nitem.ss=3;
                }
                else {
                  nitem.ss=0;
                }
              }
              else {
                nitem.ss=2;
              }
            }else {
                m$.dealImage({
                    val:res.message,
                    time:600
                })
            }
        })
      },
      listDcrOrder(status){
        if(status==0){
          status=0
        };
        let othis=this;
        if(othis.isMoreData.init){
          m$.scroll(function () {
            othis.isMoreData.more=true;
          })
        }
        this.postAjax(this.srcUrl.listDcrOrderUrl,{pageNum:this.pageNum,pageSize:'10',completeStatus:status},(res)=>{
          var j=res.data;
          for (var i=0;i<res.data.length; i++){
            var obj={};
            obj.orderNo    =res.data[i].orderNo;
            obj.totalAmount=res.data[i].totalAmount;
            obj.phases     =res.data[i].phases;
            obj.ss=3;
            obj.sindex=4;
            obj.activeId="";
            for(var ii=0; ii<res.data[i].phases.length;ii++){
              obj.phases[ii].id=res.data[i].phases[ii].id;
              obj.phases[ii].name=res.data[i].phases[ii].name;
              obj.phases[ii].status=res.data[i].phases[ii].status;
              if(res.data[i].phases[ii].status==3&&ii==4){
                obj.ss=res.data[i].phases[ii].status;
                obj.activeId=res.data[i].phases[ii].id;
              }
              else {
                if(!obj.activeId&&res.data[i].phases[ii].status!=3){
                  obj.ss=res.data[i].phases[ii].status;
                  obj.sindex=ii,
                  obj.activeId=res.data[i].phases[ii].id;
                }
              }
            }
            this.carOrder.push(obj);
          }
          if(this.carOrder.length==0){
            this.noData=true;
          }
          othis.isMoreData.more=false;
          if(j&&j.length>0&&j.length%10==0){
            othis.isMoreData.init=true;
            m$.scroll(function () {
              othis.pageNum++;
              othis.listDcrOrder(othis.$route.params.id)
            })
          }
          else{
            m$.scroll(function () {
              othis.isMoreData.more=false;
            })
          }
        })
      },
    },
    created(){
      m$.documentTitle("装修订单");
      this.listDcrOrder(this.$route.params.id);
    },
    watch:{
      '$route':function (val) {
        this.carOrder=[];
        this.pageNum=1;
        this.isMoreData={
          init:true,
          more:false,
        }
        this.noData=false;
        this.listDcrOrder(val.params.id)
      }
    }
  }
</script>
<style scoped lang="less">
  .my-order {
    min-height: 6.72rem;
    _height: 6.72rem;
    font-size: 0.2rem;
    section {
      margin-top: 0.12rem;
      min-height: 5.88rem;
      _height: 5.88rem;
      position: relative;
      border: 1px solid #dbdbdb;
      .order-item-warp {
        table {
          width: 100%;
        }
        .item-head {
          font-family: PingFangSC-Regular;
          font-size: 0.18rem;
          color: #333333;
          background-color: #f8f8f8;
          > th {
            border-bottom: 1px solid #dbdbdb;
          }

          th {
            &:first-of-type {
              text-align: left;
              padding-left: 0.18rem;
            }
            font-size: 0.18rem;
          }
          height: 0.48rem;
          letter-spacing: 0px;
        }
        .item-content {
          /*.progress{*/
          .refuse,.agree{
            background:#ffffff;
            border:1px solid #fb4874;
            border-radius:0.04rem;
            width:0.88rem;
            height:0.32rem;
            font-family:PingFangSC-Regular;
            font-size:0.16rem;
            color:#fb4874;
            letter-spacing:0px;
          }
          .agree {
            margin-bottom:0.1rem ;
            background:#fb4874;
 color: #ffffff;
          }

          .progress-item {
            display: inline-block;
            width: 1.42rem;
            &:last-of-type>div>span:after{
            display: none;
            }
            div {
              &>span:after{
                content: "";
                width: 0.82rem;
                position: absolute;
                display: inline-block;
                top: 50%;
                border-bottom: 1px dashed #dbdbdb;
                left: 0.45rem;

              }
              > span {
                width: 0.3rem;
                height: 0.3rem;
                display: inline-block;
                color: #fff;
                font-family:PingFangSC-Regular;
                font-size:0.22rem;
                letter-spacing:0px;
                text-align:center;
                position: relative;
                border-radius: 100%;
                background-color: #bababa;

              }
            }
            p {
              font-family: PingFangSC-Regular;
              font-size: 0.18rem;
              margin-top: 0.12rem;
              color: #999;
              letter-spacing: 0px;
              height: 100%;
              text-align: center;
            }
          }
          .progress-right {
            > div {
              &>span:after{
                border-bottom: 1px dashed #333;
              }
              > span {
                background-color: #333;
              }
            }
            > p {
              color: #333;
            }
          }
          /*}*/
          > td {

            &:last-of-type {
              text-align: left;
              font-family:PingFangSC-Regular;
              font-size:0.18rem;
              color:#fb4874;
              letter-spacing:0px;
            }
            text-align: center;
            border-bottom: 1px solid #dbdbdb;
            border-top: 1px solid #dbdbdb;
          }
          height: 1.16rem;

        }
      }
    }
  }
</style>



