*作者---杨彬
*
*/
<template>
    <div class="my-order">
    <my-nav :listData="myIndexDataNav.orderNav"></my-nav>
      <section v-if="!noData">
        <div class="order-item-warp">
         <table class="" cellpadding="0" cellspacing="0" v-for="(oitem,oindex) in listData" :key="oitem">
          <tr class="item-head">
            <th v-text="'订单编号：'+oitem.sn"></th>
            <th style="width:0.86rem;">单价</th>
            <th style="width:0.86rem;">数量</th>
            <th style="width:1.18rem;" v-text="oitem.status==0?myIndexDataNav.realPayCn[0]:myIndexDataNav.realPayCn[1]"></th>
            <th style="width:1.18rem;">订单状态</th>
            <th style="width:1.22rem;">交易操作</th>
          </tr>

           <tr class="item-content" v-for="(nitem,nindex) in oitem.items" :key="nitem">
             <td class="one com-cursor-hander" @click="clickOrderDetail(oitem.id)"><img :src="nitem.thumbnail" class="img" @click.stop="goGoodsDetail(nitem.productId)"/><span v-text="nitem.name"></span></td>
             <td class="two" v-text="'¥'+nitem.price"></td>
             <td class="three" v-text="''+nitem.quantity"></td>
             <td :rowspan="nitem.rowSize" v-if="nindex==0" class="four" v-text="'¥'+oitem.totalAmount"></td>
             <td :rowspan="nitem.rowSize" v-if="nindex==0" class="five" v-text="oitem.statusText"></td>
             <td :rowspan="nitem.rowSize" v-if="nindex==0&&oitem.status==6" class="six">交易成功</td>
             <td :rowspan="nitem.rowSize" v-if="nindex==0&&oitem.status!=6&&myIndexDataNav.orderType[oitem.status].url.length==0" class="six">--</td>
             <td :rowspan="nitem.rowSize" v-if="nindex==0&&oitem.status!=6&&myIndexDataNav.orderType[oitem.status].url.length>0" class="six"><button type="button" v-text="sitem.text" @click=itemOperation(oitem.id,sitem.val,oitem)
                                                                               v-for="sitem in myIndexDataNav.orderType[oitem.status].url"  v-show="!(sitem.val==1&&oitem.isTimeout==1)" class="com-cursor-hander"></button></td>
           </tr>
         </table>
        </div>
        <section class="com-loading" v-if="isMoreData.more"><img src="../../assets/loading.png"></section>
      </section>
      <section v-if="noData">
        <img src="../../assets/noOrder.png" class="com-div-center-center-ab">
      </section>

    </div>
</template>
<script type="text/javascript">
  import myNav from "src/components/com/myNav.vue"
  import {myIndexData} from 'src/assets/componentsData.js'
  export default {
    data(){
      return{
        myIndexDataNav:myIndexData,
        listData: [],
        isMoreData:{
          init:true,
          more:false,
        },
        pageNum:1,
        noData:false,
      }
    },
    components:{myNav},
    methods:{
      clickOrderDetail(id){
        this.$router.push('/my/myOrder/orderDetail/'+id)
      },
      goGoodsDetail(id){
        this.$router.push('/goodsDetail/'+id)
      },
      itemOperation(orderId, operaionId, nitem){
        if (operaionId == 1) {
          var arr = [];
          var obj = {};
          obj.orderId = orderId;
          obj.paidAmount = nitem.totalAmount;
          arr.push(obj);
          if(nitem.isTimeout==1){
            return;
          }
          m$.localStrages.set('payInfo', arr);
          window.location.href = (`#/goPayByOrder`);
        } else if (operaionId == 0) {
          this.postAjax(this.url.cancelOrder, {id: orderId}, (res) => {
            if (res.code == 200) {
              let _this = this;
              nitem.status=4;
              nitem.statusText='已取消';
              m$.template({
                val: '取消成功',
                time: 800,
              });
            }
          })
        }else if(operaionId == 2){
          //确认收货
          this.postAjax(this.srcUrl.confirmRecevied, {orderId: orderId}, (res) => {
            if (res.code = 200) {
              nitem.status=3;
              nitem.statusText='已完成';
              // m$.template({
              //   val: '操作成功',
              //   time: 800,
              //   fn(){
              //     window.location.href=('#/orderList/6')
              //   }
              // });
            }
          })
        }
      },
      getData(status){
        if (status == -1) {
          status = ''
        } else if (status == 0) {
          status = 0
        } else if (status == 3) {
          status = 1
        } else if (status == 6) {
          status = 3
        }else {
          status = ''
        }
        let othis=this;
        if(othis.isMoreData.init){
          m$.scroll(function () {
            othis.isMoreData.more=true;
          })
        }

        this.postAjax(this.srcUrl.orderUrl, {status: status,pageSize:10,pageNum:othis.pageNum}, (res) => {
          if(res.code==200){
            var newlist=[];
            var j=res.data.data;
            for(var i=0;i<j.length;i++){
              switch (true){
                case j[i].status==0:j[i]['statusText']='待付款';break;
                case j[i].status==1:j[i]['statusText']='待发货';break;
                case j[i].status==3:j[i]['statusText']='已完成';break;
                case j[i].status==2:j[i]['statusText']='待收货';break;
                case j[i].status==4:j[i]['statusText']='已取消';break;
                default:j[i]['statusText']='--';break;
              }
               j[i].items[0]['rowSize']=j.length;
              this.listData.push(j[i])
            }
            if(this.listData.length==0){
              this.noData=true;
            }
            othis.isMoreData.more=false;
            if(j&&j.length>0&&j.length%10==0){
              othis.isMoreData.init=true;
              m$.scroll(function () {
                othis.pageNum++;
                othis.getData(othis.$route.params.id)
              })
            }
            else{
              m$.scroll(function () {
              })
            }
          }
        })
      },
    },
    created(){
      m$.documentTitle("订单列表");
      this.getData(this.$route.params.id)
    },
    watch: {
      '$route': function (val) {
        this.pageNum=1;
        this.listData = [];
        this.isMoreData={
          init:true,
            more:false,
        };
        this.noData=false;
        this.getData(val.params.id)
      }
    },

  }
</script>
<style scoped lang="less">
  .com-loading{
    text-align: center;
    height: 0.3rem;
    box-sizing: content-box;
    padding: 0.1rem 0;
    overflow: hidden;

    img{
      height: 0.3rem;
    }
  }
   .my-order{
     min-height: 6.72rem;
     _height: 6.72rem;
     font-size: 0.2rem;
     section{
       margin-top: 0.12rem;
       min-height: 5.88rem;
       _height: 5.88rem;
       position: relative;
       border:1px solid #dbdbdb;
       .order-item-warp{
         table{
         width: 100%;
         }
         .item-head{
           font-family:PingFangSC-Regular;
           font-size:0.18rem;
           color:#333333;
           background-color:#f8f8f8;
           >th{
             border-bottom: 1px solid #dbdbdb;
           }

      th{
        &:first-of-type{
          text-align: left;
          padding-left: 0.18rem;
        }
        font-size:0.18rem;
      }
           height:0.48rem;
           letter-spacing:0px;
         }
         .item-content{
           .one{
             text-align: left;
          padding-left: 0.2rem;
             >*{
               vertical-align: middle;
             }
             span{
               font-size: inherit;
               margin-left: 0.1rem;
             }
             >.img{
               height: 0.88rem;
             }
           }
           .four{
             font-weight: bolder;
           }
           .five{

             color:#fb4874;
           }
           .six{
             color:#fb4874;
             >button{

               border-radius:0.04rem;
               width:0.9rem;
               height:0.34rem;
               vertical-align: middle;
               font-family:PingFangSC-Regular;
               font-size:0.16rem;

               background:#fb4874;
               color: #ffffff;
               letter-spacing:0px;
               line-height:0.34rem;
               text-align:center;
               margin-top: 0.26rem;
               &:first-of-type{
                 margin-top: 0;
               }
               &:last-of-type{
                 background-color: transparent;
                 border:1px solid #fb4874;
                 color:#fb4874;
                 margin-bottom: 0;
               }
             }
           }
           >td{
             font-family:PingFangSC-Regular;
             font-size:0.18rem;
             color:#333333;
             letter-spacing:0px;
             text-align:center;
           }
           height: 1.16rem;
           >td:nth-of-type(n+4){
      border-left: 1px solid #dbdbdb;
             border-bottom: 1px solid #dbdbdb;
           }
           >td:nth-of-type(-n+3){
      border-bottom: 1px solid #dbdbdb;
           }
         }
       }
     }
   }
</style>



