/*
*作者---杨彬
*
*/
<template>
    <div class="comProductType">
      <com-head :thisTime="thisTime"></com-head>
      <nav-search class="index-search" @goSearch="gotoSeach"></nav-search>
      <section class="type-banner">
        <img src="../../assets/productType/banner.png">
      </section>
      <div class="typeItemList" v-if='allGoodsData'>
        <section>
          <div class="head" v-text="headName"></div>
          <section class="type-container" v-if="!noData">
            <type-item :allGoodsData='allGoodsData' v-if='allGoodsData' @newCarNum="newCarNum"></type-item>
          </section>
          <section v-if="noData" class="type-container min-type-height">
            <img src="../../assets/noOrder.png" class="com-div-center-center-ab">
          </section>
          <div class="com-loading" v-if="isMoreData.more"><img src="../../assets/loading.png"></div>
        </section>
      </div>
      <com-foot style="margin-top:0.6rem;"></com-foot>
    </div>
</template>
<script type="text/javascript">
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import navSearch from 'src/components/index/navSearch.vue'
  import typeItem from 'src/components/com/typeItem.vue'
  export default {
    components:{comHead,navSearch,comFoot,typeItem},
    data(){
      return{
        bannerData:'',
        typeNav:'',
        allGoodsData:[],
        headName:'',
        searchVal: '',
        nav: [],
        pageNum:1,
        thisTime:'',
        isMoreData:{
          init:true,
          more:true,

        },
        noData:false,
      }
    },
    methods:{
      newCarNum(oitem){
        this.thisTime=oitem;
      },
      gotoSeach(val){
        this.pageNum=1;
        this.searchVal=val;
        this.allGoodsData = [];
        this.goodsData();
      },
      typeNavData(){
        this.getAjax(this.srcUrl.getAllBusinessTypeUrl,{},(res)=>{
          for(var i=0,j=res.data;i<j.length;i++){
            if(j[i].id==this.$route.params.id){
              console.log(j[i])
              this.headName=j[i].name
            }
          };

        })
      },
      goodsData(){
        let othis=this;
        if(othis.isMoreData.init){
          m$.scroll(function () {
            othis.isMoreData.more=true;
          })
        }
        this.postAjax(this.srcUrl.getProductVoByTypeUrl,{
          businessTypeId: this.$route.params.id>0?this.$route.params.id:'',
          keywords: this.searchVal,
          pageNum: this.pageNum,
          pageSize: '10',
          priceType: '',
          status: '1'
        },(res)=>{
          if(res.code==200){
            othis.isMoreData.more=false;
            var j=res.data.data;
            for (var i=0;i<j.length;i++){
              this.allGoodsData.push(j[i]);
            }
            if(j&&j.length>0&&j.length%10==0){
              othis.isMoreData.init=true;
              m$.scroll(function () {
                othis.pageNum++;
                othis.goodsData()
              })
            }
            else{
              m$.scroll(function () {

                othis.isMoreData.more=false;
              })
            }
            if(this.allGoodsData.length==0){
              this.noData=true;
            }
            else {
              this.noData=false;
            }
          }
        })
      }
    },
    created(){

      if(this.$route.params.id==0){
        this.headName="全部商品";
        if(m$.sessionStores.get('thisPcSearchVal')||m$.sessionStores.get('thisPcSearchVal')===0){
          this.searchVal=m$.sessionStores.get('thisPcSearchVal');
         setTimeout(function () {
           m$.sessionStores.remove('thisPcSearchVal');
         },100)
        }
      }
      else {
        this.typeNavData();
      }

      this.goodsData();
    },
    watch: {
      '$route': function (val) {
        this.pageNum=1;
        this.allGoodsData = [];
        this.searchVal="";
        this.isMoreData={
          init:true,
          more:false,
        };
        if(this.$route.params.id==0){
          this.headName="全部商品";
        }
        else {
          this.typeNavData();
        }
        this.goodsData()
      }
    },
  }
</script>
<style scoped lang="less">
.type-banner{
  >img{
    width: 100%;
    max-height: 4.7rem;
  }
}
.typeItemList{
  width: 100%;
  text-align: center;
  >section{
    display: inline-block;
    width: 12rem;
    .head{
      font-family:PingFangSC-Medium;
      font-size:0.2rem;
      color:#fb4874;
      letter-spacing:0px;
      line-height:100%;
      text-align:left;
      padding: 0.14rem 0;
      box-sizing: content-box;
      border-bottom:0.04rem solid #fb4874 ;
      margin-bottom: 0.1rem;

    }
    .type-container{
      text-align: left;
      position: relative;

    }
    .min-type-height{
      min-height:2.5rem ;
    }
  }

}
</style>



