/*
*作者---杨彬
*
*/
<template>
  <div class="my-phone">
    <div class="phone-code-warp">
      <div class="row">
        <div class="one">*</div>
        <div class="two">原手机号：</div>
        <div class="three">
          <label class="input-label">
            <!--<span class="input-placeHolder com-div-middle-ab" v-if="!oldPhone">请输入您原手机号码</span>-->
            <input type="text" class="input-tel" v-model="two.phone" :value="two.phone" name="two.phone" disabledplaceholder="请输入您原手机号码" disabled>
          </label>
        </div>
      </div>
      <!---->
      <div class="row paddingRow">
        <div class="one">*</div>
        <div class="two">验 证 码：</div>
        <div class="three">
          <label class="input-label">
            <!--<span class="input-placeHolder com-div-middle-ab" v-if="!oldCode">请输入验证码</span>-->
            <input type="text" class="input-tel" v-model="two.codeCheck" :value="two.codeCheck" :disabled="two.disableCode"
                   name="two.codeCheck" style="width:2.1rem;" placeholder="请输入验证码">
          </label>
          <button class="get-code com-cursor-hander" @click="getAuthCode(2)" v-if="two.time==0">获取验证码</button>
          <button class="get-code" v-text="two.time+'s'"  v-if="two.time!=0"></button>
        </div>
      </div>
      <!---->
      <div class="row paddingRow">
        <div class="one">*</div>
        <div class="two">新手机号：</div>
        <div class="three">
          <label class="input-label">
            <!--<span class="input-placeHolder com-div-middle-ab" v-if="!newPhone">请输入您新手机号码</span>-->
            <input type="text" class="input-tel" v-model="one.phone" name="one.phone" placeholder="请输入您新手机号码">
          </label>
        </div>
      </div>
      <!---->
      <div class="row">
        <div class="one">*</div>
        <div class="two">验 证 码：</div>
        <div class="three">
          <label class="input-label">
            <!--<span class="input-placeHolder com-div-middle-ab" v-if="!newCode">请输入验证码</span>-->
            <input type="text" class="input-tel" v-model="one.codeCheck" name="one.codeCheck" :disabled="one.disableCode"
                   style="width:2.1rem;" placeholder="请输入验证码">
          </label>
          <button class="get-code com-cursor-hander" @click="getAuthCode(1)" v-if="one.time==0">获取验证码</button>
          <button class="get-code" v-text="one.time+'s'"  v-if="one.time!=0"></button>
        </div>
      </div>
    </div>
    <div class="submit-warp">
      <button class="submit-button" @click="nextStatus()" type="button">确定提交</button>
    </div>
  </div>
</template>
<script type="text/javascript">
  export default {
    data(){
      return {
        one:{
          phone:"",
          codeCheck: '',
          selIt:'',
          time:0,
          disableCode:true
        },
        two:{
          phone:"",
          codeCheck: '',
          selIt:'',
          time:0,
          disableCode:true
        },

        perStageCode:'',
      }
    },
    methods: {
      nextStatus(){
        var thisReg=/^1[3|4|5|7|8][0-9]{9}$/;
        let willSendData = {};
        let willSendData2 = {};
        let othis=this;
        if(!this.one.codeCheck||!this.two.codeCheck){
          m$.template({
            time: 1200,
            val: '请输入<br>验证码'
          });
          return
        }if(thisReg.test(this.two.phone)==false){
          m$.template({
            time: 800,
            val: '原手机号<br>输入有误'
          });
          return
        }
        if(thisReg.test(this.one.phone)==false){
          m$.template({
            time: 800,
            val: '新手机号<br>输入有误'
          });
          return
        }

          willSendData['mobile'] = this.two.phone;
          willSendData['authCode'] = this.two.codeCheck;
          this.getAjax(this.url.ckeckOp, willSendData, (res)=> {
            if (res.data == true) {
              willSendData2['newMobile'] = this.one.phone;
              willSendData2['authCode'] = this.one.codeCheck;
              this.postAjax(this.url.ckeckNp, willSendData2,
                (res)=> {
                  console.log(res.data)
                  if (res.code == 200) {
                    m$.template({
                      time:800,
                      val: '手机号码<br>设置成功<br>请重新登录',
                      fn(){
                        othis.$router.push('/login')
                      }
                    })
                  }
                  else {
                    m$.template({
                      time:800,
                      val:res.message
                    })
                  }
                })
            }
            else {
              m$.template({
                time: 800,
                val: '原手机号<br>验证码错误'
              })
            }
          })
      },
      getAuthCode(oindex){
        var thisReg=/^1[3|4|5|7|8][0-9]{9}$/;
        let  autoCodeUrl='';//验证码初始地址
        let  autoCodeData='';//验证码初始地址
        var othis=this;
        let statusObj=this.two;
        if(oindex==1){
          statusObj=this.one;

        }
        if(!statusObj.phone||thisReg.test(statusObj.phone)==false){
          m$.template({
            time:800,
            val:'请正确填写<br>手机号'
          })
        }
        else {
          if (oindex== 2) {
            autoCodeUrl=this.url.sendOpCode;
            autoCodeData={}
          }
          else {
            autoCodeUrl=this.url.sendNpCode;
            autoCodeData={newMobile:statusObj.phone}
          }
          this.getAjax(
            autoCodeUrl,autoCodeData,
            (res)=> {
              if(res.code==200){
                m$.template({
                  time:800,
                  val:'验证码<br>已发送',
                  fn(){
                    statusObj.disableCode=false;
                    statusObj.time=59;
                    statusObj.selIt =window.setInterval(function () {
                      statusObj.time--;
                      if(statusObj.time==0){

                        window.clearInterval(statusObj.selIt);
                      }
                    },1000)
                  }
                })
              }

            })
        }
      }
    },
    created(){
      let othis=this;
      m$.documentTitle('修改手机号码');
        this.getAjax(this.url.getUserMobile,{},(res)=>{
          if(res.code==200){
            this.two.phone=res.data;
          }
          else {
            m$.template({
              time:800,
              val:res.data,
              fn(){
                othis.$router.go(-1)
              }
            })
          }
        })
    },
    beforeDestroy(){
      let othis=this;

      if(othis.two.selIt){
        window.clearInterval(othis.two.selIt);
      }
      if(othis.one.selIt){
        window.clearInterval(othis.one.selIt);
      }
    }
  }
</script>
<style scoped lang="less">
  .input-label{
    height: 100%;
    position: relative;
    display: inline-block;
    background-color: #f0f0f0;
    .input-placeHolder{
      left: 0;
      font-size:0.2rem;
      padding: 0 0.15rem ;
      letter-spacing:0px;
      text-align:left;
      color: #999999;
    }
    .input-tel {
      background-color: transparent;
      border: 1px solid #c4c4c4;
      border-radius: 0.02rem;
      width: 2.86rem;
      height: 100%;
      font-family:PingFangSC-Regular;
      font-size:0.2rem;
      padding: 0 0.15rem ;
      letter-spacing:0px;
      text-align:left;
      color: #999999;
    }
  }
  .paddingRow{
    padding: 0.28rem 0;
  }
  .submit-warp{
  margin-top: 0.7rem;
    text-align: center;
    .submit-button{
      cursor: pointer;
      background:#333333;
      border-radius:0.04rem;
      width:2.02rem;
      height:0.56rem;
      font-family:PingFangSC-Regular;
      font-size:0.22rem;
      color:#ffffff;
      letter-spacing:0px;
    }

  }
  .get-code{
    margin-left: 0.06rem;
    background:#fb4874;
    border-radius:0.02rem;
    width:1.34rem;
    height: 100%;
    font-family:PingFangSC-Regular;
    font-size:0.2rem;
    color:#ffffff;
    letter-spacing:0px;
    text-align:center;
  }
  .my-phone {
    min-height: 6.68rem;
    _height: 6.68rem;
    padding-top: 1.08rem;
    border: 1px solid #dbdbdb;
    .phone-code-warp {
      .row {
        > div {
          display: inline-block;
          vertical-align: top;
          height: 0.54rem;
          line-height: 0.54rem;
        }
        .one {
          width: 2.46rem;
          /* * copy 2:*/;
          font-family: PingFangSC-Regular;
          font-size: 0.2rem;
          color: #fb4874;
          letter-spacing: 0px;
          text-align: right;
          padding-right: 0.06rem;
        }
        .two {
          width: 1rem;
          /* * copy 2:*/;
          font-family: PingFangSC-Regular;
          font-size: 0.2rem;
          color: #333;
          letter-spacing: 0px;
          text-align: right;
        }
        .three {
          .input-label{
            height: 100%;
            position: relative;
            display: inline-block;
            background-color: #f0f0f0;
            .input-placeHolder{
             left: 0;
              font-size:0.2rem;
              padding: 0 0.15rem ;
              letter-spacing:0px;
              text-align:left;
              color: #999999;
            }
            .input-tel {
              background-color: transparent;
              border: 1px solid #c4c4c4;
              border-radius: 0.02rem;
              width: 2.86rem;
              height: 100%;
              font-family:PingFangSC-Regular;
              font-size:0.2rem;
              padding: 0 0.15rem ;
              letter-spacing:0px;
              text-align:left;
              color: #999999;
            }
          }

        }
      }
    }
  }
</style>



