<template>
  <div class="decorate-address">
    <div class="address-edit com-div-center-center-ab">
      <div class="head">
        <div>
          添加新地址
        </div>
        <span @click="cancelFn(false)"><img src="./../../assets/goods/sureOrder/close.png"></span>
      </div>
      <section class="edit-content">
        <div class="phone-code-warp">
          <div class="row">
            <div class="one">*</div>
            <div class="two">收&nbsp;货&nbsp;人：</div>
            <div class="three">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!name">请输入收货人的姓名</span>-->
                <input style='width:2.7rem' type="text" class="input-tel" v-model="willSendData.consignee"
                       placeholder="请输入您的姓名（必填）">
              </label>
            </div>
          </div>

          <!---->
          <div class="row">
            <div class="one">*</div>
            <div class="two">所在区域：</div>
            <div class="three">
              <label class="input-label" @click="openALertFn()">
                <img class="arrowDown com-div-middle-ab" src='../../assets/my/arrowDown.png'>
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!area">请选择您所在的区域</span>-->
                <input style='width:3.9rem' type="text" class="input-tel com-cursor-hander" :value="thisPCD"
                       placeholder="请选择您的地址（必填）" disabled>
              </label>
            </div>
          </div>
          <!---->
          <div class="row paddingRow">
            <div class="one">*</div>
            <div class="two">详细地址：</div>
            <div class="three textarea">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab address" v-if="!address">请输入您的详细地址</span>-->
                <input style='width:3.9rem' class="input-tel" v-model="willSendData.dcAddress"
                       placeholder="请输入您的详细地址（必填）">
              </label>
            </div>
          </div>
          <!---->
          <div class="row paddingRow">
            <div class="one">*</div>
            <div class="two">联系电话：</div>
            <div class="three">
              <label class="input-label">
                <!--<span class="input-placeHolder com-div-middle-ab" v-if="!phone">请输入联系电话</span>-->
                <input style='width:3.9rem' type="tel" class="input-tel" v-model="willSendData.mobile"
                       placeholder="请输入您的电话（必填）">
              </label>
            </div>
          </div>
          <!--!-->
          <div class="set-addr-deflaut">
            <span style="margin-right:.1rem">
              <input type="checkbox" v-model='isDefaultData' :value='isDefaultData'>
              <img class="choice_no" src="./../../assets/goods/car/choice_no.png">
              <img class="choice" src="./../../assets/goods/car/choice.png">
            </span>
            <div>设置为默认收货地址</div>
          </div>
        </div>
        <div class="submit-warp">
          <button class="submit-button com-cursor-hander" type="button" @click="submitFn" style="margin-right:.44rem">
            保存
          </button>
          <button class="submit-button com-cursor-hander submit-del" type="button" @click="cancelFn(false)">取消</button>
        </div>
      </section>
      <div class="area-mark" v-if="openAreaMark">
        <area-linkage @upAreaMsg='upAreaMsgFn'
                      :upAreaData='willSendData'>

        </area-linkage>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import areaLinkage from 'src/components/com/areaLinkage/areaLinkage.vue';
  export default{
    components: {areaLinkage},
    props: ['isEdterC'],
    data(){
      return {
        openAreaMark: false,
        thisPCD: '',
        willSendData: {
          "dcArea": "",
          "provinceName": "",
          "cityName": "",
          "districtName": "",
          "provinceCode": '',
          "cityCode": '',
          "districtCode": '',
          "dcAddress": "",
          "mobile": "",
          "consignee": "",
        },
        isDefaultDataModel:'1',
        isDefaultData:'',
        isUpDataUrl:''
      }
    },
    watch:{
      isDefaultData(val){
          console.log(val)
      }
    },
    methods: {
      cancelFn(obool){
        // var boo = 777;
        this.$emit('cancelFn',obool)
      },
      openALertFn(){
        this.openAreaMark = true;
      },
      upAreaMsgFn(oObj){
        // console.log(oObj)
        this.openAreaMark = false;
        if (oObj == false) {
          return;
        }
        if (!oObj.province.id) {
          return;
        }
        ;
        this.willSendData.provinceName = oObj.province.name;
        this.willSendData.provinceCode = oObj.province.id;
        this.willSendData.cityName = oObj.city.name;
        this.willSendData.cityCode = oObj.city.id;
        this.willSendData.districtName = oObj.district.name;
        this.willSendData.districtCode = oObj.district.id;
        this.buildPCDFn();
      },
      buildPCDFn(){
        let ostring = "";
        if (this.willSendData.provinceName) {
          ostring += '/' + this.willSendData.provinceName
        }
        if (this.willSendData.cityName) {
          ostring += '/' + this.willSendData.cityName
        }
        if (this.willSendData.districtName) {
          ostring += '/' + this.willSendData.districtName
        }
        ostring = ostring.substring(1, ostring.length);
        this.thisPCD = ostring;
      },
      submitFn(){
        let othis = this;
        var addressExg = /^([A-Za-z0-9]|[\u4E00-\u9FA5])+$/;
        var tel = /^1[3|4|5|7|8][0-9]\d{8}$/;
        // let sdata = {
        //   // 'coupon.id': this.$route.params.id,
        //   realName: this.willSendData.realName,
        //   mobile: this.willSendData.mobile,
        //   dcDistrict: this.willSendData.districtCode,
        //   dcArea: '' + this.willSendData.provinceName + this.willSendData.cityName + this.willSendData.districtName,
        //   dcAddress: this.willSendData.dcAddress
        // };

        if(this.isEdterC){
          this.isUpDataUrl=this.url.updateReceiver;
          var sdata={
            id:this.willSendData.id,
            consignee:this.willSendData.consignee,
            phone:this.willSendData.mobile,
            provinceCode:this.willSendData.provinceCode,
            cityCode:this.willSendData.cityCode,
            districtCode:this.willSendData.districtCode,
            detailAddress:this.willSendData.dcAddress,
            isDefault:this.isDefaultData?1:0,
          };
        }else {
          this.isUpDataUrl=this.url.saveReceiver;
          var sdata={
            consignee:this.willSendData.consignee,
            phone:this.willSendData.mobile,
            provinceCode:this.willSendData.provinceCode,
            cityCode:this.willSendData.cityCode,
            districtCode:this.willSendData.districtCode,
            detailAddress:this.willSendData.dcAddress,
            isDefault:this.isDefaultData?1:0,
          };
        }

        if (!sdata.phone || !sdata.consignee) {
          m$.template({
            val: '手机号和姓名<br>不能为空',
            time: 1200
          });
          return;
        };
        if (!tel.test(sdata.phone)) {
          m$.template({
            val: '手机号<br>格式不正确',
            time: 1200
          });
          return;
        };
        if (!this.thisPCD || !sdata.detailAddress) {
          m$.template({
            val: '地址信息<br>不能为空',
            time: 1200
          });
          return;
        }
        if (addressExg.test(this.willSendData.dcAddress) == false) {
          m$.template({
            val: '地址详情不能<br>含有特殊字符',
            time: 500
          });
          return;
        }
        this.postAjax(this.isUpDataUrl, sdata, (res) => {
          if (res.code == 200) {
              var othis=this;
            m$.template({
              val: okImgDiv,
              time: 1000,
              fn(){
                othis.cancelFn(true)
              }
            })
          }
          else {
            m$.template({
              val: res.message,
              time: 1200,
            })
          }
        })
      },
    },
    created(){
      if (this.isEdterC) {

        this.getAjax(this.url.findReceiver, {
          isDefault: this.isEdterC.isDefault,
          receiveId: this.isEdterC.id
        }, (res) => {
          this.isDefaultData = res.data[0].isDefault;
          this.willSendData.consignee = res.data[0].consignee;
          this.willSendData.id = res.data[0].id;
          this.willSendData.mobile = res.data[0].phone;
          this.willSendData.dcAddress = res.data[0].detailAddress;
          this.willSendData.provinceCode = res.data[0].provinceCode;
          this.willSendData.cityCode = res.data[0].cityCode;
          this.willSendData.districtCode = res.data[0].districtCode;
          this.willSendData.provinceName = res.data[0].provinceName;
          this.willSendData.cityName = res.data[0].cityName;
          this.willSendData.districtName = res.data[0].districtName;
          this.thisPCD=res.data[0].provinceName+res.data[0].cityName+res.data[0].districtName

        })
      }else {

      }
      // var othis=this;
      // othis.getAjax(othis.url.my,{},(res)=>{
      //   if(res.code==200){
      //     if(res.data.mobile){
      //       othis.canInput=true;
      //       othis.willSendData.mobile=res.data.mobile;
      //     }
      //   }
      // });
      // document.title = '装修贷款申请';
      // m$.documentTitle("装修贷款申请")
    }
  }
</script>
<style scoped lang="less">
  .area-mark {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.71);
    z-index: 9999;
  }

  .decorate-address {
    width: 100%;
    height: 100%;
    z-index: 270;
    background-color: rgba(19, 19, 19, .8);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0 auto;
    padding-top: 0.33rem;
  }

  .address-edit {
    width: 6.6rem;
    height: 6.06rem;
    margin: 0 auto;
    background-color: #fff;
  }

  .deleteRed {
    color: #fb4874;
  }

  .editGrenn {
    color: #0baf7f;
    margin-left: 0.1rem;

  }

  .arrowDown {
    right: 0.14rem;
    height: 0.08rem;
  }

  .paddingRow {
    /*padding: 0.28rem 0;*/
  }

  .submit-warp {
    margin-top: 0.02rem;
    text-align: center;
    .submit-button {
      background: #333333;
      border-radius: 0.04rem;
      width: 1.88rem;
      height: 0.5rem;
      font-family: PingFangSC-Regular;
      font-size: 0.22rem;
      color: #ffffff;
      letter-spacing: 0px;
    }
    .submit-del {
      background: #ffffff;
      border: 1px solid #333333;
      color: #333;
    }
  }

  /*border:1px solid #dbdbdb;*/

  .head {
    padding: 0 .3rem;
    position: relative;
    > div {
      border-bottom: 3px solid #dbdbdb;
      height: 0.77rem;
      padding-left: 0.3rem;
      line-height: 0.77rem;
      font-family: PingFangSC-Regular;
      font-size: 0.22rem;
      color: #333333;
      letter-spacing: 0px;
      text-align: left;
    }
    > span {
      position: absolute;
      right: .31rem;
      top: .24rem;
      cursor: pointer;
    }
  }

  .edit-content {
    border: 1px solid #dbdbdb;
    border-top: none;
    height: 5.28rem;
    padding-top: 0.3rem;
    .phone-code-warp {
      .row {
        margin-bottom: .28rem;
        > div {
          display: inline-block;
          vertical-align: top;
          height: 0.44rem;
          line-height: 0.44rem;
        }
        .one {
          width: .9rem;
          /* * copy 2:*/;
          font-family: PingFangSC-Regular;
          font-size: 0.2rem;
          color: #fb4874;
          letter-spacing: 0px;
          text-align: right;
          padding-right: 0.06rem;
        }
        .two {
          width: 1.2rem;
          /* * copy 2:*/;
          font-family: PingFangSC-Regular;
          font-size: 0.2rem;
          padding-right: 0.2rem;
          color: #333;
          letter-spacing: 0px;
          text-align: right;
        }
        .three {
          &.textarea {
            width: 3.9rem;
            height: 0.54rem;
            line-height: 0.54rem;
            .input-label {
              width: 100%;
              .input-tel {
                width: 100%;
                resize: none;
              }
            }
          }
          .input-label {
            height: 100%;
            position: relative;
            display: inline-block;
            background-color: #f0f0f0;
            .input-placeHolder {
              left: 0;
              font-size: 0.2rem;
              padding: 0 0.15rem;
              letter-spacing: 0px;
              text-align: left;
              color: #999999;
            }
            .address {
              top: 0.28rem;
            }
            .input-tel {
              border: 1px solid #c4c4c4;
              border-radius: 0.02rem;
              width: 3.46rem;
              height: 100%;
              font-family: PingFangSC-Regular;
              font-size: 0.2rem;
              padding: 0 0.15rem;
              letter-spacing: 0px;
              text-align: left;
              color: #999999;
            }
          }

        }
      }
      .set-addr-deflaut {
        padding-left: .9rem;
        height: .18rem;
        line-height: .18rem;
        margin-bottom: .4rem;
        > div {
          font-size: .18rem;
          display: inline-block;
          vertical-align: middle;
        }
        > span {
          vertical-align: middle;
          display: inline-block;
          position: relative;
          width: .2rem;
          height: .2rem;
          > input {
            vertical-align: middle;
            display: inline-block;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 2;
            width: .2rem;
            height: .2rem;
            opacity: 0;
            &:checked ~ .choice_no {
              display: none;
            }
            &:checked ~ .choice {
              display: inline-block;
            }
          }
          .choice {
            display: none;
          }
          > img {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
</style>



