/*
*作者---杨彬
*
*/
<template>
    <div class="">
      <com-head :thisTime='thisTime'></com-head>
      <nav-search class="index-search" bgVal="true" searchHidden="true"></nav-search>
      <section class="my-index">
     <div class="menu">
       <div class="space-warp head-warp">
         <div>
         <div class="head"><input type="file" id="myHeadInput" name="wwf" @change="upDateImg()" accept="image/*" class="com-div-center-center-ab"><img :src="userData.headIcon"></div>
           <p class="name" v-text="userData.mobile"></p>
        </div>
       </div>
       <div v-for="(oitem,oindex) in myIndexData.menu" :key="oitem" class="menu-list-warp">
         <img src="../../assets/my/menuImg.png">
         <router-link class="menu-list" :class="citem.red?'menu-red':''"  v-for="(citem,cindex) in oitem" :key="citem" tag="div" :to="citem.url" active-class="active-div">
           <div v-text="citem.name">
           </div>
         </router-link>
       </div>

     </div>
     <router-view class="view"></router-view>
      </section>
      <com-foot></com-foot>
    </div>
</template>
<script type="text/javascript">
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import navSearch from 'src/components/index/navSearch.vue'
  import {myIndexData} from 'src/assets/componentsData.js'
  export default {
    components:{comFoot,navSearch,comHead},
    data(){
      return{
        myIndexData:myIndexData,
        thisTime:'',
        userData:{

        },
      }
    }  ,
    methods:{
      upDateImg(){
        let othis=this;
        if(!m$.dom('#myHeadInput')[0].value){
          return;
        }
        myImage(m$.dom('#myHeadInput')[0],{width:300,quality:1},(data)=>{
          othis.postAjax(othis.url.updateHeadIcon,{
            headImage:data.split(',')[1]

          },(res)=>{
            if(res.code==200){
              othis.getAjax(othis.url.my,{},(res)=>{
                if(res.code==200){
                  othis.userData=res.data;
                }
                else{
                  m$.template({
                    val:res.message,
                    time:600,
                  })
                }
              })
              // othis.myData.headIcon=data;
              othis.thisTime=new Date().getTime()
              m$.template({
                val:'头像上传<br>成功',
                time:600
              })
            }
            else {
              m$.template({
                val:res.message,
                time:1000
              })
            }
          })
        })
      },
      getMyData(){
        this.getAjax(this.url.my,{},(res)=>{
          if(res.code==200){
            this.userData = res.data
          }
        });
      },
    },
    created(){
      this.getMyData();

    }  }
</script>
<style scoped lang="less">
 .my-index{
   width: 12rem;
   margin: 0.3rem auto 0.4rem auto;
   >*{
     display: inline-block;
     vertical-align: top;
   }
   .menu{
     width: 2rem;
     margin-right: 0.1rem;
     .space-warp{
       padding-left: 0.06rem;
     }
     .head-warp{
       >div{
         background:#f8f8f8;
       }
       .head{
         text-align: center;
  padding: 0.16rem 0 0.02rem 0;
         position: relative;
         input{
           height: 0.84rem;
           width: 0.84rem;
           border-radius: 100%;
           top: 0.6rem;
           z-index: 3;
           opacity: 0;
         }
         img{
           height: 0.84rem;
           width: 0.84rem;
           position: relative;
           z-index: 1;
           border-radius: 100%;
         }
       }
       .name{
         height: 0.44rem;
         line-height: 0.44rem;
         font-family:PingFangSC-Regular;
         font-size:0.16rem;
         color:#666666;
         letter-spacing:0px;
         text-align:center;
       }
     }
.menu-list-warp{
  position: relative;
  >img{
    position: absolute;
    left: 0.22rem;
    height: 0.14rem;
    z-index: 3;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    top: 0.29rem;

  }
}
     .menu-list{
       padding-left: 0.06rem;
       height:0.58rem;
       >div{
         height: 0.58rem;
         border-bottom: 1px solid #cfcfcf;;
         background-color: #f8f8f8;
         position: relative;
         padding-left: 0.38rem;
         font-family:PingFangSC-Regular;
         font-size:0.2rem;
         color:#666666;
         letter-spacing:0px;
         line-height:0.58rem;
         text-align:left;
           cursor: pointer;
       }

     }
     .active-div{
       >div{
         background-color:#dedede;
       }

     }
     .menu-red{
       padding-left: 0;
       >div{
         background-color:#fb4874;
         font-family:PingFangSC-Medium;
         color:#ffffff;
         cursor: default;
         padding-left: 0.44rem;
       }
     }
   }
   .view{
    width: 9.9rem;
   }
 }
</style>



