*
*/
<template>
  <div class="my-password">
    <div class="phone-code-warp">
      <div class="row">
        <div class="one">*</div>
        <div class="two">原 密 码：</div>
        <div class="three">
          <label class="input-label">
            <!--<span class="input-placeHolder com-div-middle-ab" v-if="!oldPassword">请输入您原密码</span>-->
            <input type="password" class="input-tel" v-model="oldPassword" placeholder="请输入您的原密码">
          </label>
        </div>
      </div>
      <!---->
      <div class="row paddingRow">
        <div class="one">*</div>
        <div class="two">新 密 码：</div>
        <div class="three">
          <label class="input-label">
            <!--<span class="input-placeHolder com-div-middle-ab" v-if="!newPassword">请输入您新密码</span>-->
            <input type="password" class="input-tel" v-model="newPassword" placeholder="请输入您的新密码">
          </label>
        </div>
      </div>
      <!---->
      <div class="row">
        <div class="one">*</div>
        <div class="two">确认密码：</div>
        <div class="three">
          <label class="input-label">
            <!--<span class="input-placeHolder com-div-middle-ab" v-if="!againPassword">请再次输入您新密码</span>-->
            <input type="password" class="input-tel" v-model="surePassword" placeholder="请再次输入您的新密码">
          </label>
        </div>
      </div>
      <!---->
    </div>
    <div class="submit-warp">
      <button class="submit-button com-cursor-hander" @click="nextStatus()">确定提交</button>
    </div>
  </div>
</template>
<script type="text/javascript">
  export default {
    data(){
      return {
        oldPassword:'',
        newPassword: '',
        surePassword: '',
        time:0,
        selIt:'',
      }
    },
    methods: {
      nextStatus(){
        let willSendData = {}
        let othis=this;

          var pwdReg=/^[A-Za-z0-9]{6,20}$/;
          if(!this.oldPassword){
            m$.template({
              time:600,
              val:'请输入<br>原密码',
            })
            return;
          }  if(!pwdReg.test(this.newPassword)){
            m$.template({
              time:600,
              val:'密码由:6-20位<br>字母数字组合',
            })
            return;
          }
          if(this.newPassword!=this.surePassword||(!this.newPassword)){
            m$.template({
              time:600,
              val:'两次密码<br>不一致',
            })
            return;
          }

          willSendData['newPassword'] = this.newPassword;
          willSendData['oldPassword'] = this.oldPassword;
          this.postAjax(this.url.resetPassword, willSendData,
            (res)=> {
              console.log(res.data)
              if (res.code == 200) {
                m$.template({
                  time:800,
                  val: '密码重置<br>成功',
                  fn(){
                    othis.$router.replace('/my')
                  }
                })
              }
              else {
                m$.template({
                  time:800,
                  val:res.message
                })
              }
            })
        },
      getAuthCode(){
        var thisReg=/^1[3|4|5|7|8][0-9]{9}$/;
        let  autoCodeUrl='';//验证码初始地址
        var othis=this;
        if(!this.phone||thisReg.test(this.phone)==false){
          m$.template({
            time:1200,
            val:'请正确填写<br>手机号'
          })
        }
        else {
          if (this.status == 1) {
            autoCodeUrl=this.url.passwordAuthCode;
          }
          else {}
          this.postAjax(
            autoCodeUrl, {mobile:this.phone},
            (res)=> {
              if(res.code==200){
                m$.template({
                  time:1200,
                  val:'验证码<br>已发送',
                  fn(){
                    othis.time=59;
                    m$.localStrages.set('ybpc','ttyy')
                    othis.selIt =window.setInterval(function () {
                      othis.time--;
                      if(othis.time==0){
                        window.clearInterval(othis.selIt);
                      }
                    },1000)
                  }
                })
              }

            })
        }
      }
    },
    created(){

        m$.documentTitle('修改登录密码');

    },
    beforeDestroy(){
      let othis=this;
      if(othis.selIt){
        window.clearInterval(othis.selIt);
      }
    }
  }
</script>
<style scoped lang="less">
  .input-label{
    height: 100%;
    position: relative;
    display: inline-block;
    background-color: #f0f0f0;
    .input-placeHolder{
      left: 0;
      font-size:0.2rem;
      padding: 0 0.15rem ;
      letter-spacing:0px;
      text-align:left;
      color: #999999;
    }
    .input-tel {
      background-color: transparent;
      border: 1px solid #c4c4c4;
      border-radius: 0.02rem;
      width: 2.86rem;
      height: 100%;
      font-family:PingFangSC-Regular;
      font-size:0.2rem;
      padding: 0 0.15rem ;
      letter-spacing:0px;
      text-align:left;
      color: #999999;
    }
  }
  .paddingRow{
    padding: 0.28rem 0;
  }
  .submit-warp{
    margin-top: 0.7rem;
    text-align: center;
    .submit-button{
      background:#333333;
      border-radius:0.04rem;
      width:2.02rem;
      height:0.56rem;
      font-family:PingFangSC-Regular;
      font-size:0.22rem;
      color:#ffffff;
      letter-spacing:0px;
    }

  }
  .my-password {
    min-height: 6.68rem;
    _height: 6.68rem;
    padding-top: 1.58rem;
    border: 1px solid #dbdbdb;
    .phone-code-warp {
      .row {
        > div {
          display: inline-block;
          vertical-align: top;
          height: 0.54rem;
          line-height: 0.54rem;
        }
        .one {
          width: 2.46rem;
          /* * copy 2:*/;
          font-family: PingFangSC-Regular;
          font-size: 0.2rem;
          color: #fb4874;
          letter-spacing: 0px;
          text-align: right;
          padding-right: 0.06rem;
        }
        .two {
          width: 1rem;
          /* * copy 2:*/;
          font-family: PingFangSC-Regular;
          font-size: 0.2rem;
          color: #333;
          letter-spacing: 0px;
          text-align: right;
        }
        .three {
          .input-label{
            height: 100%;
            position: relative;
            display: inline-block;
            background-color: #f0f0f0;
            .input-placeHolder{
              left: 0;
              font-size:0.2rem;
              padding: 0 0.15rem ;
              letter-spacing:0px;
              text-align:left;
              color: #999999;
            }
            .input-tel {
              background-color: transparent;
              border: 1px solid #c4c4c4;
              border-radius: 0.02rem;
              width: 2.86rem;
              height: 100%;
              font-family:PingFangSC-Regular;
              font-size:0.2rem;
              padding: 0 0.15rem ;
              letter-spacing:0px;
              text-align:left;
              color: #999999;
            }
          }

        }
      }
    }
  }
</style>



