<template>
    <div class="order-detail">
      <div class="header">
        订单详情
        <div class="surplus-time-warp com-div-middle-ab" v-if="defaultData.status==0">
          <p class="surplus-time-cn" v-if="leftTimeFlag">付款剩余时间</p>
          <p class="surplus-time" v-text="leftTime" v-if="leftTimeFlag"></p>
        </div>
      </div>
  <section>
    <div class="order-item-warp">
      <table cellpadding="0" cellspacing="0">
        <tr class="item-head">
          <th v-text="'订单编号：'+defaultData.sn" style="width:3.5rem;"></th>
          <th>单价</th>
          <th style="width:0.8rem;">数量</th>
          <th style="width:0.74rem;">运费</th>
          <th style="width:0.98rem;">优惠券</th>
          <th style="width:1.48rem;" v-text="defaultData.status==0?sData.realPayCn[0]:sData.realPayCn[1]"></th>
          <th style="width:1.18rem;">订单状态</th>
        </tr>
        <tr v-for="(nitem,nindex) in goods" :key="nitem" class="item-content">
          <td class="one com-cursor-hander"><img :src="nitem.thumbnail" class="img" @click.stop="goGoodsDetail(nitem.productId)"/><span v-text="nitem.name"></span></td>
          <td class="two" v-text="'¥'+nitem.price"></td>
          <td class="three" v-text="''+nitem.quantity"></td>
          <td :rowspan="goods.length" v-if="nindex==0" class="" v-text="defaultData.freight?'¥'+defaultData.freight :'¥0.00'"></td>
          <td :rowspan="goods.length" v-if="nindex==0" class="" v-text="defaultData.couponAmount?'¥'+defaultData.couponAmount :'¥0.00'"></td>
          <td :rowspan="goods.length" v-if="nindex==0" class="four" v-text="'¥'+defaultData.paidAmount"></td>
          <td :rowspan="goods.length" v-if="nindex==0" class="five" v-text="defaultData.statusText"></td>
        </tr>
        <tr class="item-bottom-tr">
          <td colspan="7">
           <div class="left">
             <p v-text="'收货信息： '+defaultData.name+defaultData.receiverMobile+defaultData.address"></p>
             <p v-text="'配送方式： '+(sendType?sendType:'')+' '+(sendSn?sendSn:'')"></p>
             <p v-text="'订单时间： '+gtTime"></p>
           </div>
           <div class="right item-operation" v-if="sData.orderType[defaultData.status].url.length>0">
             <!--<div class="surplus-time-warp com-div-middle-ab" v-if="defaultData.status==0">-->
               <!--<p class="surplus-time-cn" v-text="sData.myOrder.detail.textOther[2]" v-if="leftTimeFlag"></p>-->
               <!--<p class="surplus-time" v-text="leftTime" v-if="leftTimeFlag"></p>-->
             <!--</div>-->
             <button type="button" v-text="sitem.text" @click=itemOperation(defaultData.id,sitem.val,defaultData) :key="sitem"
                     v-for="(sitem,sindex) in sData.orderType[defaultData.status].url" v-show="!(sitem.val==1&&leftTimeFlag==false)"></button>
           </div>
          </td>
        </tr>
      </table>
    </div>
  </section>

    </div>
</template>
<script type="text/javascript">
  import {myIndexData} from 'src/assets/componentsData.js';
  export  default {
    data(){
      return{
        sData:myIndexData,
        defaultData:
        { sendTime:"2017-1-11 10:00~11:00",
          orderTime:"2017-01-11 10:00",
          remark:"请尽快配送",
          number:5,
          sn:'',
          status:"1",id:"232321111222",
          leftTime:'00:30:59',
          money:"100",title:'华为Mate9 4GB+32GB版',
          detail:'月光银',
          img:[1], freight:20,point:100,coupon:20,
          address:'四川省成都市双流县华阳镇街道龙灯山路一段名著司南1栋1单元011',tel:'18202830089',name:'张虹'
        },
        goods:[],
        sendType:'',
        sendSn:'',
        payType:'',
        mark:'',
        gtTime:'',
        timer:'',
        leftTime:'',
        leftTimeFlag:false,
        fun1:function (aindex) {
          return (aindex==0 ? this.sendType :
              (aindex==1 ? this.sendSn :
                (aindex==2?this.payType:(aindex==3?this.mark:(aindex==4?this.gtTime:""))))
          )
        },
        funMoney:function (aindex) {
          return (aindex==0 ? this.defaultData.paidAmount :
              (aindex==1 ? this.defaultData.freight :
                (aindex==2?this.defaultData.couponAmount:""))
          )
        },
        fun:function (aindex) {
          return (aindex==0 ? this.sendType :
              (aindex==1 ? this.mark :
                (aindex==2?this.gtTime:""))
          )
        },
      }
    },
    methods:{
      goGoodsDetail(id){
        this.$router.push('/goodsDetail/'+id)
      },
      itemOperation(orderId, operaionId, nitem){
        if (operaionId == 1) {
          if(this.leftTimeFlag==false){
            return
          }
          var arr = [];
          var obj = {};
          obj.orderId = orderId;
          obj.paidAmount = nitem.paidAmount;
          arr.push(obj);
          m$.localStrages.set('payInfo', arr);
          window.location.href = (`#/goPayByOrder`);
        } else if (operaionId == 0) {
          this.postAjax(this.url.cancelOrder, {id: orderId}, (res) => {
            if (res.code == 200) {
              let _this = this;
              m$.template({
                val: '取消成功',
                time: 600,
                fn:function(){
                    _this.defaultData.status=4;
                  _this.defaultData.statusText='已取消';
                }
              });
            }
            else {
              m$.template({
                val: res.message,
                time: 800,
              });
            }
          })
        }else if(operaionId == 2){
          //确认收货
          this.postAjax(this.srcUrl.confirmRecevied, {orderId: orderId}, (res) => {
            if (res.code == 200) {
              this.defaultData.status=3;
              this.defaultData.statusText='已完成';
              // m$.template({
              //   val: '操作成功',
              //   time: 600,
              // });
            }
          })
        }
      },
      formatSeconds(value){
        // alert(value)
        var theTime = parseInt(value);// 秒
        var result=""
        var theTime1 = 0;// 分
        var theTime2 = 0;// 小时
        if (theTime >= 60) {
          // alert(theTime)
          theTime1 = parseInt(theTime / 60);
          theTime = theTime%60;
          if (theTime1 >= 60) {
            theTime2 = parseInt(theTime1 / 60);
            theTime1 = parseInt(theTime1 % 60);
          }
        }

        result += theTime;
        if (theTime1 >= 0) {
          result = "" + parseInt(theTime1) + ":" + result;
        }
        if (theTime2 >= 0) {
          result = "" + parseInt(theTime2) + ":" + result;
        }
        this.leftTime = result
      }
    },
    created(){
      m$.documentTitle("订单详情");
      this.postAjax(this.srcUrl.orderDetailsUrl,{orderId:this.$route.params.id},(res)=>{
        console.log(res.data)
        this.defaultData.address            =   res.data.address//地址
        this.defaultData.id                 =   res.data.id//订单号
        this.defaultData.sn                 =   res.data.sn//订单号
        this.defaultData.name               =   res.data.receiverName+'，'//姓名
        this.defaultData.receiverMobile     =   res.data.receiverMobile+'，'//电话
        this.defaultData.status             =   res.data.status//订单状态
        this.defaultData.paidAmount         =   res.data.paidAmount//实付款
        this.defaultData.number             =   res.data.quantity//实际数量
        this.defaultData.freight            =   res.data.freight//运费
        this.defaultData.couponAmount       =   res.data.couponAmount//优惠
        this.goods                          =   res.data.items//商品图片等信息
        // this.sendType                       =   res.data.shippingMethod;//配送方式
      this.sendType                       =   '快递';//配送方式
        this.sendSn                         =   res.data.shippingSn;//运单号
        this.payType                        =   res.data.payMethod;//支付方式
        this.mark                           =   res.data.remark;//运单备注
        this.gtTime                         =   res.data.gmtCreate;//下单时间
        switch (true){
          case this.defaultData.status==0:this.defaultData['statusText']='待付款';break;
          case this.defaultData.status==1:this.defaultData['statusText']='待发货';break;
          case this.defaultData.status==3:this.defaultData['statusText']='已完成';break;
          case this.defaultData.status==2:this.defaultData['statusText']='待收货';break;
          case this.defaultData.status==4:this.defaultData['statusText']='已取消';break;
          default:this.defaultData['statusText']='--';break;
        }
        if(res.data.currentDate&&res.data.gmtCreate){
          var currentDate = new Date(res.data.currentDate.replace(
            /\-/g,'/'));
          var gmtStart = new Date(res.data.gmtCreate.replace(
            /\-/g,'/'));
          var time = (gmtStart.getTime()+1800000 - currentDate.getTime()) / 1000;
          if(time<0){
            return;
          }
          //获得 秒杀结束时间
          var _this = this;

          _this.timer = setInterval(function () {
            _this.leftTimeFlag=true;
            time--;
            if(time==0){
              _this.leftTimeFlag=false;
              clearInterval(_this.timer);
              _this.leftTime='00:00:00'
            }
            _this.formatSeconds(time);
          }, 1000)
        }

      })
    }
  }
</script>
<style scoped lang="less">
