    min-height: 6.7rem;
    _height: 6.7rem;
    font-size: 0.2rem;
    .header{
      position: relative;
      >.surplus-time-warp{
          right: 0.38rem;
        text-align: center;
          .surplus-time-cn{
            font-family:PingFangSC-Regular;
            font-size:0.16rem;
            color:#fb4874;
            letter-spacing:0px;
            line-height:100%;
          }
          .surplus-time{
            margin-top: 0.1rem;
            font-family:PingFangSC-Regular;
            font-size:0.18rem;
            color:#222222;
            letter-spacing:0px;
            line-height:100%;
          }
        }
      height: 0.68rem;
      border: 1px solid #dbdbdb;
      line-height: 0.68rem;
      padding-left: 0.38rem;
      font-family:PingFangSC-Medium;
      font-size:0.2rem;
      color:#333333;
      letter-spacing:0px;
      text-align:left;
      margin-bottom: 0.12rem;
    }
    >section {
      //margin-top: 0.12rem;
      min-height: 5.9rem;
      _height: 5.9rem;
      border: 1px solid #dbdbdb;
      .order-item-warp{
        table{
          width: 100%;
        }
        .item-head{
          font-family:PingFangSC-Regular;
          font-size:0.18rem;
          color:#333333;
          background-color:#f8f8f8;
          >th{
            border-bottom: 1px solid #dbdbdb;
          }

          th{
            &:first-of-type{
              text-align: left;
              padding-left: 0.18rem;
            }
            font-size:0.18rem;
          }
          height:0.48rem;
          letter-spacing:0px;
        }
        .item-content{
          .one{
            text-align: left;
            padding-left: 0.2rem;
            >*{
              vertical-align: middle;
            }
            span{
              font-size: inherit;
              margin-left: 0.1rem;
            }
            >.img{
              height: 0.88rem;
            }
          }
          .four{
            font-weight: bolder;
          }
          .five{

            color:#fb4874;
          }
          .six{
            color:#fb4874;
            >button{

              border-radius:0.04rem;
              width:0.9rem;
              height:0.34rem;
              vertical-align: middle;
              font-family:PingFangSC-Regular;
              font-size:0.16rem;

              background:#fb4874;
              color: #ffffff;
              letter-spacing:0px;
              line-height:0.34rem;
              text-align:center;
              margin-top: 0.26rem;
              &:first-of-type{
                margin-top: 0;
              }
              &:last-of-type{
                background-color: transparent;
                border:1px solid #fb4874;
                color:#fb4874;
                margin-bottom: 0;
              }
            }
          }
          >td{
            font-family:PingFangSC-Regular;
            font-size:0.18rem;
            color:#333333;
            letter-spacing:0px;
            text-align:center;
          }
          height: 1.16rem;
          >td:nth-of-type(n+4){
            border-left: 1px solid #dbdbdb;
            border-bottom: 1px solid #dbdbdb;
          }
          >td:nth-of-type(-n+3){
            border-bottom: 1px solid #dbdbdb;
          }
        }
        .item-bottom-tr{
          td{
            height: 1.17rem;
            border-bottom: 1px solid #dbdbdb;
            >div{
              display: inherit;
              vertical-align: top;
              height: 1.16rem;
              &.left{
             width:  7.54rem;
                padding-top: 0.12rem;
                p{
                  line-height: 0.32rem;
                  height: 0.32rem;
                  padding-left: 0.24rem;
                  width: 100%;
                  font-family:PingFangSC-Regular;
                  font-size:0.18rem;
                  color:#333333;
                  letter-spacing:0px;
                  text-align:left;
                   overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
              }
              }
              &.right{
                text-align: right;
                width: 2.35rem;
                line-height: 1.17rem;
                >button{
                  border:1px solid #fb4874;
                  border-radius:0.04rem;
                  width:0.9rem;
                  height:0.35rem;
                  cursor: pointer;
                  vertical-align: middle;
                  font-family:PingFangSC-Regular;
                  font-size:0.16rem;
                  color:#fb4874;
                  letter-spacing:0px;
                  line-height:0.34rem;
                  text-align:center;
                  background-color: transparent;
                  margin-right: 0.27rem;
                  &:last-of-type{
                    background:#fb4874;
                    color: #ffffff;

                  }
                }
              }
            }
          }
        }
      }
    }
  }
</style>



