/*
*作者---杨彬
*
*/
<template>
  <div class="index-sale">
    <section v-if="groupType2">
      <div class="menu-title">
        <div>
          <span class="title" v-text="'特价专区'"></span>
          <router-link to="/todayGoods/type/0" class="will-go-skill com-div-middle-ab com-cursor-hander" tag="div"><span v-text="'更多特价商品'"></span><img
            :src="indexSkillData.go.img"></router-link>
        </div>
      </div>
      <div class="title-line"></div>
      <div class="menu-contain">

        <div class="swiper-container" dir="ltr">
          <div class="swiper-wrapper">
            <div class="sale-item swiper-slide" v-for="(oitem,oindex) in groupType2" :key="oindex">
              <div class="banner com-cursor-hander" @click="goToday(oitem.id)">
                <img :src="oitem.groupImg">
              </div>
              <div class="item-warp">
                <div class="item-product com-cursor-hander" v-for="(sitem,sindex) in oitem.detailVos" :key="sitem"
                     v-if="oitem.detailVos&&sindex<4">
                  <div class="item-img-warp">
                    <img :src="sitem.thumbnail" @click="goGoodsDetal(sitem.productId)" class="com-dev-center-center-ab">
                  </div>
                  <p class="p1" v-text="sitem.productName"></p>
                  <p class="p2"><span class="span1" v-text="'￥'+sitem.price+' '"></span><span class="span2"
                                                                                                    v-text="'¥'+sitem.oldPrice"></span>
                  </p>
                </div>

              </div>
              <div class="item-buttom"></div>
            </div>
          </div>
          <!--<img class="com-div-middle-ab left com-cursor-hander" src="../../assets/index/leftarrow.png">-->
          <!--<img class="com-div-middle-ab right com-cursor-hander" src="../../assets/index/rightarrow.png">-->
        </div>
        <!--<div class="two">-->
        <!--<img src="../../assets/index/skill.png">-->
        <!--</div>-->
      </div>

    </section>
  </div>
</template>
<script type="text/javascript">
  import {indexSkillData} from 'src/assets/componentsData.js';
  export default {
    data(){
      return {
        indexSkillData: indexSkillData,
        groupType2: ''
      }

    },
    methods: {
      goGoodsDetal(productId){
        window.location.href = (`#/goodsDetail/${productId}`)
      },
      goToday(oid){
        window.location.href = (`#/todayGoods/type/${oid}`)
      },
      getData(){
        this.postAjax(this.srcUrl.getProductGroupShopVo, {
          status: '',
          groupType: 2,
        }, (res)=> {
          if (res.data && res.data.data && res.data.data.length >= 1) {
            this.groupType2 = res.data.data;
            setTimeout(function () {
              var swipe1 = new Swiper('.menu-contain .swiper-container', {
                // autoplay: 2500,
                slidesPerView: 3,
                spaceBetween: 29.5,
                nextButton: '.right',
                prevButton: '.left'
              });
            }, 10)


          }
        })
      },
    },
    created(){
      this.getData();
    }
  }
</script>
<style scoped lang="less">
  .menu-title {
    padding: 0.22rem 0;
    text-align: left;
    > div {
      min-height: 0.22rem;
      border-left: 0.02rem solid #fb4874;
      padding-left: 0.12rem;
      position: relative;
      /*line-height: 0.24rem;*/
      font-size: 0;
      .will-go-skill {
        right: 0;
        > span {
          font-family: PingFangSC-Regular;
          font-size: 0.18rem;
          color: #666666;
          letter-spacing: 0px;
          line-height: 0.18rem;
          text-align: center;
        }
        > img {
          margin-left: 0.1rem;
        }
      }
      > * {
        display: inline-block;
        vertical-align: top;
      }
      .title {
        height: 0.26rem;
        font-family: PingFangSC-Medium;
        font-size: 0.22rem;
        color: #333333;
        letter-spacing: 0px;
        line-height: 0.24rem;
        text-align: center;
      }
    }
  }

  .index-sale {
    text-align: center;
    > section {
      position: relative;
      width: 12rem;
      display: inline-block;
      .title-line {
        display: inline-block;
        height: 1px;
        width: 11.5rem;
        border-bottom: 1px solid #d5d5d5;
        margin-bottom: 0.1rem;
      }
    }
  }

  .menu-contain {
    position: relative;
   >div{
     > img {
       width: 0.34rem;
       height: 0.34rem;
       z-index: 10;
       top: 0.71rem;
       &.left {
         left: 0;
       }
       &.right {
         right: 0;
       }
     }
   }
    .sale-item {
      width: 3.8rem;
      background-color: #fff;
      //margin-left: 0.295rem;
      &:first-of-type {
        margin-left: 0;
      }
      .item-buttom {
        height: 0.12rem;
        box-shadow: 0px 2px 10px 0px rgba(179, 179, 179, 0.50);
      }
      .banner {
        position: relative;
        > img {
          width: 100%;
          height: 1.42rem;

        }
        &:after {
          content: "";
          display: inline-block;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          -webkit-transform: translateX(-50%);
          -o-transform: translateX(-50%);
          -moz-transform: translateX(-50%);
          -ms-transform: translateX(-50%);
          transform: translateX(-50%);
          border-bottom: 0.08rem solid #fff;
          border-bottom: 0;
          border-left: 0.08rem solid transparent;
          border-right: 0.08rem solid transparent;

        }
      }
      .item-warp {

        text-align: center;
        padding: 0.1rem 0.1rem 0rem;
        /*border-bottom: 1px solid;*/

        > * {
          width: 0.86rem;
          display: inline-block;
          vertical-align: top;
          margin-left: 0.05rem;
          &:first-of-type {
            margin-left: 0;
          }
        }
        .item-img-warp {
          position: relative;
          height: 0.8rem;
>img{
  max-height: 100%;
}
        }
        .item-product {
          > p {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
          }
        }

        .p1 {
          font-family: PingFangSC-Regular;
          font-size: 0.14rem;
          margin: 0.1rem 0 0.03rem 0;
          color: #6a6a6a;
          letter-spacing: 0px;
          line-height: 100%;
          text-align: center;
        }
        .p2 {
          > * {
            font-family: PingFangSC-Regular;
            font-size: 0.12rem;
            color: #fb4874;
            letter-spacing: 0px;
            line-height: 100%;
            text-align: center;
          }
          .span1 {
            color: #fb4874;
          }
          .span2 {
            margin-left: 0.01rem;
            text-decoration: line-through;
            color: #c2c2c2;
          }
        }
      }
    }
    /*.two{*/
    /*width: 1.6rem;*/
    /*height: 100%;*/
    /*img{*/
    /*width: 100%;*/
    /*height: 100%;*/
    /*}*/
    /*}*/
  }
</style>



