<template>
  <div class="goods-head">
    <div class="header">
      <router-link tag='div' to='/index' class="left">
        <img class="com-cursor-hander"
             src="data:image/png;base64,">
      </router-link>
      <div class="right">
         <span class="i1">
           <i class="first"></i>
           <p>购物车</p>
         </span>
        <span :class="{i2:lineData>=2}" >
           <i></i>
           <p>确认订单</p>
         </span>
        <span :class="{l:lineData>=3,i3:lineData>=4}" >
           <i></i>
           <p>付款并完成购买</p>
         </span>

      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default{
    props: ['lineData'],
    data(){
      return {

      }
    }
  }
</script>
<style lang="less" scoped>
  //@import "${fileName}";
  .goods-head {
    background: #f8f8f8;
    .header {
      width: 12rem;
      margin: 0 auto;
      overflow: hidden;
      background: #f8f8f8;
      > div {
        height: 1.1rem;
      }
      .left {
        float: left;
        line-height: 1.1rem;
        > img {
          height: .66rem;
          display: inline-block;
          vertical-align: middle;
        }
      }
      .right {
        float: right;
        height: 1.1rem;
        padding-top: .3rem;
        > span {
          float: left;
          margin-left: .2rem;
          width: 1.26rem;
          height: .66rem;
          text-align: center;

          > i {
            display: inline-block;
            width: .18rem;
            height: .18rem;
            background-color: #999;
            border-radius: 10rem;
            margin-bottom: .1rem;
            position: relative;
            z-index: 22;
          }
          > i:before {
            z-index: 2;
            content: '';
            display: inline-block;
            position: absolute;
            width: 1.29rem;
            height: .04rem;
            left: -1.29rem;
            top: .06rem;
            background-color: #999;
          }
          > .first:before {
            content: '';
            left: .18rem;
            background-color: #f8f8f8;
          }
          > p {
            font-size: .18rem;
            color: #999;
          }
        }
        .l{
          > i {
            background-color: #999;
          }
          > i:before {
            background-color: #333;
          }
        }
        .i1, .i2, .i3 {
          > i {
            background-color: #333;
          }
          > i:before {
            background-color: #333;
          }
          > p {
            color: #333;
          }
        }

      }
    }
  }
</style>



