<template>
  <div class="buy-success">
    <com-head></com-head>
    <goods-head :lineData='lineStatus'></goods-head>
    <div class="no-car" v-if='isPaySuccess'>
      <div class="login">
        <img src="./../assets/goods/sureOrder/success.png">
      </div>
      <p>购买已完成，详情去我的订单查看吧~</p>
      <div class="btn"><span @click="goOrder">我的订单</span></div>
    </div>
    <div class="pay-box" v-if='!isPaySuccess'>
      <div class="logo">
        <div><img src="./../assets/goods/pay/bby.png"></div>
        <span>收银台</span>
      </div>
      <div class="info">
        <div class="left">
          <p>下单编号：<span v-text='payDatas.outTradeNumber'>56789</span></p>
          <p>订单类型：<span v-if='payType==1'>微信</span>
            <span v-if='payType==2'>支付宝</span>
          </p>
        </div>
        <div class="right">
          <div>支付: <span v-text="'￥'+payDatas.totalAmount">￥67890</span></div>
        </div>
      </div>
      <div class="pay-type">
      <div class="type1 " :class="{type2:payType==1}">
      <input type="radio" v-model='payType' name="r" value="1">
      <span><img src="./../assets/goods/sureOrder/weixin.png">微信支付</span>
      </div>

      <div class="type1" :class="{type2:payType==2}">
      <input type="radio" v-model='payType' name="r" value="2">
      <span><img src="./../assets/goods/sureOrder/zhifubao.png">支付宝支付</span>
      </div>
      </div>
      <div class="img-box">
        <span class="img" v-if='payType==2'><img src="./../assets/goods/pay/alipay.png"></span>
        <span class="img" v-if='payType==1'><img src="./../assets/goods/pay/WePayLogo.png"></span>
      </div>
      <div class="pay">
        <span id="qcodeSpan" class="com-div-center-center-ab qcode-span" v-show='!outTime'></span>
        <div class="out-time" v-if='outTime'>
          <p>二维码已过期</p>
          <div>
            <button @click="refreshQcode">点击刷新</button>
          </div>
        </div>
      </div>
    </div>
    <com-foot style="margin-top:0.6rem;"></com-foot>
  </div>
</template>

<script type="text/javascript">
  import comHead from 'src/components/com/comHead.vue'
  import comFoot from 'src/components/com/comFoot.vue'
  import goodsHead from 'src/goodsPages/comGoods/goodsHead.vue'
  export default{
    components: {goodsHead, comHead, comFoot},
    data(){
      return {
        lineStatus:3,
        payDatas: '',
        payType:1,
        outTime: false,
        payNum:0,
        isPaySuccess:false,
        typeUrl:'',
        QRCodeData:'',
        timer:null
      }
    },
    watch:{
      payType(val){
        this.getDefaultData(val)
      }
    },
    methods: {
      getDefaultData(typeUr){
        clearTimeout(this.timer);
        this.payNum=0;
        var typeUrl=this.srcUrl.payWeixinByPc;//默认微信
        if (this.payType == 1) {
          typeUrl = this.srcUrl.payWeixinByPc
        } else if (this.payType == 2) {
          typeUrl = this.srcUrl.alipayByPc
        }
        var payDatas = JSON.stringify(m$.localStrages.get('payInfo'));
        this.postAjax(typeUrl, payDatas, (res) => {
          if (res.code == 200) {
            var ii = {};
            var othis = this;
            ii.outTradeNumber = res.data.outTradeNumber;
            ii.totalAmount = res.data.totalAmount;
            ii.qrcode = res.data.qrcode;
            this.payDatas = ii;
            m$.dom('#qcodeSpan')[0].innerHTML='';
            this.QRCodeData=new QRCode('qcodeSpan', {
              text: res.data.qrcode,
              width: 240,
              height: 240,
              colorDark: '#000000',
              colorLight: '#ffffff',
              correctLevel: QRCode.CorrectLevel.H
            });
            this.timer=setTimeout(function () {
              othis.wx(res.data.outTradeNumber)
            }, 2000)
          } else if (res.code == 202) {
            this.outTime = true
          }
        })
      },
      refreshQcode(){
        this.outTime=false;
        this.getDefaultData();
      },
      goOrder(){
        window.location.href=('#/my/myOrder/type/3')
      },
      wx(outTradeNumber){
        var url='';
        if(this.payType==1){
          url=`/productorder/${outTradeNumber}/wxPayQRCodeStatus`;
        }else if(this.payType==2){
          url= `/productorder/${outTradeNumber}/aliPayQRCodeStatus`;
        }
        var _this=this;
        this.getAjax(url, {}, (res) => {
          if(res.status=='ok'){
            if (res.qrCodeStatus === 'WAIT'&&_this.payNum<=30) {
              _this.payNum++;
              if(_this.payNum==31){
                _this.outTime = true;
                return;
              }
              //等待支付
              this.timer=setTimeout(function () {
                _this.wx(outTradeNumber);
              },2000)
            } else if (res.qrCodeStatus === 'SUCCESS') {
              //成功
              this.isPaySuccess = true;
              this.lineStatus=4;
              m$.dom('#qcodeSpan')[0].innerHTML = '';
            } else if (res.qrCodeStatus === "FAIL") {
              //支付失败
              m$.template({
                val:'支付失败',
                time:1000,
                fn(){
                  window.location.href=('#/my/myOrder/type/0')
                }
              })
            } else if (res.qrCodeStatus === "CANCEL") {
              //用户取消
              m$.template({
                val:'用户取消',
                time:1000,
                fn(){
                  window.location.href=('#/my/myOrder/type/0')
                }
              })
            }
          }
        })
      }
    },

    created(){
      this.getDefaultData();
    },
    beforeDestroy(){
      clearTimeout(this.timer)
    }

  }
</script>
<style lang="less" scoped>
  //@import "${fileName}";
  .buy-success {
    width: 100%;
    height: 100%;
    .no-car {
      padding-top: 2.3rem;
      min-height: 7.7rem;
      _height:7.7rem;
      .login {
        height: 1.2rem;
        text-align: center;
        > img {
          height: 1.2rem;
          display: inline-block;
        }
      }
      > p {
        height: 1rem;
        line-height: 1rem;
        text-align: center;
        font-size: .24rem;
        color: #333;
      }
      .btn {
        text-align: center;
        > span {
          cursor: pointer;
          text-align: center;
          display: inline-block;
          border: 1px solid #333333;
          border-radius: 4px;
          width: 2rem;
          height: .54rem;
          line-height: .54rem;
          color: #333;
          font-size: .22rem;
        }
      }
    }
    .pay-box {
      width: 12rem;
      margin: 0 auto;
      padding-top: .1rem;
      .logo {
        margin-bottom: .3rem;
        margin-top: .2rem;
        > span {
          font-size: .24rem;
          color: #333;
          display: inline-block;
          vertical-align: middle;
        }
        > div {
          display: inline-block;
          vertical-align: middle;
          width: .3rem;
          height: .3rem;
          margin-right: .2rem;
          > img {
            display: inline-block;
            vertical-align: middle;
            width: 100%;
            height: 100%;
          }
        }
      }
      .info {
        overflow: hidden;
        padding: .22rem 0.3rem;
        margin-bottom: .5rem;
        background-color: #F2F2F2;
        > div {
          > p {
            font-size: .18rem;
            height: .4rem;
            line-height: .4rem;
            > span {
              font-size: .18rem;
            }
          }
        }
        .left {
          float: left;
        }
        .right {
          float: right;
          > div {
            font-size: .18rem;
            height: .8rem;
            line-height: .8rem;
            > span {
              font-size: .18rem;
              color: #fb4874;
            }
          }
        }
      }
      .img-box {
        .img {
          width: 2rem;
          height: .6rem;
          display: block;
          margin: 0 auto;
          > img {
            width: 100%;
            height: 100%;
          }
        }
      }
      .pay {
        margin: 0 auto;
        margin-top: .2rem;
        width: 5rem;
        height: 4rem;
        border: .01rem solid #333;
        position: relative;
        > span {
          display: inline-block;
        }
        .out-time {
          position: absolute;
          padding-top: 1.8rem;
          z-index: 22;
          width: 100%;
          height: 100%;
          background-color: rgba(222, 222, 222, .3);
          > p {
            font-size: .18rem;
            color: #333;
            text-align: center;
          }
          > div {
            margin-top: .2rem;
            text-align: center;
            > button {
              cursor: pointer;
              display: inline-block;
              width: 1.5rem;
              height: .4rem;
              border-radius: .06rem;
              font-size: .2rem;
            }
          }

        }
      }
      .pay-type{
        height: 1.35rem;
        padding: .4rem 0;
        .type1{
          width: 2rem;
          height: .56rem;
          line-height: .56rem;
          background:#ffffff;
          border:1px solid #333333;
          text-align: center;
          float: left;
          margin-right: .6rem;
          position: relative;
          cursor: pointer;
          >input{
            cursor: pointer;
            position: absolute;
            z-index: 222;
            left: 0;
            top: 0;
            display: inline-block;
            width: 1.98rem;
            height: .54rem;
            opacity: 0;
          }
          >span{
            display: inline-block;
            vertical-align: middle;
            font-size: .2rem;
            >img{
              margin-right: .1rem;
              display: inline-block;
              vertical-align: middle;
            }
          }
        }
        .type2{
          border:1px solid #fb4874;
          background: url("./../assets/goods/sureOrder/detailChoice.png") no-repeat right bottom;
        }
      }
    }
  }
</style>



